import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { ComponentPortal } from '@angular/cdk/portal';
import { ComponentRef, Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { IndexableObject, LoggerService } from 'ng-zorro-antd/core';
import { ModalControlService } from './cmacs-modal-control.service';
import { CmacsModalRef } from './cmacs-modal-ref.class';
import { CmacsModalComponent } from './cmacs-modal.component';
import { ConfirmType, ModalOptions, ModalOptionsForService } from './cmacs-modal.type';

// A builder used for managing service creating modals
export class ModalBuilderForService {
  private modalRef: ComponentRef<CmacsModalComponent> | null; // Modal ComponentRef, "null" means it has been destroyed
  private overlayRef: OverlayRef;

  constructor(private overlay: Overlay, options: ModalOptionsForService = {}) {
    this.createModal();

    if (!('getContainer' in options)) {
      // As we use CDK to create modal in service by force, there is no need to use getContainer
      options.getContainer = undefined; // Override getContainer's default value to prevent creating another overlay
    }

    this.changeProps(options);
    this.modalRef!.instance.open();
    this.modalRef!.instance.afterClose.subscribe(() => this.destroyModal()); // [NOTE] By default, close equals destroy when using as Service
  }

  getInstance(): CmacsModalComponent | null {
    return this.modalRef && this.modalRef.instance;
  }

  destroyModal(): void {
    if (this.modalRef) {
      this.overlayRef.dispose();
      this.modalRef = null;
    }
  }

  private changeProps(options: ModalOptions): void {
    if (this.modalRef) {
      Object.assign(this.modalRef.instance, options); // DANGER: here not limit user's inputs at runtime
    }
  }

  // Create component to ApplicationRef
  private createModal(): void {
    this.overlayRef = this.overlay.create();
    this.modalRef = this.overlayRef.attach(new ComponentPortal(CmacsModalComponent));
  }
}

@Injectable({
  providedIn: 'root'
})
export class CmacsModalService {
  // Track of the current close modals (we assume invisible is close this time)
  get openModals(): CmacsModalRef[] {
    return this.modalControl.openModals;
  }

  get afterAllClose(): Observable<void> {
    return this.modalControl.afterAllClose.asObservable();
  }

  constructor(private overlay: Overlay, private logger: LoggerService, private modalControl: ModalControlService) {}

  // Closes all of the currently-open dialogs
  closeAll(): void {
    this.modalControl.closeAll();
  }

  create<T>(options: ModalOptionsForService<T> = {}): CmacsModalRef<T> {
    if (typeof options.onCancel !== 'function') {
      options.onCancel = () => {}; // Leave a empty function to close this modal by default
    }

    // NOTE: use CmacsModalComponent as the CmacsModalRef by now, we may need archive the real CmacsModalRef object in the future
    const modalRef = new ModalBuilderForService(this.overlay, options).getInstance()!;

    return modalRef;
  }

  confirm<T>(options: ModalOptionsForService<T> = {}, confirmType: ConfirmType = 'confirm'): CmacsModalRef<T> {
    if ('footer' in options) {
      this.logger.warn(`The Confirm-Modal doesn't support "footer", this property will be ignored.`);
    }
    if (!('width' in options)) {
      options.width = 416;
    }
    if (typeof options.onOk !== 'function') {
      // NOTE: only support function currently by calling confirm()
      options.onOk = () => {}; // Leave a empty function to close this modal by default
    }

    options.modalType = 'confirm';
    options.className = `ant-modal-confirm ant-modal-confirm-${confirmType} ${options.className || ''}`;
    options.cmacsMaskClosable = false;
    return this.create(options);
  }

  info<T>(options: ModalOptionsForService<T> = {}): CmacsModalRef<T> {
    return this.simpleConfirm(options, 'info');
  }

  success<T>(options: ModalOptionsForService<T> = {}): CmacsModalRef<T> {
    return this.simpleConfirm(options, 'success');
  }

  error<T>(options: ModalOptionsForService<T> = {}): CmacsModalRef<T> {
    return this.simpleConfirm(options, 'error');
  }

  warning<T>(options: ModalOptionsForService<T> = {}): CmacsModalRef<T> {
    return this.simpleConfirm(options, 'warning');
  }

  private simpleConfirm<T>(options: ModalOptionsForService<T> = {}, confirmType: ConfirmType): CmacsModalRef<T> {
    const iconMap: IndexableObject = {
      info: 'info-circle',
      success: 'check-circle',
      error: 'close-circle',
      warning: 'exclamation-circle'
    };
    if (!('iconType' in options)) {
      options.iconType = iconMap[confirmType];
    }
    if (!('cancelText' in options)) {
      // Remove the Cancel button if the user not specify a Cancel button
      options.cmacsCancelText = null;
    }
    return this.confirm(options, confirmType);
  }
}
