import {
  Component, EventEmitter, Input, Output,
  ViewEncapsulation, ViewChild
} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import { SignaturePad } from 'angular2-signaturepad/signature-pad';
import {UploadChangeParam, UploadFile, UploadXHRArgs} from "ng-zorro-antd";
import {CmacsMessageService} from "../cmacs-message/cmacs-message.service";

export const SIGNATURE_LOCALIZATION = {
  'Click here to sign': 'Click here to sign',
  'Sign to Complete': 'Sign to Complete',
  'Cancel': 'Cancel',
  'Verify': 'Verify',
  'Enter Your Name': 'Enter Your Name',
  'Text Here': 'Text Here',
  'Type': 'Type',
  'Draw': 'Draw',
  'Image': 'Image',
  'I, User Full Name, have reviewed and completed this report.': 'I, User Full Name, have reviewed and completed this report.',
  'Select here to save your signature for future use.': 'Select here to save your signature for future use.',
  'Signature': 'Signature',
  'Drag & Drop File Here or': 'Drag & Drop File Here or',
  'Browse Computer': 'Browse Computer'
};

@Component({
  selector: 'cmacs-signature',
  exportAs: 'cmacsSignature',
  templateUrl: './cmacs-signature.component.html',
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  styleUrls: ['./cmacs-signature.component.css']
})
export class CmacsSignatureComponent {

  _isVisible = false;
  formGroup: FormGroup;
  saveSignature = true;
  _i18n = SIGNATURE_LOCALIZATION;
  current = 0;
  signaturePadOptions: Object = {
    'dotSize': 0.4,
    'canvasWidth': 480,
    'canvasHeight': 126
  };
  files = [];

  @Input() i18n: any = SIGNATURE_LOCALIZATION;

  @Output() oncancel: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() onsubmit: EventEmitter<any> = new EventEmitter<any>();
  @ViewChild(SignaturePad) signaturePad: SignaturePad;

  constructor(private fb: FormBuilder, private msg: CmacsMessageService) {
    this.formGroup = this.fb.group({
      username: ['', [Validators.required]]
    });
  }

  ngAfterViewInit() {
    this.signaturePad.clear();
  }

  beforeUpload = (file: UploadFile): boolean => {
    this.files = [].concat(file);
    return false;
  };

  showUploadList = {
    showPreviewIcon: true,
    showRemoveIcon: true,
    hidePreviewIconInNonImage: true
  };

  previewImage: string | undefined = '';
  previewVisible = false;

  handlePreview = (file: UploadFile) => {
    this.previewImage = file.url || file.thumbUrl;
    this.previewVisible = true;
  };


  isValid() {
    switch (this.current) {
      case 0:
        return !this.formGroup.valid;
      case 1:
        return true;
      case 2:
        return true;
      default:
        return true;
    }
  }

  updateCurrent(idx) {
    this.current = idx;
  }

  drawComplete() {
    console.log(this.signaturePad.toDataURL());
  }

  openModal() {
    this._isVisible = true;
  }

  handleCancel() {
    this._isVisible = false;
    this.oncancel.emit(this._isVisible);
  }

  handleOk() {
    this.onsubmit.emit();
  }

  checkPressedKeys($event) {
    if ($event.which === 90 && $event.ctrlKey) {
      const data = this.signaturePad.toData();
      if (data) {
        data.pop();
        this.signaturePad.fromData(data);
      }
    }
  }

  handleChange({ file, fileList }: UploadChangeParam): void {
    const status = file.status;
    if (status !== 'uploading') {
      console.log(file, fileList);
    }
    if (status === 'done') {
      this.msg.success(`${file.name} file uploaded successfully.`);
    } else if (status === 'error') {
      this.msg.error(`${file.name} file upload failed.`);
    }
  }

}
