import { ChangeDetectionStrategy, Component, ElementRef, Input, OnInit, ViewEncapsulation, AfterContentInit, ViewChildren, ContentChildren, QueryList } from '@angular/core';

import { NzSizeLDSType, NzUpdateHostClassService, InputNumber, InputBoolean } from 'ng-zorro-antd/core';
import { CmacsButtonComponent } from './cmacs-button.component';

@Component({
// tslint:disable-next-line: component-selector
  selector: 'cmacs-button-group',
  exportAs: 'cmacsButtonGroup',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  providers: [NzUpdateHostClassService],
  styleUrls: ['./cmacs-button-group.component.css'],
  templateUrl: './cmacs-button-group.component.html'
})
export class CmacsButtonGroupComponent implements OnInit, AfterContentInit {
  @Input()
  get nzSize(): NzSizeLDSType {
    return this._size;
  }

  set nzSize(value: NzSizeLDSType) {
    this._size = value;
    this.setClassMap();
  }
  @Input() @InputBoolean() overlap = false;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() disabledNav = false;

  constructor(private nzUpdateHostClassService: NzUpdateHostClassService, private elementRef: ElementRef) {}

// tslint:disable-next-line: variable-name
  private _size: NzSizeLDSType;
  private prefixCls = 'ant-btn-group';
  buttons: CmacsButtonComponent[] = [];
  indexBtn = 0;

  @ContentChildren(CmacsButtonComponent) contentButtons: QueryList<CmacsButtonComponent>;

  setClassMap(): void {
    const classMap = {
      [this.prefixCls]: true,
      [`${this.prefixCls}-lg`]: this.nzSize === 'large',
      [`${this.prefixCls}-sm`]: this.nzSize === 'small'
    };
    this.nzUpdateHostClassService.updateHostClass(this.elementRef.nativeElement, classMap);
  }

  ngOnInit(): void {
    this.setClassMap();
  }

  ngAfterContentInit(): void {
    this.buttons = this.contentButtons.toArray();
    if (this.overlap) {
      for (let index = 1; index < this.buttons.length; index++) {
        this.buttons[index].hideBtn();
      }
    }
    if (this.disabled) {
      for (const iterator of this.buttons) {
        iterator.disabledBtn();
      }
    }
  }

  moveLeft(): void {
    this.buttons[this.indexBtn--].hideBtn();
    this.buttons[this.indexBtn].showBtn();
  }

  moveRight(): void {
    this.buttons[this.indexBtn++].hideBtn();
    this.buttons[this.indexBtn].showBtn();
  }

  isDisableLeft(): boolean {
    return this.indexBtn === 0 || this.disabled || this.disabledNav;
  }

  isDisableRight(): boolean {
    return this.indexBtn === this.buttons.length - 1 || this.disabled || this.disabledNav;
  }
}
