import { DatePipe } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  AfterViewInit,
  TemplateRef,
  ViewChild,
  HostListener
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil, count } from 'rxjs/operators';

import { InputBoolean, InputNumber, NzSizeMDSType } from 'ng-zorro-antd/core';
import { NzI18nService } from 'ng-zorro-antd/i18n';

import { ExportAsService, ExportAsConfig } from 'ngx-export-as';
import jsPDF from 'jspdf';
import 'jspdf-autotable';
import { GridConfig, Field } from '../core/interfaces/grid-config';
import { GridExpConfig } from '../core/interfaces/grid-exp-config';
import { TemplateType } from '../core/enums/templateType.enum';
import { CeldType } from '../core/enums/celdType.enum';
import { ExcelService } from '../core/services/excel.service';
import { ExportType } from '../core/enums/export-type.enum';
import {CookieService} from "ngx-cookie-service";
import {CheckboxSelect} from "../cmacs-grid/cmacs-table.component";
import {isArray} from "util";
import {CdkDragDrop, moveItemInArray} from "@angular/cdk/drag-drop";
import {isNotNil, NzDropdownContextComponent, NzDropdownService, NzMenuDropdownService} from "ng-zorro-antd";
import {FormControl} from "@angular/forms";

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'cmacs-compact-table',
  exportAs: 'cmacsCompactTable',
  templateUrl: './cmacs-compact-table.component.html',
  styleUrls: ['./cmacs-compact-table.component.css'],
  host: {
    '[class.cmacs-compact-table-logs]': 'logs'
  }
})
// tslint:disable-next-line no-any
export class CmacsCompactTableComponent<T = any> implements OnInit, OnChanges, OnDestroy, AfterViewInit {
  locale: any = {}; // tslint:disable-line:no-any
  headerBottomStyle = {};
  private destroy$ = new Subject<void>();
  // tslint:disable-next-line: no-input-rename
  @Input() size: NzSizeMDSType = 'default';
  @Input() showTotal: TemplateRef<{ $implicit: number; range: [number, number] }>;
  @Input() pageSizeOptions = [10, 20, 30, 40, 50];
  @Input() @InputBoolean() virtualScroll = false;
  @Input() @InputBoolean() logs = false;
  @Input() @InputBoolean() expandable = false;
  @Input() @InputBoolean() smartTable = false;
  @Input() @InputBoolean() draggable = false;
  @Input() @InputNumber() virtualItemSize = 0;
  @Input() @InputBoolean() expandAll = false;
  @Input() loadingDelay = 0;
  @Input() loadingIndicator: TemplateRef<void>;
  @Input() total = 0;
  @Input() title: string | TemplateRef<void>;
  @Input() footer: string | TemplateRef<void>;
  @Input() noResult: string | TemplateRef<void>;
  @Input() widthConfig: string[] = [];
  @Input() pageIndex = 1;
  @Input() pageSize = 10;
  // tslint:disable-next-line: no-input-rename
  @Input() data = [];
  @Input() config: GridConfig;
  @Output() configChange: EventEmitter<GridConfig> = new EventEmitter<GridConfig>();
  @Input() fieldId: string;
  @Input() gridID: string;
  @Input() paginationPosition: 'top' | 'bottom' | 'both' = 'bottom';
  @Input() scroll: { x?: string | null; y?: string | null } = { x: null, y: null };
  @Input() @ViewChild('renderItemTemplate') nzItemRender: TemplateRef<{
    $implicit: 'page' | 'prev' | 'next';
    page: number;
  }>;
  @Input() @InputBoolean() frontPagination = true;
  @Input() @InputBoolean() templateMode = false;
  @Input() @InputBoolean() bordered = false;
  @Input() @InputBoolean() showPagination = true;
  @Input() @InputBoolean() loading = false;
  @Input() @InputBoolean() showSizeChanger = false;
  @Input() @InputBoolean() hideOnSinglePage = false;
  @Input() @InputBoolean() showQuickJumper = false;
  @Input() @InputBoolean() simple = false;
  @Input() @InputBoolean() checkboxSelect = false;
  @Input() @InputBoolean() inLineEdit = false;
  @Input() @InputBoolean() dataTable = false;
  @Input() @InputBoolean() showRate = false;
  @Input() exportEvent = new EventEmitter<GridExpConfig>();
  @Output() buttonClick = new EventEmitter<any>();
  @Output() rateChange = new EventEmitter<any>();
  @Output() selectionChange = new EventEmitter<T[]>();
  @Output() ondlclickRow = new EventEmitter<any>();
  @Output() onclickRow = new EventEmitter<any>();
  @Output() onedit = new EventEmitter<any>();
  @Output() ondrop = new EventEmitter<any>();
  @Input() rateCount = 5;
  @Input() @InputBoolean() multiSelect = false;
  @Output() sortChange = new EventEmitter<any>();
  @Output() onrowdeleted = new EventEmitter<any>();
  @Output() onrowadded = new EventEmitter<any>();

  @Input() extra: string | TemplateRef<void>;
  @Input() contextmenu: string | TemplateRef<void>;
  @Input() indentSize: number = 0;

  public dropdown: NzDropdownContextComponent;

  selected = false;
  defaultSortOrder = null;
  checkboxCache: CheckboxSelect[] = [];
  isIndeterminate = false;
  allChecked = false;
  editId: string | null;
  property: string | null;
  rowOnEdition = -1;
  nodeOnEdition = null;
  mapOfExpandedData: { [key: string]: any[] } = {};
  defaultTimeValue = new Date();

  fieldID: any = null;

  @ViewChild('fieldTypeInput', { read: ElementRef }) inputElement: ElementRef;
  @ViewChild('fieldTypeInputNumber', { read: ElementRef }) inputNumberElement: ElementRef;
  @ViewChild('fieldTypeDatePicker', { read: ElementRef }) datePickerElement: ElementRef;
  @ViewChild('fieldTypeDateTimePicker', { read: ElementRef}) dateTimePickerElement: ElementRef;
  @ViewChild('fieldTypeSelect', { read: ElementRef }) selectElement: ElementRef;
  @ViewChild('fieldTypeBool', { read: ElementRef }) boolElement: ElementRef;

  /*Context Menu */
  contextMenu($event: MouseEvent, template: TemplateRef<void>): void {
    this.dropdown = this.nzDropdownService.create($event, template);
  }

  validate(data: any, field: Field) {
    const formControl =  field.validators ? new FormControl(data[field.property], field.validators) : new FormControl(data[field.property]);
    data.valid = data.valid ? data.valid : {};
    data.valid[field.property] = formControl.valid;
    return formControl.valid;
  }

  addRow(idx: number) {
    this.onrowadded.emit(idx);
  }

  drop(event: CdkDragDrop<string[]>) {
    if (!this.draggable) {
      return;
    }
    moveItemInArray(this.data, event.previousIndex, event.currentIndex);
    this.data = [...this.data];
    this.ondrop.emit(event);
  }

  deleteRow(idx: number) {
    const itemToRemove = this.data[idx];
    this.onrowdeleted.emit(itemToRemove);
  }

  startEdit(id: string, property: string, event: MouseEvent): void {
    if (this.inLineEdit) {
      event.preventDefault();
      event.stopPropagation();
      if (this.editId !== null) {
        this.emitOnEditEvent();
      }
      this.editId = id;
      this.property = property;
    }
  }

  sort($event: any, fieldProperty: string, ){
    this.sortChange.emit({sortName: fieldProperty, sortValue: $event});
  }

  @HostListener('window:click', ['$event'])
  handleClick(e: Event): void {
    const element = e.target as HTMLElement;
    if (this.editId && this.property) {
      if (
        (this.inputElement && this.inputElement.nativeElement !== e.target) ||
        (this.inputNumberElement && !this.childOf(element, this.inputNumberElement.nativeElement)) ||
        (this.datePickerElement && !this.childOf(element, this.datePickerElement.nativeElement)) ||
        (this.dateTimePickerElement &&  !this.childOf(element, this.dateTimePickerElement.nativeElement)) ||
        (this.selectElement && !this.childOf(element, this.selectElement.nativeElement) ||
        (this.boolElement && this.boolElement.nativeElement !== e.target))
      ) {
        this.editId = null;
        this.emitOnEditEvent();
        this.property = null;
      }
    }
  }

 getCustomPadding(item: any, i: number) {
    if (!i) {
      if (!item.level) {
        return !!item.children ? 6 : 28;
      } else {
        return item.level * this.indentSize;
      }
    }
    return 6;
  }

  childOf( node: any, ancestor: any ) {
    let child = node;
    while (child !== null) {
      if (child === ancestor) return true;
      if (child.classList && child.classList.length > 0 && child.className && typeof child.className === 'string' &&
        child.className.indexOf('cdk-overlay-pane') >=0 ) return true;
      child = child.parentNode;
    }
    return false;
  }

  endEditMode($event: KeyboardEvent, index: number, data: any = null) {
    if ($event.key === ('Enter' || 'enter')) {
        this.editId = null;
        this.rowOnEdition = -1;
        if (this.expandable) {
          data.cmacsEditedProp = this.property;
          this.onedit.emit(data);
          return;
        }
        this.data[index].cmacsEditedProp = this.property;
        this.onedit.emit(this.data[index]);
        this.property = null;
    } else {
      if (this.expandable) {
        this.nodeOnEdition = data;
        return;
      }
      this.rowOnEdition = index;
    }
  }

  endEditModeNgModel(index: number, data: any = null, property: any = null) {
    if (this.expandable) {
      data.cmacsEditedProp = property ? property : this.property;
      this.onedit.emit(data);
    } else {
      this.data[index].cmacsEditedProp = property ? property : this.property;
      this.onedit.emit(this.data[index]);
    }
    this.editId = null;
    this.property = null;
    this.rowOnEdition = -1;
    this.nodeOnEdition = null;

  }

  ngModelChange(index: number, data: any = null) {
    if (this.expandable) {
      this.nodeOnEdition = data;
      return;
    }
    this.rowOnEdition = index;
  }

  getIndex(id): number {
    let i = -1;
    i = this.checkboxCache.findIndex(item => item.data[this.config.fieldId] === id);
    return i;
  }

  updateCheckboxCache(): void {
    this.checkboxCache.length = 0;
    this.data.forEach(item => {
      this.checkboxCache.push({
        selected: item.selected ? item.selected : false,
        data: { ...item }
      });
    });
  }

  checkChildrenState(item: any) {
    let indeterminate;
    let init = true;

    if (item.children && item.children.length > 0) {
      const res = this.checkChildrenStateRec(item.children);
      //console.log(res)
      /* if (init) {
         indeterminate = res;
       } else {
         return res !== indeterminate ? true : res;
       }
     } else {
       return false;
     }*/
    }
  }

  checkChildrenStateRec(item) {

    if (isArray(item)) {
      item.forEach(elem => {
        this.checkChildrenStateRec(elem);
      });
    } else {
      const node = this.getNode(item[this.fieldID]);
      return node.selected;
    }
  }

  updateCheckboxCacheTreeData() {
    this.convertExpandTreeToList(this.data, this.checkboxCache);
  }

  convertExpandTreeToList(item: any, plainList: any[]) {
    if (isArray(item)) {
      item.forEach(elem => {
        let elementInCache = plainList.findIndex(el => el.data[this.config.fieldId] === elem[this.config.fieldId]);
        if (elementInCache < 0) {
          plainList.push({
            selected: false,
            data: { ...elem }
          });
        }

        if (elem.children && elem.children.length > 0) {
          this.convertExpandTreeToList(elem.children, plainList);
        }
      });
    } else {
      let elementInCache = plainList.findIndex(el => el.data[this.config.fieldId] === item[this.config.fieldId]);
      if (elementInCache < 0) {
        plainList.push({
          selected: false,
          data: {...item}
        });
      }
    }
  }

  onButtonClick(field: any) {
    this.buttonClick.emit(field);
  }

  onCheckboxChange(oninit = false) {
    const dataSelected = this.checkCheckboxState();
    if (!oninit) {
      this.selectionChange.emit(dataSelected.map(item => item.data));
    }
  }

  checkCheckboxState (){
    const dataSelected = this.checkboxCache.filter(item => item.selected);
    this.selected = this.allChecked = dataSelected.length === this.checkboxCache.length;
    this.isIndeterminate = dataSelected.length > 0 && !this.allChecked;
    return dataSelected;
  }


  // tslint:disable-next-line: no-shadowed-variable
  onRateChange(count: number, data: any) {
    data[this.config.fieldRate] = count;
    this.rateChange.emit(data);
  }

  onRateClick(event: any) {
    event.preventDefault();
    event.stopPropagation();
  }

  getFields(): Field[] {
    if (this.config && this.config.fields) {
      return this.config.fields.filter(item => item.hidden === undefined || !item.hidden);
    }
    return [];
  }

  onCheckboxAllChange(status: boolean) {
    this.checkboxCache.forEach(element => {
      element.selected = status;
    });
    this.isIndeterminate = false;

    this.selectionChange.emit((status) ? this.data : []);
  }

  getLabel(data: any, field: Field): string {
    let result = '';
    if (field.editTemplate === TemplateType.Select && field.select !== undefined) {
      // tslint:disable-next-line: no-shadowed-variable
      const item = field.select.selectData.find(item => item !== undefined && item[field.select.value] === data[field.property]);
      result = (item !== undefined) ? item[field.select.label] : '';
    }

    return result;
  }

  isSelect(field: Field): boolean {
    return field !== undefined && field.editTemplate === TemplateType.Select;
  }

  isString(field: Field): boolean {
    return field !== undefined && field.editTemplate === TemplateType.String;
  }

  isReadOnly(field: Field): boolean{
    return field !== undefined && field.readonly !== undefined && field.readonly;
  }

  isTypeNumber(value: any): boolean {
    return value !== undefined && typeof (value) === 'number';
  }

  isNumber(field: Field): boolean {
    return field !== undefined && field.editTemplate === TemplateType.Number;
  }

  isBoolean(field: any): boolean {
    return field !== undefined && field.editTemplate === TemplateType.Boolean ;
  }

  isObject(value: any): boolean {
    return value !== undefined && typeof (value) === 'object';
  }

  isDate(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Default && field.editTemplate === TemplateType.Date;
  }

  isTime(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Default && field.editTemplate === TemplateType.Time;
  }

  isCeldTypeDefault(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Default;
  }

  isCeldTypeButton(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Button;
  }

  isCeldTypeTag(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Tag;
  }

  isCeldTypeTemplateRef(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.TemplateRef;
  }


  isUndefined(value: any) {
    return value === undefined;
  }

  isRowSelected(data: any): boolean {
    if (this.config) {
      const dataSelectd = this.checkboxCache.filter(item => item.selected).map(item => item.data[this.config.fieldId]);
      return dataSelectd.indexOf(data[this.config.fieldId]) !== -1;
    }
    return false;
  }

  constructor(
    private cdr: ChangeDetectorRef,
    private i18n: NzI18nService,
    private exportAsService: ExportAsService,
    private excelService: ExcelService,
    private datePipe: DatePipe,
    private nzDropdownService: NzDropdownService,
    private cookies: CookieService
  ) {
    // renderer.addClass(elementRef.nativeElement, 'ant-table-wrapper');
  }

  ngAfterViewInit() {
    if (this.cookies.check(this.gridID)) {
      const savedConfigStr = this.cookies.get(this.gridID);
      // reset expiration date
      this.cookies.set(this.gridID, savedConfigStr, 365);

      // parse cookie value to typescript const
      const savedConfig = JSON.parse(this.cookies.get(this.gridID)) as GridConfig;
      if (typeof savedConfig === "object") {
        this.config = savedConfig;
        this.configChange.emit(this.config);
      }
    }
    this.cdr.detectChanges();
  }

  ngOnInit(): void {
    this.i18n.localeChange.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.locale = this.i18n.getLocaleData('Table');
      this.cdr.markForCheck();
    });

    if (this.checkboxSelect && !this.expandable) {
      this.updateCheckboxCache();
      this.onCheckboxChange(true);
    }

    if (this.checkboxSelect && this.expandable && this.config) {
      this.updateCheckboxCacheTreeData();
    }

    this.checkCheckboxState();

    if (this.dataTable && this.data.length > 1) {
      while (this.data.length > 1) {
        this.data.pop();
      }
      this.showPagination = false;
    }

    this.exportEvent.subscribe((config: GridExpConfig) => {

      switch (config.exportType) {
        case ExportType.Pdf:
          this.exportToPdf(config.fileName);
          break;
        case ExportType.Xslx:
          this.exportToExcel(config.fileName);
          break;
        case ExportType.Png:
          this.exportToPng(config.fileName);
          break;
        case ExportType.PdfTree:
          this.exportTreePdf(config.fileName);
          break;
        case ExportType.XslxTree:
          this.exportTreeExcel(config.fileName);
          break;
      }
    });

    /* Convert tree to list if expandable */
    if (this.expandable && this.config) {
      this.fieldID = this.config.fieldId;
      const coerceData = this.data as any[];
      coerceData.forEach(item => {
        this.mapOfExpandedData[item[this.fieldID]] = this.convertTreeToList(item);
      });
    }

  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.data && this.config) {
      if (this.expandable) {

        if (!this.data.length) {
          this.checkboxCache = [];
          this.mapOfExpandedData = {};
        }

        this.updateCheckboxCacheTreeData();

        this.fieldID = this.config.fieldId;
        const coerceData = this.data as any[];
        coerceData.forEach(item => {

          if(!this.mapOfExpandedData[item[this.fieldID]]) {
            this.mapOfExpandedData[item[this.fieldID]] = this.convertTreeToList(item);
          }

        });
        this.onCheckboxChange();

      } else {
        this.updateCheckboxCache();
      }
    }
  }

 /* getTreeNodeByKey(node: any, key: any) {
    console.log(node)
    if (isArray(node)) {
      node.forEach(el => {
        this.getTreeNodeByKey(el, key);
      })
    } else if (node[this.fieldID] === key) {
      return node;
    } else if (node.children) {
      node.children.forEach(el => {
        this.getTreeNodeByKey(el, key);
      })
    }
  }*/

  /* Expandable Rows */
  exportTreePdf(fileName: string): void {
    const doc = new jsPDF();
    const columns = [];
    const rows = [];

    this.config.fields.filter(item => item.celdType === CeldType.Default).forEach(field => {
      columns.push(field.display);
    });

    this.config.fields.filter(item => item.celdType === CeldType.TemplateRef).forEach(field => {
      columns.push(field.display);
    });

    this.exportTreeToPdfRec(rows, this.data, 0);

    doc.autoTable({
      theme: 'striped',
      margin: { top: 5 },
      body: rows,
      columns
    });

    doc.save(fileName + '_export_' + Date.now());
  }

  exportTreeToPdfRec (rows: any, data: any, offSetMargin = 0) {

    data.forEach(item => {
      const itemToExport = [];
      const coercedItem = item as any;
      let parentStyles: any = {cellPadding:  [2, 2, 2, 2]};

      // tslint:disable-next-line: no-shadowed-variable
      this.config.fields.filter(item => item.celdType === CeldType.Default || item.celdType === CeldType.TemplateRef).forEach((field, idx) => {

        parentStyles = {cellPadding:  [2, 2, 2, 2]};
        if (!idx) {
          parentStyles.cellPadding = [2, 2, 2, 2 + offSetMargin];
        }

        if (coercedItem.children && coercedItem.children.length) {
          parentStyles.fillColor = [153, 204, 255];
        }

        if (item.celdType === CeldType.TemplateRef) {
          itemToExport.push({content: item[field.property].context.exportValue, styles: parentStyles});
        } else {
          switch (field.editTemplate) {
            case TemplateType.Select:
              const selectItem = field.select.selectData.find(option => option[field.select.value] === item[field.property]);

              if (selectItem !== undefined) {
                itemToExport.push({content: selectItem[field.select.label], styles: parentStyles});
              }
              break;
            case TemplateType.Date:
              itemToExport.push({content: this.datePipe.transform(item[field.property], 'MMMM dd yyyy'), styles: parentStyles});
              break;
            case TemplateType.Time:
              itemToExport.push({content: this.datePipe.transform(item[field.property], 'h:mm a'),  styles: parentStyles});
              break;
            default:
              itemToExport.push({content: item[field.property], styles: parentStyles});
              break;
          }
        }

      });

      rows.push(itemToExport);
      if (coercedItem.children && coercedItem.children.length) {
        this.exportTreeToPdfRec(rows, coercedItem.children, 5 + offSetMargin);
      }

    });
  }

  convertTreeToList(root: any): any[] {
    const stack: any[] = [];
    const array: any[] = [];
    const hashMap = {};
    stack.push({ ...root, level: 0, expand: this.expandAll ? this.expandAll : root.expand });

    while (stack.length !== 0) {
      const node = stack.pop();
      this.visitNode(node, hashMap, array);
      if (node.children) {
        for (let i = node.children.length - 1; i >= 0; i--) {
          stack.push(
            {...node.children[i],
              level: node.level + 1,
              expand: this.expandAll ? this.expandAll : node.children[i].expand,
              parent: node
            });
        }
      }
    }

    return array;
  }

  visitNode(node: any, hashMap: any, array: any[]): void {
    if (!hashMap[node[this.fieldID]]) {
      hashMap[node[this.fieldID]] = true;
      array.push(node);
    }
  }

  collapse(array: any[], data: any, $event: boolean): void {
    if ($event === false) {
      if (data.children) {
        data.children.forEach(d => {
          const target = array.find(a => a[this.fieldID] === d[this.fieldID])!;
          target.expand = false;
          this.collapse(array, target, false);
        });
      } else {
        return;
      }
    }
  }

  onCheckboxTreeChange($event, item) {
    this.updateTreeCheckboxes($event, this.data, item[this.fieldID]);
    this.onCheckboxChange();
  }

  updateTreeCheckboxes ($event: boolean, array: any, key: any) {

    if (isArray(array)) {
      array.forEach(item => {
        if (item[this.fieldID] === key) {
          this.getNode(key).selected = $event;
          item.selected = $event;
          if (item.children && item.children.length > 0) {
            item.children.forEach(c => {
              this.updateTreeCheckboxes($event, c, c[this.fieldID]);
            });
          }
          return;
        }
        if (item.children) {
          item.children.forEach((d: any) => {
            this.updateTreeCheckboxes($event, d, key);
          });
        } else {
          return;
        }
      });
    } else {
      if (array[this.fieldID] === key) {
        array.selected = $event;
        this.getNode(key).selected = $event;
        return;
      }
    }

  }

  getNode(key: any) {
    let test = this.checkboxCache.filter(n => n.data[this.fieldID] === key);
    return test ? test[0] : {selected: false};
  }

  exportToPng(fileName: string): void {
    const exportAsConfig: ExportAsConfig = {
      type: 'png', // the type you want to download
      elementId: this.gridID, // the id of html/table element
    };

    this.frontPagination = false;
    this.showPagination = false;
    const interval = setInterval(() => {
      // tslint:disable-next-line: max-line-length
      this.exportAsService.save(exportAsConfig, fileName + '_export_' + Date.now());
      this.frontPagination = true;
      this.showPagination = true;
      clearInterval(interval);
    }, 100);


  }

  exportToExcel(fileName: string): void {
    const dataToExport: any[] = [];

    this.data.forEach(item => {
      const itemToExport = {};
      // tslint:disable-next-line: no-shadowed-variable
      this.config.fields.filter(item => item.celdType === CeldType.Default || item.celdType === CeldType.TemplateRef).forEach(field => {
        if (field.editTemplate === TemplateType.Select) {
          const selectItem = field.select.selectData.find(option => option[field.select.value] === item[field.property]);

          if (selectItem !== undefined) {
            itemToExport[field.display] = selectItem[field.select.label];
          }
        } else if (field.celdType === CeldType.TemplateRef) {
          itemToExport[field.display] = item[field.property].context.exportValue;
        } else {
          itemToExport[field.display] = item[field.property];
        }
      });

      dataToExport.push(itemToExport);
    });

    this.excelService.exportAsExcelFile(dataToExport, fileName);
  }

  /* Expandable Rows */
  exportTreeExcel(fileName: string): void {
    const dataToExport: any[] = [];
    this.exportTreeExcelRec(this.data, dataToExport);
    this.excelService.exportAsExcelFile(dataToExport, fileName);
  }

  exportTreeExcelRec(data: any, dataToExport: any): void {

    data.forEach(item => {
      const itemToExport = {};
      // tslint:disable-next-line: no-shadowed-variable
      this.config.fields.filter(item => item.celdType === CeldType.Default || item.celdType === CeldType.TemplateRef).forEach(field => {
        if (item.celdType === CeldType.TemplateRef) {
          itemToExport[field.display] = item[field.property].context.exportValue;
        } else {
          if (field.editTemplate === TemplateType.Select) {
            const selectItem = field.select.selectData.find(option => option[field.select.value] === item[field.property]);

            if (selectItem !== undefined) {
              itemToExport[field.display] = selectItem[field.select.label];
            }
          } else {
            itemToExport[field.display] = item[field.property];
          }
        }
      });

      dataToExport.push(itemToExport);

      const coercedItem = item as any;
      if (coercedItem.children && coercedItem.children.length) {
        this.exportTreeExcelRec(coercedItem.children, dataToExport);
      }
    });

  }


  exportToPdf(fileName: string): void {
    const doc = new jsPDF();
    const columns = [];
    const rows = [];

    this.config.fields.filter(item => item.celdType === CeldType.Default || item.celdType === CeldType.TemplateRef).forEach(field => {
      columns.push(field.display);
    });

    this.data.forEach(item => {
      const itemToExport = [];
      // tslint:disable-next-line: no-shadowed-variable
      this.config.fields.filter(item => item.celdType === CeldType.Default || item.celdType === CeldType.TemplateRef).forEach(field => {
        if (field.celdType === CeldType.TemplateRef) {
          itemToExport.push(item[field.property].context.exportValue);
        } else {
          switch (field.editTemplate) {
            case TemplateType.Select:
              const selectItem = field.select.selectData.find(option => option[field.select.value] === item[field.property]);

              if (selectItem !== undefined) {
                itemToExport.push(selectItem[field.select.label]);
              }
              break;
            case TemplateType.Date:
              itemToExport.push(this.datePipe.transform(item[field.property], 'MMMM dd yyyy'));
              break;
            case TemplateType.Time:
              itemToExport.push(this.datePipe.transform(item[field.property], 'h:mm a'));
              break;
            default:
              itemToExport.push(item[field.property]);
              break;
          }
        }
      });

      rows.push(itemToExport);

    });

    doc.autoTable({
      theme: 'striped',
      margin: { top: 5 },
      body: rows,
      columns
    });

    doc.save(fileName + '_export_' + Date.now());
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  clickRow(data: any) {
    this.onclickRow.emit(data);
    if (!this.checkboxSelect) {
      if (!this.multiSelect) {
        this.checkboxCache.filter(item => item.selected && item.data[this.config.fieldId] !== data[this.config.fieldId])
          .forEach(elem => elem.selected = false);
      }
      const index = this.checkboxCache.findIndex(item => item.data[this.config.fieldId] === data[this.config.fieldId]);
      if (index !== -1) {
        this.checkboxCache[index].selected = !this.checkboxCache[index].selected;
      }
    }
    this.selectionChange.emit(this.checkboxCache.filter(item => item.selected).map(item => item.data));
  }

  dblClickRow(data: any) {
    this.ondlclickRow.emit(data);
  }

  clickBooleanCell(data: any, id: any, property: string, event: MouseEvent): void {
    if (data[property] === null || data[property] === undefined) {
      data[property] = true;
    } else {
      data[property] = (data[property] === false || data[property] === 'false');
    }
    this.endEditModeNgModel(id, data, property);
  }

  emitOnEditEvent() {
    if (this.rowOnEdition >= 0) {
      this.data[this.rowOnEdition].cmacsEditedProp = this.property;
      this.onedit.emit(this.data[this.rowOnEdition]);
      this.rowOnEdition = -1;
      return;
    }
    if (this.nodeOnEdition) {
      this.nodeOnEdition.cmacsEditedProp = this.property;
      this.onedit.emit(this.nodeOnEdition);
      this.nodeOnEdition = null;
    }
  }

  getClassMap(field: Field) {
    return {
      [`cmacs-compact-table-logs-header-th`] : this.logs,
      [`cmacs-compact-table-logs-header-th-font`] : this.logs,
      [`${field.ngClass}`]: field.ngClass && field.ngClass.length
    };
  }
}

