import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Host,
  Injector,
  Input,
  OnChanges,
  OnDestroy,
  Optional,
  Output,
  Self,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

import { slideMotion, NzDropdownHigherOrderServiceToken, NzNoAnimationDirective } from 'ng-zorro-antd/core';

import { menuServiceFactory, CmacsDropdownComponent } from './cmacs-dropdown.component';
import { CmacsDropdownDirective } from './cmacs-dropdown.directive';
import { CmacsMenuDropdownService } from './cmacs-menu-dropdown.service';

@Component({
  selector: 'cmacs-dropdown-button',
  exportAs: 'cmacsDropdownButton',
  preserveWhitespaces: false,
  animations: [slideMotion],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    CmacsMenuDropdownService,
    {
      provide: NzDropdownHigherOrderServiceToken,
      useFactory: menuServiceFactory,
      deps: [[new Self(), Injector]]
    }
  ],
  templateUrl: './cmacs-dropdown-button.component.html',
  styles: [
    `
      cmacs-dropdown-button {
        position: relative;
        display: inline-block;
      }

      .ant-dropdown {
        top: 100%;
        left: 0;
        position: relative;
        width: 100%;
        margin-top: 4px;
        margin-bottom: 4px;
      }
    `
  ]
})
export class CmacsDropdownButtonComponent extends CmacsDropdownComponent implements OnDestroy, AfterContentInit, OnChanges {
  @Input() size = 'default';
  @Input() type = 'default';
  @Output() readonly cmacsClick= new EventEmitter<MouseEvent>();
  @ViewChild(CmacsDropdownDirective) cmacsDropDownDirective: CmacsDropdownDirective;

  constructor(
    cdr: ChangeDetectorRef,
    cmacsMenuDropdownService: CmacsMenuDropdownService,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    super(cdr, cmacsMenuDropdownService, noAnimation);
  }

  /** rewrite afterViewInit hook */
  ngAfterContentInit(): void {
    this.startSubscribe(this.visible$);
  }
}
