import {
  ChangeDetectionStrategy,
  Component,
  Input,
  OnChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

import { InputBoolean } from 'ng-zorro-antd/core';
import { Subject } from 'rxjs';

@Component({
// tslint:disable-next-line: component-selector
  selector: 'cmacs-option',
  exportAs: 'cmacsOption',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './cmacs-option.component.html'
})
export class CmacsOptionComponent implements OnChanges {
  changes = new Subject();
  @ViewChild(TemplateRef) template: TemplateRef<void>;
// tslint:disable-next-line: no-input-rename
  @Input('label') nzLabel: string;
  @Input('divider') divider: string;
  // tslint:disable-next-line:no-any
  @Input('value') nzValue: any;
  @Input('disabled') @InputBoolean() nzDisabled = false;
  @Input('customContent') @InputBoolean() nzCustomContent = false;

  ngOnChanges(): void {
    this.changes.next();
  }
}
