import {
  forwardRef,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Host,
  Optional,
  Renderer2,
  ViewEncapsulation, Input, OnInit
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

import { NzNoAnimationDirective } from 'ng-zorro-antd/core';
import { DateHelperService, NzI18nService } from 'ng-zorro-antd/i18n';

import { CmacsDateRangePickerComponent } from './date-range-picker.component';

@Component({
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
// tslint:disable-next-line: component-selector
  selector: 'cmacs-range-picker',
  exportAs: 'cmacsRangePicker',
  templateUrl: './date-range-picker.component.html',
  styleUrls: ['./date-range-picker.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => CmacsRangePickerComponent)
    }
  ],
  host: {
    '[class.cmacs-open-range-picker]': 'cmacsOpen'
  }
})
export class CmacsRangePickerComponent extends CmacsDateRangePickerComponent implements OnInit{
  isRange = true;
  showWeek = false;
  @Input() openPickerTitle = '';
  @Input() openPickerLeftRangeSub = '';
  @Input() openPickerRightRangeSub = '';

  constructor(
    i18n: NzI18nService,
    cdr: ChangeDetectorRef,
    dateHelper: DateHelperService,
    renderer: Renderer2,
    elementRef: ElementRef,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    super(i18n, cdr, dateHelper, noAnimation);
    renderer.addClass(elementRef.nativeElement, 'ant-calendar-picker');
  }

  ngOnInit() {
    if (this.mode === 'week') {
      this.showWeek = true;
    }
    super.ngOnInit();
  }

}
