import {
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  Input,
  QueryList,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { CmacsCommentActionComponent as CommentAction } from './cmacs-comment-cells';

@Component({
  selector: 'cmacs-comment',
  exportAs: 'cmacsComment',
  templateUrl: './cmacs-comment.component.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    class: 'ant-comment'
  },
  styleUrls: ['./cmacs-comment.component.css'],
  styles: [
    `
      cmacs-comment {
        display: block;
      }

      cmacs-comment-content {
        display: block;
      }
    `
  ]
})
export class CmacsCommentComponent {
  @Input() author: string | TemplateRef<void>;
  @Input() datetime: string | TemplateRef<void>;

  @ContentChildren(CommentAction) actions: QueryList<CommentAction>;
  constructor() {}
}
