import { AnimationEvent } from '@angular/animations';
import {
  CdkConnectedOverlay,
  CdkOverlayOrigin,
  ConnectedOverlayPositionChange,
  ConnectionPositionPair
} from '@angular/cdk/overlay';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  EventEmitter,
  Host,
  Input,
  OnChanges,
  Optional,
  Output,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

import {
  getPlacementName,
  isNotNil,
  toBoolean,
  zoomBigMotion,
  DEFAULT_TOOLTIP_POSITIONS,
  NzNoAnimationDirective,
  POSITION_MAP
} from 'ng-zorro-antd/core';

@Component({
  selector: 'cmacs-tooltip',
  exportAs: 'cmacsTooltipComponent',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [zoomBigMotion],
  templateUrl: './cmacs-tooltip.component.html',
  preserveWhitespaces: false,
  styleUrls: ['./cmacs-tooltip.component.css'],
  styles: [
    `
      .ant-tooltip {
        position: relative;
      }
    `
  ]
})
export class CmacsTooltipComponent implements OnChanges {
  _hasBackdrop = false;
  _prefix = 'ant-tooltip-placement';
  _positions: ConnectionPositionPair[] = [...DEFAULT_TOOLTIP_POSITIONS];
  _classMap = {};
  _placement = 'top';
  _trigger = 'hover';
  overlayOrigin: CdkOverlayOrigin;
  visibleSource = new BehaviorSubject<boolean>(false);
  visible$: Observable<boolean> = this.visibleSource.asObservable();
  @ViewChild('overlay') overlay: CdkConnectedOverlay;
  @Input() @ContentChild('nzTemplate') title: string | TemplateRef<void> | null;
  @Input() overlayClassName = '';
  @Input() overlayStyle: { [key: string]: string } = {};
  @Input() mouseEnterDelay = 0.15; // second
  @Input() mouseLeaveDelay = 0.1; // second

  @Input()
  set cmacsVisible(value: boolean) {
    const visible = toBoolean(value);
    if (this.visibleSource.value !== visible) {
      this.visibleSource.next(visible);
      this.cmacsVisibleChange.emit(visible);
    }
  }

  get cmacsVisible(): boolean {
    return this.visibleSource.value;
  }

  @Input()
  set trigger(value: string) {
    this._trigger = value;
    this._hasBackdrop = this._trigger === 'click';
  }

  get trigger(): string {
    return this._trigger;
  }

  @Input()
  set placement(value: string) {
    if (value !== this._placement) {
      this._placement = value;
      this._positions = [POSITION_MAP[this.placement], ...this._positions];
    }
  }

  get placement(): string {
    return this._placement;
  }

  @Output() readonly cmacsVisibleChange = new EventEmitter<boolean>();

  [property: string]: any; // tslint:disable-line:no-any

  constructor(public cdr: ChangeDetectorRef, @Host() @Optional() public noAnimation?: NzNoAnimationDirective) {}

  ngOnChanges(): void {
    Promise.resolve().then(() => {
      this.updatePosition();
    });
  }

  // Manually force updating current overlay's position
  updatePosition(): void {
    if (this.overlay && this.overlay.overlayRef) {
      this.overlay.overlayRef.updatePosition();
    }
  }

  onPositionChange(position: ConnectedOverlayPositionChange): void {
    this.placement = getPlacementName(position)!;
    this.setClassMap();
    this.cdr.detectChanges(); // TODO: performance?
  }

  show(): void {
    if (!this.isContentEmpty()) {
      this.cmacsVisible = true;
    }
  }

  hide(): void {
    this.cmacsVisible = false;
  }

  _afterVisibilityAnimation(e: AnimationEvent): void {
    if (e.toState === 'false' && !this.cmacsVisible) {
      this.cmacsVisibleChange.emit(false);
    }
    if (e.toState === 'true' && this.cmacsVisible) {
      this.cmacsVisibleChange.emit(true);
    }
  }

  setClassMap(): void {
    this._classMap = {
      [this.overlayClassName]: true,
      [`${this._prefix}-${this._placement}`]: true
    };
  }

  setOverlayOrigin(origin: CdkOverlayOrigin): void {
    this.overlayOrigin = origin;
  }

  protected isContentEmpty(): boolean {
    return this.title instanceof TemplateRef ? false : this.title === '' || !isNotNil(this.title);
  }
}
