
import { InjectionToken } from '@angular/core';

export interface CmacsMessageConfig {
  animate?: boolean;
  duration?: number;
  maxStack?: number;
  pauseOnHover?: boolean;
  top?: number | string;
  width?: number;

  [index: string]: any; // tslint:disable-line:no-any
}

export const CMACS_MESSAGE_DEFAULT_CONFIG = new InjectionToken<CmacsMessageConfig>('CMACS_MESSAGE_DEFAULT_CONFIG');

export const CMACS_MESSAGE_CONFIG = new InjectionToken<CmacsMessageConfig>('CMACS_MESSAGE_CONFIG');

export const CMACS_MESSAGE_DEFAULT_CONFIG_PROVIDER = {
  provide: CMACS_MESSAGE_DEFAULT_CONFIG,
  useValue: {
    animate: true,
    duration: 3000,
    maxStack: 7,
    pauseOnHover: true,
    top: 24
  }
};
