
import {
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  ElementRef,
  Input,
  QueryList,
  Renderer2,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { CmacsListItemMetaComponent } from './cmacs-list-item-meta.component';

@Component({
  selector: 'cmacs-list-item',
  exportAs: 'cmacsListItem',
  templateUrl: './cmacs-list-item.component.html',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CmacsListItemComponent {
  @ContentChildren(CmacsListItemMetaComponent) metas!: QueryList<CmacsListItemMetaComponent>;
  @Input() actions: Array<TemplateRef<void>> = [];
  @Input() content: string | TemplateRef<void>;
  @Input() extra: TemplateRef<void>;

  constructor(public elementRef: ElementRef, private renderer: Renderer2) {
    this.renderer.addClass(this.elementRef.nativeElement, 'ant-list-item');
  }
}
