import { Component, OnInit, Input, forwardRef} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { toBoolean, InputBoolean } from 'ng-zorro-antd';

@Component({
  selector: 'cmacs-time-picker',
  templateUrl: './cmacs-time-picker.component.html',
  styleUrls: ['./cmacs-time-picker.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CmacsTimePickerComponent),
      multi: true
    }
  ],
  // changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CmacsTimePickerComponent implements ControlValueAccessor, OnInit {
  @Input() defaultOpenValue = new Date();
  @Input() @InputBoolean() disabled: boolean = false;
  @Input() @InputBoolean() allowEmpty = true;
  @Input() @InputBoolean() use12Hours = false;

  @Input() placeholder: string = "Select a time";
  @Input() format = 'HH:mm:ss';
  @Input() _open = false;
  public _value: Date | null = null;
  private _onChange: (value: Date | null) => void;
  private _onTouched: () => void;

  onModelChange(value: any): void {
    this.value = value;
  }

  set value(value: Date | null) {
    console.log("set value");

    this._value = value;
    if (this._onChange) {
      this._onChange(this.value);
    }
    if (this._onTouched) {
      this._onTouched();
    }
  }

  get value(): Date | null {
    return this._value;
  }

  constructor() {}

  ngOnInit(): void {
  }

  writeValue(time: Date | null): void {
    this.value = time;
  }

  registerOnChange(fn: (time: Date | null) => void): void {
    this._onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this._onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
}
