import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  TemplateRef,
  ViewChild,
  ViewChildren,
  ViewEncapsulation
} from '@angular/core';
import { fromEvent, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { CmacsOptionGroupComponent } from './cmacs-option-group.component';
import { CmacsOptionLiComponent } from './cmacs-option-li.component';
import { CmacsOptionComponent } from './cmacs-option.component';
import { CmacsSelectService } from './cmacs-select.service';

@Component({
  selector: '[cmacs-option-container]',
  exportAs: 'cmacsOptionContainer',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  templateUrl: './cmacs-option-container.component.html',
  styleUrls: ['./cmacs-option-container.component.css']
})
export class CmacsOptionContainerComponent implements OnDestroy, OnInit {
  private destroy$ = new Subject();
  private lastScrollTop = 0;
  inputValue: string;
  @ViewChildren(CmacsOptionLiComponent) listOfNzOptionLiComponent: QueryList<CmacsOptionLiComponent>;
  @ViewChild('dropdownUl') dropdownUl: ElementRef;
  @ViewChild('inputElement') inputElement: ElementRef;
  @Input() nzNotFoundContent: string;
  @Input() showSearch = false;
  @Input() showCmacsSearch = false;
  @Input() nzMenuItemSelectedIcon: TemplateRef<void>;
  @Output() readonly nzScrollToBottom = new EventEmitter<void>();
  @Output() readonly onSearch = new EventEmitter<string>();

  scrollIntoViewIfNeeded(option: CmacsOptionComponent): void {
    // delay after open
    setTimeout(() => {
      if (this.listOfNzOptionLiComponent && this.listOfNzOptionLiComponent.length && option) {
        const targetOption = this.listOfNzOptionLiComponent.find(o =>
          this.nzSelectService.compareWith(o.nzOption.nzValue, option.nzValue)
        );
        /* tslint:disable:no-any */
        if (targetOption && targetOption.el && (targetOption.el as any).scrollIntoViewIfNeeded) {
          (targetOption.el as any).scrollIntoViewIfNeeded(false);
        }
        /* tslint:enable:no-any */
      }
    });
  }

  setInputValue(value: string): void {
    if (this.inputElement) {
      this.inputElement.nativeElement.value = value;
    }
    this.onSearch.emit(value);
  }

  trackLabel(_index: number, option: CmacsOptionGroupComponent): string | TemplateRef<void> {
    return option.nzLabel;
  }

  // tslint:disable-next-line:no-any
  trackValue(_index: number, option: CmacsOptionComponent): any {
    return option.nzValue;
  }

  constructor(public nzSelectService: CmacsSelectService, private cdr: ChangeDetectorRef, private ngZone: NgZone) {}

  ngOnInit(): void {
    this.nzSelectService.activatedOption$.pipe(takeUntil(this.destroy$)).subscribe(option => {
      this.scrollIntoViewIfNeeded(option!);
    });
    this.nzSelectService.check$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.cdr.markForCheck();
    });
    this.ngZone.runOutsideAngular(() => {
      const ul = this.dropdownUl.nativeElement;
      fromEvent<MouseEvent>(ul, 'scroll')
        .pipe(takeUntil(this.destroy$))
        .subscribe(e => {
          e.preventDefault();
          e.stopPropagation();
          if (ul && ul.scrollTop > this.lastScrollTop && ul.scrollHeight < ul.clientHeight + ul.scrollTop + 10) {
            this.lastScrollTop = ul.scrollTop;
            this.ngZone.run(() => {
              this.nzScrollToBottom.emit();
            });
          }
        });
    });
  }

  isAllChecked() {
    const listOfNotDisabledItems = this.nzSelectService.listOfNzOptionComponent.filter(item => !item.nzDisabled);
    if (this.nzSelectService.listOfCachedSelectedOption.length ===  listOfNotDisabledItems.length) {
      return 1;
    } else if (!this.nzSelectService.listOfCachedSelectedOption.length) {
      return 0;
    } else {
      return -1;
    }
  }

  updateCheckboxCache() {
    const isCheckedState = this.isAllChecked();

    if (!isCheckedState || isCheckedState === -1) {
      const listOfSelectedValue = [];
      this.nzSelectService.listOfNzOptionComponent.forEach(option => {
        if (!option.nzDisabled) {
          listOfSelectedValue.push(option.nzValue);
        }
      });
      this.nzSelectService.updateListOfSelectedValue(listOfSelectedValue, true);
    } else {
      this.nzSelectService.listOfCachedSelectedOption.forEach(option => {
        this.nzSelectService.removeValueFormSelected(option);
      });
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
