import {
  AfterContentInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  Input,
  QueryList,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { InputBoolean, NgClassType, NzSizeLDSType } from 'ng-zorro-antd/core';
import { CmacsInputDirective } from './cmacs-input.directive';

@Component({
// tslint:disable-next-line: component-selector
  selector: 'cmacs-input-group',
  exportAs: 'cmacsInputGroup',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './cmacs-input-group.component.html',
// tslint:disable-next-line: use-host-property-decorator
  host: {
    '[class.ant-input-group-compact]': 'compact',
    '[class.ant-input-search-enter-button]': 'search',
    '[class.ant-input-search]': 'search',
    '[class.ant-input-search-sm]': 'isSmallSearch',
    '[class.ant-input-affix-wrapper]': 'isAffixWrapper',
    '[class.ant-input-group-wrapper]': 'isAddOn',
    '[class.ant-input-group]': 'isGroup',
    '[class.ant-input-group-lg]': 'isLargeGroup',
    '[class.ant-input-group-wrapper-lg]': 'isLargeGroupWrapper',
    '[class.ant-input-affix-wrapper-lg]': 'isLargeAffix',
    '[class.ant-input-search-lg]': 'isLargeSearch',
    '[class.ant-input-group-sm]': 'isSmallGroup',
    '[class.ant-input-affix-wrapper-sm]': 'isSmallAffix',
    '[class.ant-input-group-wrapper-sm]': 'isSmallGroupWrapper'
  }
})
export class CmacsInputGroupComponent implements AfterContentInit {
  @ContentChildren(CmacsInputDirective) listOfNzInputDirective: QueryList<CmacsInputDirective>;
// tslint:disable-next-line: variable-name
  private _size: NzSizeLDSType = 'default';
  @Input() addOnBeforeIcon: NgClassType;
  @Input() addOnAfterIcon: NgClassType;
  @Input() prefixIcon: NgClassType;
  @Input() suffixIcon: NgClassType;
  @Input() addOnBefore: string | TemplateRef<void>;
  @Input() addOnAfter: string | TemplateRef<void>;
  @Input() prefix: string | TemplateRef<void>;
  @Input() suffix: string | TemplateRef<void>;
  @Input() @InputBoolean() search = false;
  @Input() @InputBoolean() compact = false;

  @Input() set size(value: NzSizeLDSType) {
    this._size = value;
    this.updateChildrenInputSize();
  }

  get size(): NzSizeLDSType {
    return this._size;
  }

  get isLarge(): boolean {
    return this.size === 'large';
  }

  get isSmall(): boolean {
    return this.size === 'small';
  }

  get isAffix(): boolean {
    return !!(this.suffix || this.prefix || this.prefixIcon || this.suffixIcon);
  }

  get isAddOn(): boolean {
    return !!(this.addOnAfter || this.addOnBefore || this.addOnAfterIcon || this.addOnBeforeIcon);
  }

  get isAffixWrapper(): boolean {
    return this.isAffix && !this.isAddOn;
  }

  get isGroup(): boolean {
    return !this.isAffix && !this.isAddOn;
  }

  get isLargeGroup(): boolean {
    return this.isGroup && this.isLarge;
  }

  get isLargeGroupWrapper(): boolean {
    return this.isAddOn && this.isLarge;
  }

  get isLargeAffix(): boolean {
    return this.isAffixWrapper && this.isLarge;
  }

  get isLargeSearch(): boolean {
    return this.search && this.isLarge;
  }

  get isSmallGroup(): boolean {
    return this.isGroup && this.isSmall;
  }

  get isSmallAffix(): boolean {
    return this.isAffixWrapper && this.isSmall;
  }

  get isSmallGroupWrapper(): boolean {
    return this.isAddOn && this.isSmall;
  }

  get isSmallSearch(): boolean {
    return this.search && this.isSmall;
  }

  updateChildrenInputSize(): void {
    if (this.listOfNzInputDirective) {
      this.listOfNzInputDirective.forEach(item => (item.size = this.size));
    }
  }

  ngAfterContentInit(): void {
    this.updateChildrenInputSize();
  }
}
