import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  Host,
  Input,
  Optional,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { isNotNil, zoomBigMotion, NzNoAnimationDirective } from 'ng-zorro-antd/core';
import {CmacsTooltipComponent } from "../cmacs-tooltip/cmacs-tooltip.component";

@Component({
  selector: 'cmacs-popover',
  exportAs: 'cmacsPopoverComponent',
  animations: [zoomBigMotion],
  templateUrl: './cmacs-popover.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  styleUrls: ['./cmacs-popover.component.css'],
  styles: [
    `
      .ant-popover {
        position: relative;
      }
    `
  ]
})
export class CmacsPopoverComponent extends CmacsTooltipComponent {
  _prefix = 'ant-popover-placement';

  /** Used to remove NzToolTipComponent @ContentChild('nzTemplate') */
  @Input() @ContentChild('neverUsedTemplate') title: string | TemplateRef<void>;
  @Input() @ContentChild('nzTemplate') content: string | TemplateRef<void>;

  constructor(cdr: ChangeDetectorRef, @Host() @Optional() public noAnimation?: NzNoAnimationDirective) {
    super(cdr, noAnimation);
  }

  protected isContentEmpty(): boolean {
    const isTitleEmpty = this.title instanceof TemplateRef ? false : this.title === '' || !isNotNil(this.title);
    const isContentEmpty =
      this.content instanceof TemplateRef ? false : this.content === '' || !isNotNil(this.content);
    return isTitleEmpty && isContentEmpty;
  }
}
