import {
  AfterContentInit,
  ContentChildren,
  Directive,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  SimpleChanges,
  SkipSelf
} from '@angular/core';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  InputBoolean,
  NzDirectionVHIType,
  NzDropdownHigherOrderServiceToken,
  NzMenuBaseService,
  NzUpdateHostClassService
} from 'ng-zorro-antd/core';

import { CmacsMenuItemDirective } from './cmacs-menu-item.directive';
import { CmacsMenuServiceFactory } from './cmacs-menu.resolver';
import { CmacsMenuService } from './cmacs-menu.service';
import { CmacsSubMenuComponent } from './cmacs-submenu.component';

export type CmacsMenuModeType = 'side-bar';

@Directive({
// tslint:disable-next-line: directive-selector
  selector: '[cmacs-menu]',
  exportAs: 'cmacsMenu',
  providers: [
    NzUpdateHostClassService,
    CmacsMenuService,
    {
      provide: NzMenuBaseService,
      useFactory: CmacsMenuServiceFactory,
      deps: [[new SkipSelf(), new Optional(), NzDropdownHigherOrderServiceToken], CmacsMenuService]
    }
  ]
})
export class CmacsMenuDirective implements AfterContentInit, OnInit, OnChanges, OnDestroy {
  private destroy$ = new Subject();
  private cacheMode: NzDirectionVHIType;
  private listOfOpenedCmacsSubMenuComponent: CmacsSubMenuComponent[] = [];
  @ContentChildren(CmacsMenuItemDirective, { descendants: true }) listOfCmacsMenuItemDirective: QueryList<
    CmacsMenuItemDirective
  >;
  @ContentChildren(CmacsSubMenuComponent, { descendants: true }) listOfCmacsSubMenuComponent: QueryList<CmacsSubMenuComponent>;
  @Input() inlineIndent = 24;
  @Input() theme: 'light' | 'dark' = 'light';
  @Input() mode: NzDirectionVHIType = 'vertical';
  @Input() cmacsMode: CmacsMenuModeType | null = null;
  @Input() @InputBoolean() inDropDown = false;
  @Input() @InputBoolean() inlineCollapsed = false;
  @Input() @InputBoolean() selectable = !this.menuService.isInDropDown;
  @Output() readonly click = new EventEmitter<CmacsMenuItemDirective>();

  updateInlineCollapse(): void {
    if (this.listOfCmacsMenuItemDirective) {
      if (this.inlineCollapsed) {
        this.listOfOpenedCmacsSubMenuComponent = this.listOfCmacsSubMenuComponent.filter(submenu => submenu.open);
        this.listOfCmacsSubMenuComponent.forEach(submenu => submenu.setOpenState(false));
        this.mode = 'vertical';
      } else {
        this.listOfOpenedCmacsSubMenuComponent.forEach(submenu => submenu.setOpenState(true));
        this.listOfOpenedCmacsSubMenuComponent = [];
        this.mode = this.cacheMode;
      }
      this.menuService.setMode(this.mode);
    }
  }

  setClassMap(): void {
    const prefixName = this.menuService.isInDropDown ? 'ant-dropdown-menu' : 'ant-menu';
    this.updateHostClassService.updateHostClass(this.elementRef.nativeElement, {
      [`${prefixName}`]: true,
      [`${prefixName}-root`]: true,
      [`${prefixName}-${this.theme}`]: true,
      [`${prefixName}-${this.mode}`]: true,
      [`${prefixName}-inline-collapsed`]: this.inlineCollapsed,
      [`cmacs-menu-side-bar`]: this.cmacsMode === 'side-bar'
    });
  }

  constructor(
    public elementRef: ElementRef,
    private menuService: NzMenuBaseService,
    private updateHostClassService: NzUpdateHostClassService
  ) {}

  ngOnInit(): void {
    this.setClassMap();
    this.menuService.menuItemClick$.pipe(takeUntil(this.destroy$)).subscribe(menu => {
      this.click.emit(menu);
      if (this.selectable) {
        this.listOfCmacsMenuItemDirective.forEach(item => item.setSelectedState(item === menu));
      }
    });
  }

  ngAfterContentInit(): void {
    this.cacheMode = this.mode;
    this.updateInlineCollapse();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.inlineCollapsed) {
      this.updateInlineCollapse();
    }
    if (changes.inlineIndent) {
      this.menuService.setInlineIndent(this.inlineIndent);
    }
    if (changes.inDropDown) {
      this.menuService.isInDropDown = this.inDropDown;
    }
    if (changes.theme) {
      this.menuService.setTheme(this.theme);
    }
    if (changes.mode) {
      this.menuService.setMode(this.mode);
      if (!changes.mode.isFirstChange() && this.listOfCmacsSubMenuComponent) {
        this.listOfCmacsSubMenuComponent.forEach(submenu => submenu.setOpenState(false));
      }
    }
    if (changes.theme || changes.mode || changes.inlineCollapsed) {
      this.setClassMap();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
