import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { slideAlertMotion, InputBoolean, NgClassType } from 'ng-zorro-antd/core';

@Component({
  selector: 'cmacs-alert',
  exportAs: 'cmacsAlert',
  animations: [slideAlertMotion],
  templateUrl: './cmacs-alert.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  styleUrls: ['./cmacs-alert.component.css'],
  styles: [
    `
      cmacs-alert {
        display: block;
      }
    `
  ]
})
export class CmacsAlertComponent implements OnChanges {
  destroy = false;
  iconType = 'info-circle';
  iconTheme = 'fill';
  private isTypeSet = false;
  private isShowIconSet = false;
  @Input() closeText: string | TemplateRef<void>;
  @Input() cmacsIconType: NgClassType;
  @Input() message: string | TemplateRef<void>;
  @Input() description: string | TemplateRef<void>;
  @Input() type: 'success' | 'info' | 'warning' | 'error' = 'info';
  @Input() @InputBoolean() closeable = false;
  @Input() @InputBoolean() showIcon = false;
  @Input() @InputBoolean() banner = false;
  @Output() readonly onClose = new EventEmitter<boolean>();

  closeAlert(): void {
    this.destroy = true;
  }

  onFadeAnimationDone(): void {
    if (this.destroy) {
      this.onClose.emit(true);
    }
  }

  updateIconClassMap(): void {
    switch (this.type) {
      case 'error':
        this.iconType = 'close-circle';
        break;
      case 'success':
        this.iconType = 'check-circle';
        break;
      case 'info':
        this.iconType = 'info-circle';
        break;
      case 'warning':
        this.iconType = 'exclamation-circle';
        break;
    }
    this.iconTheme = this.description ? 'outline' : 'fill';
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { showIcon, description, type, banner } = changes;
    if (showIcon) {
      this.isShowIconSet = true;
    }
    if (description || type) {
      this.updateIconClassMap();
    }
    if (type) {
      this.isTypeSet = true;
    }
    if (banner) {
      if (!this.isTypeSet) {
        this.type = 'warning';
      }
      if (!this.isShowIconSet) {
        this.showIcon = true;
      }
    }
  }
}
