import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef, HostListener,
  EventEmitter,
  Output,
  Input,
  Renderer2,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

import { NgClassType } from 'ng-zorro-antd/core';
import {InputBoolean} from "ng-zorro-antd";

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'cmacs-step',
  exportAs: 'cmacsStep',
  preserveWhitespaces: false,
  styleUrls: ['./cmacs-step.component.css'],
  templateUrl: './cmacs-step.component.html',
  host: {
    '[class.ant-steps-item-wait]': 'status === "wait"',
    '[class.ant-steps-item-process]': 'status === "process"',
    '[class.ant-steps-item-finish]': 'status === "finish"',
    '[class.ant-steps-item-error]': 'status === "error"',
    '[class.ant-steps-custom]': '!!nzIcon',
    '[class.cmacs-steps-clickable]': 'clickable',
    '[class.ant-steps-next-error]': '(outStatus === "error") && (currentIndex === index + 1)'
  }
})
export class CmacsStepComponent {
  @ViewChild('processDotTemplate') processDotTemplate: TemplateRef<void>;

  @Input() title: string | TemplateRef<void>;
  @Input() parent: ElementRef;
  @Input() secondary = false;
  @Input() description: string | TemplateRef<void>;
  @Input() @InputBoolean() clickable = false;

  @Input()
  get status(): string {
    return this._status;
  }

  set status(status: string) {
    this._status = status;
    this.isCustomStatus = true;
  }

  isCustomStatus = false;
  private _status = 'wait';

  @HostListener('click', ['$event']) onClick(event: Event) {
    var customEvent = new CustomEvent(
      'indexChange',
      {
        detail: { 'index': this.index },
        bubbles: true
      }
    );
    this.parent.nativeElement.dispatchEvent(customEvent);
  }

  @Input()
  get nzIcon(): NgClassType | TemplateRef<void> {
    return this._icon;
  }

  set nzIcon(value: NgClassType | TemplateRef<void>) {
    if (!(value instanceof TemplateRef)) {
      this.isIconString = true;
      this.oldAPIIcon = typeof value === 'string' && value.indexOf('anticon') > -1;
    } else {
      this.isIconString = false;
    }
    this._icon = value;
  }

  oldAPIIcon = true;
  isIconString = true;
  private _icon: NgClassType | TemplateRef<void>;

  customProcessTemplate: TemplateRef<{ $implicit: TemplateRef<void>; status: string; index: number }>; // Set by parent.
  direction = 'horizontal';
  index = 0;
  last = false;
  outStatus = 'process';
  showProcessDot = false;

  get currentIndex(): number {
    return this._currentIndex;
  }

  set currentIndex(current: number) {
    this._currentIndex = current;
    if (!this.isCustomStatus) {
      this._status = current > this.index ? 'finish' : current === this.index ? this.outStatus || '' : 'wait';
    }
  }

  private _currentIndex = 0;

  constructor(private cdr: ChangeDetectorRef, renderer: Renderer2, elementRef: ElementRef) {
    renderer.addClass(elementRef.nativeElement, 'ant-steps-item');
  }

  markForCheck(): void {
    this.cdr.markForCheck();
  }
}
