import {
  AfterContentInit, AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  HostBinding,
  Inject,
  Input,
  NgZone,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  QueryList,
  Renderer2,
  SimpleChanges,
  ViewChild, ViewChildren,
  ViewEncapsulation
} from '@angular/core';
import { ANIMATION_MODULE_TYPE } from '@angular/platform-browser/animations';

import {
  findFirstNotEmptyNode,
  findLastNotEmptyNode,
  isEmpty,
  InputBoolean,
  NzSizeLDSType,
  NzSizeMap,
  NzUpdateHostClassService,
  NzWaveConfig,
  NzWaveDirective,
  NZ_WAVE_GLOBAL_CONFIG
} from 'ng-zorro-antd/core';
import { NzIconDirective } from 'ng-zorro-antd/icon';

export type CmacsButtonType = 'primary' | 'dashed' | 'danger' | 'default';
export type CmacsButtonShape = 'circle' | 'round' | null;

@Component({
// tslint:disable-next-line: component-selector
  selector: '[cmacs-button]',
  exportAs: 'cmacsButton',
  providers: [NzUpdateHostClassService],
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './cmacs-button.component.html',
  styleUrls: ['./cmacs-button.component.css'],
})
export class CmacsButtonComponent implements AfterContentInit, AfterViewInit, OnInit, OnDestroy, OnChanges {
  readonly el: HTMLElement = this.elementRef.nativeElement;
  private iconElement: HTMLElement;
  private iconOnly = false;
  @ViewChild('contentElement') contentElement: ElementRef;
  @ContentChildren(NzIconDirective, { read: ElementRef }) listOfIconElement: QueryList<ElementRef>;
  @HostBinding('attr.nz-wave') nzWave = new NzWaveDirective(
    this.zone,
    this.elementRef,
    this.waveConfig,
    this.animationType
  );
  // tslint:disable-next-line: no-input-rename
  @Input('block') @InputBoolean() nzBlock = false;
  // tslint:disable-next-line: no-input-rename
  @Input('ghost') @InputBoolean() nzGhost = false;
  // tslint:disable-next-line: no-input-rename
  @Input('search') @InputBoolean() nzSearch = false;
  @Input() @InputBoolean() loading = false;
  @Input() @InputBoolean() action = false;
  @Input() type: CmacsButtonType = 'default';
  @Input() shape: CmacsButtonShape = null;
  @Input() size: NzSizeLDSType = 'default';

  setClassMap(): void {
    const prefixCls = 'ant-btn';
    const prefixCmacs = 'cmacs-btn';
    const sizeMap: NzSizeMap = { large: 'lg', small: 'sm' };
    this.updateHostClassService.updateHostClass(this.el, {
      [`${prefixCls}-${this.type}`]: this.type,
      [`${prefixCls}-${this.shape}`]: this.shape,
      [`${prefixCls}-${sizeMap[this.size]}`]: sizeMap[this.size],
      [`${prefixCls}-loading`]: this.loading,
      [`${prefixCls}-icon-only`]: this.iconOnly,
      [`${prefixCmacs}-action`]: this.action,
      [`${prefixCmacs}-with-icon`]: this.listOfIconElement !== undefined && this.listOfIconElement.length > 0 && this.contentElement.nativeElement.style.display !== 'none',
      [`${prefixCls}-background-ghost`]: this.nzGhost,
      [`${prefixCls}-block`]: this.nzBlock,
      [`ant-input-search-button`]: this.nzSearch
    });
  }

  updateIconDisplay(value: boolean): void {
    if (this.iconElement) {
      this.renderer.setStyle(this.iconElement, 'display', value ? 'none' : 'inline-block');
    }
  }

  checkContent(): void {
    const hasIcon = this.listOfIconElement && this.listOfIconElement.length;
    if (hasIcon) {
      this.moveIcon();
    }
    this.renderer.removeStyle(this.contentElement.nativeElement, 'display');

    if (isEmpty(this.contentElement.nativeElement)) {
      this.renderer.setStyle(this.contentElement.nativeElement, 'display', 'none');
    } else {
      this.renderer.removeStyle(this.contentElement.nativeElement, 'display');

      if (this.contentElement.nativeElement.children.length === 1 &&
        this.contentElement.nativeElement.children[0].tagName === "I" &&
        this.contentElement.nativeElement.innerText === '') {
        this.iconOnly = true;
      }
    }
    this.setClassMap();
    this.updateIconDisplay(this.loading);
    this.cdr.detectChanges();
  }

  moveIcon(): void {
    if (this.listOfIconElement && this.listOfIconElement.length) {
      const firstChildElement = findFirstNotEmptyNode(this.contentElement.nativeElement);
      const lastChildElement = findLastNotEmptyNode(this.contentElement.nativeElement);
      if (firstChildElement && firstChildElement === this.listOfIconElement.first.nativeElement) {
        this.renderer.insertBefore(this.el, firstChildElement, this.contentElement.nativeElement);
        this.iconElement = firstChildElement as HTMLElement;
      } else if (lastChildElement && lastChildElement === this.listOfIconElement.last.nativeElement) {
        this.renderer.appendChild(this.el, lastChildElement);
      }
    }
  }

  constructor(
    private elementRef: ElementRef,
    private cdr: ChangeDetectorRef,
    private renderer: Renderer2,
    private updateHostClassService: NzUpdateHostClassService,
    private zone: NgZone,
    @Optional() @Inject(NZ_WAVE_GLOBAL_CONFIG) private waveConfig: NzWaveConfig,
    @Optional() @Inject(ANIMATION_MODULE_TYPE) private animationType: string
  ) {
    this.renderer.addClass(elementRef.nativeElement, 'ant-btn');
  }

  ngAfterContentInit(): void {
    this.checkContent();
  }

  ngAfterViewInit() {
    this.imageWrapper();
    this.cdr.detectChanges();
  }

  ngOnInit(): void {
    this.setClassMap();
    this.nzWave.ngOnInit();
  }

  imageWrapper(): void {
    const el = this.contentElement.nativeElement;
    let iconList = el.querySelectorAll('i');

    iconList.forEach(icon => {
      const wrapper = document.createElement('span');
      wrapper.classList.add('iconspan');
      icon.parentNode.insertBefore(wrapper, icon);
      wrapper.appendChild(icon);
    });
  }

  ngOnDestroy(): void {
    this.nzWave.ngOnDestroy();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes.nzBlock ||
      changes.nzGhost ||
      changes.nzSearch ||
      changes.nzType ||
      changes.nzShape ||
      changes.nzSize ||
      changes.nzLoading
    ) {
      this.setClassMap();
    }
    if (changes.nzLoading) {
      this.updateIconDisplay(this.loading);
    }
  }

  hideBtn(): void {
    this.elementRef.nativeElement.style.display = 'none';
  }

  showBtn(): void {
    this.elementRef.nativeElement.style.display = 'inline-block';
  }

  disabledBtn(): void {
    this.elementRef.nativeElement.disabled = true;
  }
}
