import { DatePipe } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  AfterViewInit,
  TemplateRef,
  ViewChild,
  HostListener
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil, count } from 'rxjs/operators';

import { InputBoolean, InputNumber, NzSizeMDSType } from 'ng-zorro-antd/core';
import { NzI18nService } from 'ng-zorro-antd/i18n';

import { ExportAsService, ExportAsConfig } from 'ngx-export-as';
import jsPDF from 'jspdf';
import 'jspdf-autotable';
import { GridConfig, Field } from '../core/interfaces/grid-config';
import { GridExpConfig } from '../core/interfaces/grid-exp-config';
import { TemplateType } from '../core/enums/templateType.enum';
import { CeldType } from '../core/enums/celdType.enum';
import { ExcelService } from '../core/services/excel.service';
import { ExportType } from '../core/enums/export-type.enum';
import {CookieService} from "ngx-cookie-service";
import {NzDropdownContextComponent, NzDropdownService} from "ng-zorro-antd";

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'cmacs-grid',
  exportAs: 'cmacsGrid',
  templateUrl: './cmacs-table.component.html',
  styleUrls: ['./cmacs-table.component.css']
})
// tslint:disable-next-line no-any
export class CmacsGridComponent<T = any> implements OnInit, OnChanges, OnDestroy, AfterViewInit {
  locale: any = {}; // tslint:disable-line:no-any
  headerBottomStyle = {};
  private destroy$ = new Subject<void>();
  // tslint:disable-next-line: no-input-rename
  @Input() size: NzSizeMDSType = 'default';
  @Input() showTotal: TemplateRef<{ $implicit: number; range: [number, number] }>;
  @Input() pageSizeOptions = [10, 20, 30, 40, 50];
  @Input() @InputBoolean() virtualScroll = false;
  @Input() @InputNumber() virtualItemSize = 0;
  @Input() loadingDelay = 0;
  @Input() loadingIndicator: TemplateRef<void>;
  @Input() total = 0;
  @Input() title: string | TemplateRef<void>;
  @Input() footer: string | TemplateRef<void>;
  @Input() noResult: string | TemplateRef<void>;
  @Input() widthConfig: string[] = [];
  @Input() pageIndex = 1;
  @Input() pageSize = 10;
  // tslint:disable-next-line: no-input-rename
  @Input() data = [];
  @Input() config: GridConfig;
  @Output() configChange: EventEmitter<GridConfig> = new EventEmitter<GridConfig>();
  @Input() fieldId: string;
  @Input() gridID: string;
  @Input() paginationPosition: 'top' | 'bottom' | 'both' = 'bottom';
  @Input() scroll: { x?: string | null; y?: string | null } = { x: null, y: null };
  @Input() @ViewChild('renderItemTemplate') nzItemRender: TemplateRef<{
    $implicit: 'page' | 'prev' | 'next';
    page: number;
  }>;
  @Input() @InputBoolean() frontPagination = true;
  @Input() @InputBoolean() templateMode = false;
  @Input() @InputBoolean() bordered = false;
  @Input() @InputBoolean() showPagination = true;
  @Input() @InputBoolean() loading = false;
  @Input() @InputBoolean() showSizeChanger = false;
  @Input() @InputBoolean() hideOnSinglePage = false;
  @Input() @InputBoolean() showQuickJumper = false;
  @Input() @InputBoolean() simple = false;
  @Input() @InputBoolean() checkboxSelect = false;
  @Input() @InputBoolean() inLineEdit = false;
  @Input() @InputBoolean() dataTable = false;
  @Input() @InputBoolean() showRate = false;
  @Input() exportEvent = new EventEmitter<GridExpConfig>();
  @Output() buttonClick = new EventEmitter<any>();
  @Output() rateChange = new EventEmitter<any>();
  @Output() selectionChange = new EventEmitter<T[]>();
  @Output() ondlclickRow = new EventEmitter<any>();
  @Output() onclickRow = new EventEmitter<any>();
  @Output() onedit = new EventEmitter<any>();
  @Input() rateCount = 5;
  @Input() @InputBoolean() multiSelect = false;
  @Output() sortChange = new EventEmitter<any>();

  @Input() contextmenu: string | TemplateRef<void>;
  public dropdown: NzDropdownContextComponent;

  selected = false;
  defaultSortOrder = null;
  checkboxCache: CheckboxSelect[] = [];
  isIndeterminate = false;
  allChecked = false;
  editId: string | null;
  property: string | null;
  rowOnEdition = -1;

  @ViewChild('fieldTypeInput', { read: ElementRef }) inputElement: ElementRef;
  @ViewChild('fieldTypeInputNumber', { read: ElementRef }) inputNumberElement: ElementRef;
  @ViewChild('fieldTypeDatePicker', { read: ElementRef }) datePickerElement: ElementRef;
  @ViewChild('fieldTypeSelect', { read: ElementRef }) selectElement: ElementRef;
  @ViewChild('fieldTypeBool', { read: ElementRef }) boolElement: ElementRef;

  /*Context Menu */
  contextMenu($event: MouseEvent, template: TemplateRef<void>): void {
    this.dropdown = this.nzDropdownService.create($event, template);
  }

  startEdit(id: string, property: string, event: MouseEvent): void {
    event.preventDefault();
    event.stopPropagation();
    this.editId = id;
    this.property = property;
  }

  sort($event: any, fieldProperty: string, ){
    this.sortChange.emit({sortName: fieldProperty, sortValue: $event});
  }

  @HostListener('window:click', ['$event'])
  handleClick(e: Event): void {
    const element = e.target as HTMLElement;
    if (this.editId && this.property) {
      if (
        (this.inputElement && this.inputElement.nativeElement !== e.target) ||
        (this.inputNumberElement && !this.childOf(element, this.inputNumberElement.nativeElement)) ||
        (this.datePickerElement && !this.childOf(element, this.datePickerElement.nativeElement)) ||
        (this.selectElement && !this.childOf(element, this.selectElement.nativeElement) ||
        (this.boolElement && this.boolElement.nativeElement !== e.target))
      ) {
        this.editId = null;
        this.property = null;
        if (this.rowOnEdition >= 0) {
          this.onedit.emit(this.data[this.rowOnEdition]);
          this.rowOnEdition = -1;
        }

      }
    }
  }

  childOf( node: any, ancestor: any ) {
    let child = node;
    while (child !== null) {
      if (child === ancestor) return true;
      if (child.classList && child.classList.length > 0 && child.className && typeof child.className === 'string'
       && child.className.indexOf('cdk-overlay-pane') >=0 ) return true;
      child = child.parentNode;
    }
    return false;
  }

  endEditMode($event: KeyboardEvent, index: number) {
    if ($event.key === ('Enter' || 'enter')) {
      this.editId = null;
      this.property = null;
      this.rowOnEdition = -1;
      this.onedit.emit(this.data[index]);
    } else {
      this.rowOnEdition = index;
    }
  }

  endEditModeNgModel(index: number) {
    this.editId = null;
    this.property = null;
    this.rowOnEdition = -1;
    this.onedit.emit(this.data[index]);
  }

  getIndex(id): number {
    let i = -1;
    i = this.checkboxCache.findIndex(item => item.data[this.config.fieldId] === id);
    return i;
  }

  updateCheckboxCache(): void {
    this.checkboxCache.length = 0;
    this.data.forEach(item => {
      this.checkboxCache.push({
        selected: item.selected ? item.selected : false,
        data: { ...item }
      });
    });
  }

  onButtonClick(field: any) {
    this.buttonClick.emit(field);
  }

  onCheckboxChange(oninit = false) {
    const dataSelectd = this.checkboxCache.filter(item => item.selected);
    this.selected = this.allChecked = dataSelectd.length === this.checkboxCache.length;
    this.isIndeterminate = dataSelectd.length > 0 && !this.allChecked;

    if (!oninit) {
      this.selectionChange.emit(dataSelectd.map(item => item.data));
    }

  }

  ngModelChange(index: number) {
    this.rowOnEdition = index;
  }

  // tslint:disable-next-line: no-shadowed-variable
  onRateChange(count: number, data: any) {
    data[this.config.fieldRate] = count;
    this.rateChange.emit(data);
  }

  onRateClick(event: any) {
    event.preventDefault();
    event.stopPropagation();
  }

  getFields(): Field[] {
    return this.config.fields.filter(item => item.hidden === undefined || !item.hidden);
  }

  onCheckboxAllChange(status: boolean) {
    this.checkboxCache.forEach(element => {
      element.selected = status;
    });
    this.isIndeterminate = false;

    this.selectionChange.emit((status) ? this.data : []);
  }

  getLabel(data: any, field: Field): string {
    let result = '';
    if (field.editTemplate === TemplateType.Select && field.select !== undefined) {
      // tslint:disable-next-line: no-shadowed-variable
      const item = field.select.selectData.find(item => item !== undefined && item[field.select.value] === data[field.property]);
      result = (item !== undefined) ? item[field.select.label] : '';
    }

    return result;
  }

  isSelect(field: Field): boolean {
    return field !== undefined && field.editTemplate === TemplateType.Select;
  }

  isReadOnly(field: Field): boolean{
    return field !== undefined && field.readonly !== undefined && field.readonly;
  }

  isNumber(value: any): boolean {
    return value !== undefined && typeof (value) === 'number';
  }

  isString(value: any): boolean {
    return value !== undefined && typeof (value) === 'string';
  }

  isBoolean(value: any): boolean {
    return value !== undefined && typeof (value) === 'boolean';
  }

  isObject(value: any): boolean {
    return value !== undefined && typeof (value) === 'object';
  }

  isDate(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Default && field.editTemplate === TemplateType.Date;
  }

  isCeldTypeDefault(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Default;
  }

  isCeldTypeButton(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Button;
  }

  isCeldTypeTag(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Tag;
  }

  isCeldTypeTemplateRef(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.TemplateRef;
  }


  isUndefined(value: any) {
    return value === undefined;
  }

  isRowSelected(data: any): boolean {
    const dataSelectd = this.checkboxCache.filter(item => item.selected).map(item => item.data[this.config.fieldId]);
    return dataSelectd.indexOf(data[this.config.fieldId]) !== -1;
  }

  constructor(
    private cdr: ChangeDetectorRef,
    private i18n: NzI18nService,
    private exportAsService: ExportAsService,
    private nzDropdownService: NzDropdownService,
    private excelService: ExcelService,
    private datePipe: DatePipe,
    private cookies: CookieService
  ) {
    // renderer.addClass(elementRef.nativeElement, 'ant-table-wrapper');
  }

  ngAfterViewInit() {
    if (this.cookies.check(this.gridID)) {
      const savedConfigStr = this.cookies.get(this.gridID);
      // reset expiration date
      this.cookies.set(this.gridID, savedConfigStr, 365);

      // parse cookie value to typescript const
      const savedConfig = JSON.parse(this.cookies.get(this.gridID)) as GridConfig;
      if (typeof savedConfig === "object") {
        this.config = savedConfig;
        this.configChange.emit(this.config);
      }
    }
    this.cdr.detectChanges();
  }

  ngOnInit(): void {
    this.i18n.localeChange.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.locale = this.i18n.getLocaleData('Table');
      this.cdr.markForCheck();
    });

    if (this.checkboxSelect) {
      this.updateCheckboxCache();
      this.onCheckboxChange(true);
    }

    if (this.dataTable && this.data.length > 1) {
      while (this.data.length > 1) {
        this.data.pop();
      }
      this.showPagination = false;
    }

    this.exportEvent.subscribe((config: GridExpConfig) => {

      switch (config.exportType) {
        case ExportType.Pdf:
          this.exportToPdf(config.fileName);
          break;
        case ExportType.Xslx:
          this.exportToExcel(config.fileName);
          break;
        case ExportType.Png:
          this.exportToPng(config.fileName);
          break;
      }
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.data) {
      this.updateCheckboxCache();
    }
  }

  exportToPng(fileName: string): void {
    const exportAsConfig: ExportAsConfig = {
      type: 'png', // the type you want to download
      elementId: this.gridID, // the id of html/table element
    };

    this.frontPagination = false;
    this.showPagination = false;
    const interval = setInterval(() => {
      // tslint:disable-next-line: max-line-length
      this.exportAsService.save(exportAsConfig, fileName + '_export_' + Date.now());
      this.frontPagination = true;
      this.showPagination = true;
      clearInterval(interval);
    }, 100);


  }

  exportToExcel(fileName: string): void {
    const dataToExport: any[] = [];

    this.data.forEach(item => {
      const itemToExport = {};
      // tslint:disable-next-line: no-shadowed-variable
      this.config.fields.filter(item => item.celdType === CeldType.Default).forEach(field => {
        if (field.editTemplate === TemplateType.Select) {
          const selectItem = field.select.selectData.find(option => option[field.select.value] === item[field.property]);

          if (selectItem !== undefined) {
            itemToExport[field.display] = selectItem[field.select.label];
          }
        } else {
          itemToExport[field.display] = item[field.property];
        }
      });
      this.config.fields.filter(item => item.celdType === CeldType.TemplateRef).forEach(field => {
        itemToExport[field.display] = item[field.property].context.exportValue;
      });

      dataToExport.push(itemToExport);
    });

    this.excelService.exportAsExcelFile(dataToExport, fileName);
  }

  exportToPdf(fileName: string): void {
    const doc = new jsPDF();
    const columns = [];
    const rows = [];

    this.config.fields.filter(item => item.celdType === CeldType.Default).forEach(field => {
      columns.push(field.display);
    });

    this.config.fields.filter(item => item.celdType === CeldType.TemplateRef).forEach(field => {
      columns.push(field.display);
    });

    this.data.forEach(item => {
      const itemToExport = [];
      // tslint:disable-next-line: no-shadowed-variable
      this.config.fields.filter(item => item.celdType === CeldType.Default).forEach(field => {
        switch (field.editTemplate) {
          case TemplateType.Select:
            const selectItem = field.select.selectData.find(option => option[field.select.value] === item[field.property]);

            if (selectItem !== undefined) {
              itemToExport.push(selectItem[field.select.label]);
            }
            break;
          case TemplateType.Date:
            itemToExport.push(this.datePipe.transform(item[field.property], 'MMMM dd yyyy'));
            break;
          default:
              itemToExport.push(item[field.property]);
            break;
        }
      });

      this.config.fields.filter(item => item.celdType === CeldType.TemplateRef).forEach(field => {
        itemToExport.push(item[field.property].context.exportValue);
      });

      rows.push(itemToExport);

    });

    doc.autoTable({
      theme: 'striped',
      margin: { top: 5 },
      body: rows,
      columns
    });

    doc.save(fileName + '_export_' + Date.now());
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  clickRow(data: any) {
    this.onclickRow.emit(data);
    if (!this.checkboxSelect) {
      if (!this.multiSelect) {
        this.checkboxCache.filter(item => item.selected && item.data[this.config.fieldId] !== data[this.config.fieldId])
          .forEach(elem => elem.selected = false);
      }
      const index = this.checkboxCache.findIndex(item => item.data[this.config.fieldId] === data[this.config.fieldId]);
      if (index !== -1) {
        this.checkboxCache[index].selected = !this.checkboxCache[index].selected;
      }
    }
    this.selectionChange.emit(this.checkboxCache.filter(item => item.selected).map(item => item.data));
  }

  dblClickRow(data: any) {
    this.ondlclickRow.emit(data);
  }
}

export interface CheckboxSelect {
  data: any;
  selected: boolean;
}
