import {
  ComponentFactory,
  ComponentFactoryResolver,
  Directive,
  ElementRef,
  Host,
  Optional,
  Renderer2,
  ViewContainerRef
} from '@angular/core';

import { NzNoAnimationDirective } from 'ng-zorro-antd/core';

import {CmacsPopoverComponent} from "./cmacs-popover.component";
import {CmacsTooltipDirective} from "../cmacs-tooltip/cmacs-tooltip.directive";

@Directive({
  selector: '[cmacs-popover]',
  exportAs: 'cmacsPopover',
  host: {
    '[class.ant-popover-open]': 'isTooltipOpen'
  }
})
export class CmacsPopoverDirective extends CmacsTooltipDirective {
  factory: ComponentFactory<CmacsPopoverComponent> = this.resolver.resolveComponentFactory(CmacsPopoverComponent);

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    @Optional() tooltip: CmacsPopoverComponent,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    super(elementRef, hostView, resolver, renderer, tooltip, noAnimation);
  }
}
