import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  forwardRef
} from '@angular/core';
import { InputBoolean, NzSizeLDSType, isNotNil, NzSelectService } from 'ng-zorro-antd';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Option } from '../core/interfaces/options';

@Component({
// tslint:disable-next-line: component-selector
  selector: 'cmacs-search',
  templateUrl: './cmacs-search.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CmacsSearchComponent),
      multi: true
    }
  ],
  styleUrls: ['./cmacs-search.component.css']
})
export class CmacsSearchComponent implements ControlValueAccessor, OnInit {

  @Input() options: Option[];
  @Input() @InputBoolean() disabled = false;
  @Input()  maxTagCount: number;
  @Input() @InputBoolean() allowClear = false;
  @Input() @InputBoolean() showSearch = false;
  @Input() @InputBoolean() cmacsOpen = false;
  @Input() @InputBoolean() tagsOut = false;
  @Input() size: NzSizeLDSType = 'default';
  @Input() placeholder = 'placeholder';
  @Input() selected: any = [];
  @Output() selectedChange: EventEmitter<any> = new EventEmitter<any>();
  @Input() mode: 'default' | 'multiple' | 'tags' = 'default';

  onChange: (value: string | string[]) => void = () => null;
  onTouched: () => void = () => null;

  onSelectedChange() {
    this.selectedChange.emit(this.selected);
  }


  writeValue(value: any | any[]): void {
    this.selected = value;
    let listValue: any[] = []; // tslint:disable-line:no-any
    if (isNotNil(value)) {
      if (Array.isArray(value)) {
        listValue = value;
      } else {
        listValue = [value];
      }
    }
  }

  registerOnChange(fn: (value: string | string[]) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }


  constructor() { }

  trackByFn(index, item: Option) {
    return item.value;
  }

  ngOnInit() {
  }

}
