import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnInit,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { InputBoolean, NzSizeLDSType, NzUpdateHostClassService } from 'ng-zorro-antd/core';

import { CmacsListGrid } from './interface';

@Component({
  selector: 'cmacs-list',
  exportAs: 'cmacsList',
  templateUrl: './cmacs-list.component.html',
  providers: [NzUpdateHostClassService],
  preserveWhitespaces: false,
  styleUrls: ['./cmacs-list.component.css'],
  encapsulation: ViewEncapsulation.None,
  // changeDetection: ChangeDetectionStrategy.OnPush,
  styles: [
    `
      cmacs-list,
      cmacs-list nz-spin {
        display: block;
      }
    `
  ]
})
export class CmacsListComponent implements OnInit, OnChanges {
  // #region fields
  // tslint:disable-next-line:no-any
  @Input() dataSource: any[];

  @Input() @InputBoolean() bordered = false;

  @Input() grid: CmacsListGrid;

  @Input() header: string | TemplateRef<void>;

  @Input() footer: string | TemplateRef<void>;

  @Input() itemLayout: 'vertical' | 'horizontal' = 'horizontal';

  @Input() renderItem: TemplateRef<void>;

  @Input() @InputBoolean() loading = false;

  @Input() @InputBoolean() classicGrid = false;

  @Input() loadMore: TemplateRef<void>;

  @Input() pagination: TemplateRef<void>;

  @Input() size: NzSizeLDSType = 'default';

  @Input() @InputBoolean() split = true;

  @Input() noResult: string | TemplateRef<void>;

  // #endregion

  // #region styles

  private prefixCls = 'ant-list';

  private _setClassMap(): void {
    const classMap = {
      [this.prefixCls]: true,
      [`${this.prefixCls}-vertical`]: this.itemLayout === 'vertical',
      [`${this.prefixCls}-lg`]: this.size === 'large',
      [`${this.prefixCls}-sm`]: this.size === 'small',
      [`${this.prefixCls}-split`]: this.split,
      [`${this.prefixCls}-bordered`]: this.bordered,
      [`${this.prefixCls}-loading`]: this.loading,
      [`${this.prefixCls}-grid`]: this.grid,
      [`${this.prefixCls}-something-after-last-item`]: !!(this.loadMore || this.pagination || this.footer)
    };
    this.updateHostClassService.updateHostClass(this.el.nativeElement, classMap);
  }

  // #endregion

  constructor(private el: ElementRef, private updateHostClassService: NzUpdateHostClassService) {}

  ngOnInit(): void {
    this._setClassMap();
  }

  ngOnChanges(): void {
    this._setClassMap();
  }
}
