import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Host,
  Input, OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Renderer2, SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {zoomMotion, NzNoAnimationDirective, InputBoolean} from 'ng-zorro-antd/core';
import { CmacsSelectService } from './cmacs-select.service';
import { CmacsOptionComponent } from './cmacs-option.component';


@Component({
  selector: '[cmacs-select-top-control]',
  exportAs: 'cmacsSelectTopControl',
  preserveWhitespaces: false,
  animations: [zoomMotion],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './cmacs-select-top-control.component.html',
  styleUrls: ['./cmacs-select-top-control.component.css']
})
export class CmacsSelectTopControlComponent implements OnInit, OnDestroy, OnChanges {
  inputValue: string;
  @Input() searchValue: string = '';
  isComposing = false;
  private destroy$ = new Subject();
  @ViewChild('inputElement') inputElement: ElementRef;
  @Input() nzShowSearch = false;
  @Input() showCustomSearch = false;
  // tslint:disable-next-line: member-ordering
  @Input() showCmacsSearch = false;
  @Input() nzPlaceHolder: string;
  @Input() tagsOut = false;
  @Input() nzOpen = false;
  @Input() action = false;
  @Input() nzMaxTagCount: number;
  @Input() nzAllowClear = false;
  @Input() nzShowArrow = true;
  @Input() nzLoading = false;
  @Input() nzSuffixIcon: TemplateRef<void>;
  @Input() nzClearIcon: TemplateRef<void>;
  @Input() nzRemoveIcon: TemplateRef<void>;
  // tslint:disable-next-line:no-any
  @Input() nzMaxTagPlaceholder: TemplateRef<{ $implicit: any[] }>;
  @Input() nzTokenSeparators: string[] = [];

  onClearSelection(e: MouseEvent): void {
    e.stopPropagation();
    this.nzSelectService.updateListOfSelectedValue([], true);
  }

  getSelectedValues(){
    return this.nzSelectService.listOfSelectedValue;
  }

  setInputValue(value: string): void {
    if (this.inputElement) {
      this.inputElement.nativeElement.value = value;
    }
    this.inputValue = value;
    this.updateWidth();
    this.nzSelectService.updateSearchValue(value);
    this.nzSelectService.tokenSeparate(this.inputValue, this.nzTokenSeparators);
  }

  get placeHolderDisplay(): string {
    return this.inputValue || this.isComposing || this.nzSelectService.listOfSelectedValue.length ? 'none' : 'block';
  }

  get selectedValueStyle(): { [key: string]: string } {
    let showSelectedValue = false;
    let opacity = 1;
    if (!this.nzShowSearch) {
      showSelectedValue = true;
    } else {
      if (this.nzOpen) {
        showSelectedValue = !(this.inputValue || this.isComposing );
        if (showSelectedValue) {
          opacity = 0.4;
        }
      } else {
        showSelectedValue = true;
      }
    }

    if (this.nzSelectService.isTagsSingleSelectMode && this.inputValue) {
      showSelectedValue = false;
    }

    if (this.nzSelectService.isTagsSingleSelectMode && !this.inputValue) {
      opacity = 0.4;
    }

    return {
      display: showSelectedValue ? 'block' : 'none',
      'margin-left.px': this.showCustomSearch ? '15' : '0',
      opacity: `${opacity}`
    };
  }

  get showCustomSearchStyle(): { [key: string]: string } {
    return {
      'margin-left.px': this.showCustomSearch ? '15' : '0',
    };
  }

  // tslint:disable-next-line:no-any
  trackValue(_index: number, option: CmacsOptionComponent): any {
    return option.nzValue;
  }

  updateWidth(): void {
    if (this.nzSelectService.isMultipleOrTags && this.inputElement) {
      if (this.inputValue || this.isComposing) {
        this.renderer.setStyle(
          this.inputElement.nativeElement,
          'width',
          `${this.inputElement.nativeElement.scrollWidth}px`
        );
      } else {
        this.renderer.removeStyle(this.inputElement.nativeElement, 'width');
      }
    }
  }

  removeSelectedValue(option: CmacsOptionComponent, e: MouseEvent): void {
    this.nzSelectService.removeValueFormSelected(option);
    e.stopPropagation();
  }

  constructor(
    private renderer: Renderer2,
    public nzSelectService: CmacsSelectService,
    private cdr: ChangeDetectorRef,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {}

  ngOnInit(): void {
    this.nzSelectService.open$.pipe(takeUntil(this.destroy$)).subscribe(open => {
      if (this.inputElement && open) {
        setTimeout(() => this.inputElement.nativeElement.focus());
      }
    });
    this.nzSelectService.clearInput$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.setInputValue('');
    });
    this.nzSelectService.check$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.cdr.markForCheck();
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  ngOnChanges(changes: SimpleChanges) {
     if (changes.searchValue && changes.searchValue.currentValue !== undefined){
       this.setInputValue(changes.searchValue.currentValue);
     }
  }
}
