import { AnimationEvent } from '@angular/animations';
import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  Renderer2,
  ViewEncapsulation
} from '@angular/core';

import { fadeMotion, InputBoolean, NzUpdateHostClassService } from 'ng-zorro-antd/core';
import {CmacsGridType, CmacsPriorityType} from "../core/interfaces/grid-config";

@Component({
  selector: 'cmacs-tag',
  exportAs: 'cmacsTag',
  preserveWhitespaces: false,
  providers: [NzUpdateHostClassService],
  animations: [fadeMotion],
  templateUrl: './cmacs-tag.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  host: {
    '[@fadeMotion]': '',
    '(@fadeMotion.done)': 'afterAnimation($event)',
    '(click)': 'updateCheckedStatus()',
    '[style.background-color]': 'presetColor ? null : color',
    '[style.border-left]': "cmacsTemplateType ? '3px solid ' + color : null"
  },
  styleUrls: ['./cmacs-tag.component.css'],
})
export class CmacsTagComponent implements OnInit, OnChanges {
  presetColor = false;
  @Input() mode: 'default' | 'closeable' | 'checkable' = 'default';
  @Input() cmacsGridType: CmacsGridType
  @Input() cmacsPriorityType: CmacsPriorityType;
  @Input() cmacsStatusType = false;
  @Input() cmacsMoneyType = false;
  @Input() cmacsTemplateType = false;
  @Input() disabled = false;
  @Input() color: string;
  @Input() @InputBoolean() checked: boolean = false;
  @Input() @InputBoolean() noAnimation: boolean = false;
  @Output() readonly afterClose = new EventEmitter<void>();
  @Output() readonly onClose = new EventEmitter<MouseEvent>();
  @Output() readonly checkedChange = new EventEmitter<boolean>();

  private isPresetColor(color?: string): boolean {
    if (!color) {
      return false;
    }
    return /^(pink|red|yellow|orange|cyan|green|blue|purple|geekblue|magenta|volcano|gold|lime)(-inverse)?$/.test(
      color
    );
  }

  private updateClassMap(): void {
    this.presetColor = this.isPresetColor(this.color);
    const prefix = 'ant-tag';
    const cmacsPrefix = 'cmacs-tag';
    this.nzUpdateHostClassService.updateHostClass(this.elementRef.nativeElement, {
      [`${prefix}`]: true,
      [`${prefix}-has-color`]: this.color && !this.presetColor,
      [`${prefix}-${this.color}`]: this.presetColor,
      [`${prefix}-checkable`]: this.mode === 'checkable',
      [`${prefix}-checkable-checked`]: this.checked,
      [`${cmacsPrefix}-closeable`]: this.mode === 'closeable',
      [`${cmacsPrefix}`]: this.cmacsStatusType || this.cmacsPriorityType || this.cmacsGridType || this.cmacsMoneyType,
      [`${cmacsPrefix}-${this.cmacsGridType}`]: this.cmacsGridType,
      [`${cmacsPrefix}-${this.cmacsPriorityType}-priority`]: this.cmacsPriorityType,
      [`${cmacsPrefix}-money`]: this.cmacsMoneyType,
      [`${cmacsPrefix}-template`]: this.cmacsTemplateType,
      [`${cmacsPrefix}-status`]: this.cmacsStatusType,
    });
  }

  updateCheckedStatus(): void {
    if (this.mode === 'checkable') {
      this.checked = !this.checked;
      this.checkedChange.emit(this.checked);
      this.updateClassMap();
    }
  }

  closeTag(e: MouseEvent): void {
    this.onClose.emit(e);
    if (!e.defaultPrevented) {
      this.renderer.removeChild(this.renderer.parentNode(this.elementRef.nativeElement), this.elementRef.nativeElement);
    }
  }

  afterAnimation(e: AnimationEvent): void {
    if (e.toState === 'void') {
      this.afterClose.emit();
    }
  }

  constructor(
    private renderer: Renderer2,
    private elementRef: ElementRef,
    private nzUpdateHostClassService: NzUpdateHostClassService
  ) {}

  ngOnInit(): void {
    this.updateClassMap();
  }

  ngOnChanges(): void {
    this.updateClassMap();
  }
}
