import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Input,
  OnDestroy,
  OnInit,
  ViewEncapsulation
} from '@angular/core';

import { moveUpMotion } from 'ng-zorro-antd/core';

import { CmacsMessageContainerComponent } from './cmacs-message-container.component';
import { CmacsMessageDataFilled, CmacsMessageDataOptions } from './cmacs-message.definitions';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'cmacs-message',
  exportAs: 'cmacsMessage',
  preserveWhitespaces: false,
  animations: [moveUpMotion],
  templateUrl: './cmacs-message.component.html',
  styleUrls: ['./cmacs-message.component.css']
})
export class CmacsMessageComponent implements OnInit, OnDestroy {
  @Input() message: CmacsMessageDataFilled;
  @Input() index: number;

  protected _options: Required<CmacsMessageDataOptions>;

  private _autoErase: boolean; // Whether to set a timeout to destroy itself.
  private _eraseTimer: number | null = null;
  private _eraseTimingStart: number;
  private _eraseTTL: number; // Time to live.

  constructor(private _messageContainer: CmacsMessageContainerComponent, protected cdr: ChangeDetectorRef) {}

  ngOnInit(): void {
    // `CmacsMessageContainer` does its job so all properties cannot be undefined.
    this._options = this.message.options as Required<CmacsMessageDataOptions>;

    if (this._options.animate) {
      this.message.state = 'enter';
    }

    this._autoErase = this._options.duration > 0;

    if (this._autoErase) {
      this._initErase();
      this._startEraseTimeout();
    }
  }

  ngOnDestroy(): void {
    if (this._autoErase) {
      this._clearEraseTimeout();
    }
  }

  onEnter(): void {
    if (this._autoErase && this._options.pauseOnHover) {
      this._clearEraseTimeout();
      this._updateTTL();
    }
  }

  onLeave(): void {
    if (this._autoErase && this._options.pauseOnHover) {
      this._startEraseTimeout();
    }
  }

  // Remove self
  protected _destroy(userAction: boolean = false): void {
    if (this._options.animate) {
      this.message.state = 'leave';
      this.cdr.detectChanges();
      setTimeout(() => this._messageContainer.removeMessage(this.message.messageId, userAction), 200);
    } else {
      this._messageContainer.removeMessage(this.message.messageId, userAction);
    }
  }

  closeMessage() {
    this._destroy();
  }

  private _initErase(): void {
    this._eraseTTL = this._options.duration;
    this._eraseTimingStart = Date.now();
  }

  private _updateTTL(): void {
    if (this._autoErase) {
      this._eraseTTL -= Date.now() - this._eraseTimingStart;
    }
  }

  private _startEraseTimeout(): void {
    if (this._eraseTTL > 0) {
      this._clearEraseTimeout();
      this._eraseTimer = setTimeout(() => this._destroy(), this._eraseTTL);
      this._eraseTimingStart = Date.now();
    } else {
      this._destroy();
    }
  }

  private _clearEraseTimeout(): void {
    if (this._eraseTimer !== null) {
      clearTimeout(this._eraseTimer);
      this._eraseTimer = null;
    }
  }
}
