import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  SimpleChanges,
  OnChanges
} from '@angular/core';

@Component({
  selector: 'cmacs-side-panel',
  templateUrl: './cmacs-side-panel.component.html',
  styleUrls: ['./cmacs-side-panel.component.css']
})
export class CmacsSidePanelComponent implements OnInit, OnChanges {

  @Input() width: number | string = 370;
  @Input() float: string = 'right';
  @Input() visible: boolean = true;
  @Input() collapsible: boolean = false;
  @Output() visibleChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor() { }

  ngOnInit() { }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.visible !== undefined) {
      this.visibleChange.emit(this.visible);
    }
  }

}
