import { Directive, ElementRef, Input, Optional, Renderer2, Self } from '@angular/core';
import { NgControl } from '@angular/forms';
import { toBoolean, NzSizeLDSType } from 'ng-zorro-antd/core';
import {InputBoolean} from "ng-zorro-antd";

@Directive({
// tslint:disable-next-line: directive-selector
  selector: '[cmacs-input]',
  exportAs: 'cmacsInput',
// tslint:disable-next-line: use-host-property-decorator
  host: {
    '[class.ant-input-disabled]': 'disabled',
    '[class.cmacs-input-opened]': 'opened',
    '[class.ant-input-lg]': `size === 'large'`,
    '[class.ant-input-sm]': `size === 'small'`
  }
})
export class CmacsInputDirective {
// tslint:disable-next-line: variable-name
  private _disabled = false;
  @Input() size: NzSizeLDSType = 'default';
  @Input() @InputBoolean() opened = false;

  @Input()
  set disabled(value: boolean) {
    this._disabled = toBoolean(value);
  }

  get disabled(): boolean {
    if (this.ngControl && this.ngControl.disabled !== null) {
      return this.ngControl.disabled;
    }
    return this._disabled;
  }

  constructor(@Optional() @Self() public ngControl: NgControl, renderer: Renderer2, elementRef: ElementRef) {
    renderer.addClass(elementRef.nativeElement, 'ant-input');
    renderer.addClass(elementRef.nativeElement, 'cmacs-input-fix');
  }
}
