import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    Input,
    OnChanges,
    OnInit,
    Renderer2,
    SimpleChanges,
    TemplateRef,
    ViewChild,
    ViewEncapsulation
} from '@angular/core';

import { CmacsTimelineMode } from './cmacs-timeline.components';

@Component({
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None,
    preserveWhitespaces: false,
    // tslint:disable-next-line:component-selector
    selector: 'cmacs-timeline-item, [cmacs-timeline-item]',
    exportAs: 'cmacsTimelineItem',
    styleUrls: ['./cmacs-timeline-item.component.css'],
    templateUrl: './cmacs-timeline-item.component.html'
})

export class CmacsTimelineItemComponent implements OnInit, OnChanges {
    @ViewChild('liTemplate') liTemplate: ElementRef;
    @Input() color = 'gray';
    @Input() dot: string | TemplateRef<void>;

    isLast = false;
    position: CmacsTimelineMode | undefined;

    constructor(private renderer: Renderer2, private cdr: ChangeDetectorRef) {}

    ngOnInit(): void {
        this.tryUpdateCustomColor();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.nzColor) {
            this.tryUpdateCustomColor();
        }
    }
    detectChanges(): void {
        this.cdr.detectChanges();
    }

    private tryUpdateCustomColor(): void {
        const defaultColors = ['blue', 'red', 'green'];
        const circle = this.liTemplate.nativeElement.querySelector('.ant-timeline-item-head');
        if (defaultColors.indexOf(this.color) === -1) {
          this.renderer.setStyle(circle, 'border-color', this.color);
        } else {
          this.renderer.removeStyle(circle, 'border-color');
        }
    }
}
