import { FocusMonitor } from '@angular/cdk/a11y';
import { CdkConnectedOverlay, CdkOverlayOrigin, ConnectedOverlayPositionChange } from '@angular/cdk/overlay';
import { Platform } from '@angular/cdk/platform';
import {
  forwardRef,
  AfterContentInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  Renderer2,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { merge, EMPTY, Subject } from 'rxjs';
import { flatMap, startWith, takeUntil } from 'rxjs/operators';

import {
  isNotNil,
  slideMotion,
  toBoolean,
  InputBoolean,
  NzNoAnimationDirective,
  NzSizeLDSType, zoomMotion,
} from 'ng-zorro-antd/core';

import {
  TFilterOption,
} from 'ng-zorro-antd';

import { CmacsSelectTopControlComponent } from './cmacs-select-top-control.component';
import { CmacsOptionComponent } from './cmacs-option.component';
import {CmacsOptionGroupComponent} from "./cmacs-option-group.component";
import {CmacsSelectService} from "./cmacs-select.service";

@Component({
// tslint:disable-next-line: component-selector
  selector: 'cmacs-select',
  exportAs: 'cmacsSelect',
  preserveWhitespaces: false,
  providers: [
    CmacsSelectService,
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CmacsSelectComponent),
      multi: true
    }
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [slideMotion, zoomMotion],
  templateUrl: './cmacs-select.component.html',
// tslint:disable-next-line: use-host-property-decorator
  host: {
    '[class.ant-select-lg]': 'size==="large"',
    '[class.ant-select-sm]': 'size==="small"',
    '[class.ant-select-enabled]': '!disabled',
    '[class.ant-select-no-arrow]': '!showArrow',
    '[class.ant-select-disabled]': 'disabled',
    '[class.ant-select-allow-clear]': 'allowClear',
    '[class.ant-select-open]': 'nzOpen',
    '(click)': 'toggleDropDown()'
  },
  styles: [
    `
      .ant-select-dropdown {
        top: 100%;
        left: 0;
        position: relative;
        width: 100%;
        margin-top: 4px;
        margin-bottom: 4px;
      }
    `
  ],
  styleUrls: ['./cmacs-select.component.css']
})
export class CmacsSelectComponent implements ControlValueAccessor, OnInit, AfterViewInit, OnDestroy, AfterContentInit {
  nzOpen = false;
  // tslint:disable-next-line:no-any
  value: any | any[];
  searchValue: string = '';
  onChange: (value: string | string[]) => void = () => null;
  onTouched: () => void = () => null;
// tslint:disable-next-line: member-ordering
  dropDownPosition: 'top' | 'center' | 'bottom' = 'bottom';
// tslint:disable-next-line: member-ordering
  triggerWidth: number;
// tslint:disable-next-line: member-ordering
// tslint:disable-next-line: variable-name
// tslint:disable-next-line: member-ordering
  private _disabled = false;
// tslint:disable-next-line: member-ordering
  private _autoFocus = false;
// tslint:disable-next-line: member-ordering
  private isInit = false;
// tslint:disable-next-line: member-ordering
  private destroy$ = new Subject();
// tslint:disable-next-line: member-ordering
  @ViewChild(CdkOverlayOrigin) cdkOverlayOrigin: CdkOverlayOrigin;
// tslint:disable-next-line: member-ordering
  @ViewChild(CdkConnectedOverlay) cdkConnectedOverlay: CdkConnectedOverlay;
// tslint:disable-next-line: member-ordering
  @ViewChild(CmacsSelectTopControlComponent) selectTopControlComponent: CmacsSelectTopControlComponent;
// tslint:disable-next-line: member-ordering
  @ContentChildren(CmacsOptionComponent) listOfCmacsOptionComponent: QueryList<CmacsOptionComponent>;
// tslint:disable-next-line: member-ordering
  @ContentChildren(CmacsOptionGroupComponent) listOfCmacsOptionGroupComponent: QueryList<CmacsOptionGroupComponent>;
// tslint:disable-next-line: member-ordering
  @Output() readonly cmacsOnSearch = new EventEmitter<string>();
// tslint:disable-next-line: member-ordering
  @Output() readonly scrollToBottom = new EventEmitter<void>();
// tslint:disable-next-line: member-ordering
  @Output() readonly openChange = new EventEmitter<boolean>();
// tslint:disable-next-line: member-ordering
  @Output() readonly cmacsBlur = new EventEmitter<void>();
// tslint:disable-next-line: member-ordering
  @Output() readonly cmacsFocus = new EventEmitter<void>();
// tslint:disable-next-line: member-ordering
  @Input() size: NzSizeLDSType = 'default';
// tslint:disable-next-line: member-ordering
  @Input() dropdownClassName: string = 'test-class';
// tslint:disable-next-line: member-ordering
  @Input() dropdownMatchSelectWidth = true;
  // tslint:disable-next-line: member-ordering
  @Input() action = false;
// tslint:disable-next-line: member-ordering
  @Input() dropdownStyle: { [key: string]: string };
// tslint:disable-next-line: member-ordering
  @Input() notFoundContent: string;
// tslint:disable-next-line: member-ordering
  @Input() @InputBoolean() allowClear = false;
  @Input() @InputBoolean() open = false;
// tslint:disable-next-line: member-ordering
  @Input() @InputBoolean() showSearch = false;
  @Input() @InputBoolean() tagsOut = false;
  // tslint:disable-next-line: member-ordering
  @Input() @InputBoolean() showCmacsSearch = false;
// tslint:disable-next-line: member-ordering
  @Input() @InputBoolean() showCustomSearch = false;
// tslint:disable-next-line: member-ordering
  @Input() @InputBoolean() loading = false;
// tslint:disable-next-line: member-ordering
  @Input() placeHolder: string;
// tslint:disable-next-line: member-ordering
  @Input() maxTagCount: number;
// tslint:disable-next-line: member-ordering
  @Input() dropdownRender: TemplateRef<void>;
// tslint:disable-next-line: member-ordering
  @Input() suffixIcon: TemplateRef<void>;
// tslint:disable-next-line: member-ordering
  @Input() clearIcon: TemplateRef<void>;
// tslint:disable-next-line: member-ordering
  @Input() removeIcon: TemplateRef<void>;
// tslint:disable-next-line: member-ordering
  @Input() menuItemSelectedIcon: TemplateRef<void>;
// tslint:disable-next-line: member-ordering
  @Input() showArrow = true;
// tslint:disable-next-line: member-ordering
  @Input() tokenSeparators: string[] = [];
  // tslint:disable-next-line:no-any
// tslint:disable-next-line: member-ordering
  @Input() maxTagPlaceholder: TemplateRef<{ $implicit: any[] }>;

  @Input()
  set autoClearSearchValue(value: boolean) {
    this.nzSelectService.autoClearSearchValue = toBoolean(value);
  }

  @Input()
  set maxMultipleCount(value: number) {
    this.nzSelectService.maxMultipleCount = value;
  }

  @Input()
  set serverSearch(value: boolean) {
    this.nzSelectService.serverSearch = toBoolean(value);
  }

  @Input()
  set mode(value: 'default' | 'multiple' | 'tags' | 'tagsSingleSelect') {
    this.nzSelectService.mode = value;
    this.nzSelectService.check();
  }

  @Input()
  set filterOption(value: TFilterOption) {
    this.nzSelectService.filterOption = value;
  }

  @Input()
  // tslint:disable-next-line:no-any
  set compareWith(value: (o1: any, o2: any) => boolean) {
    this.nzSelectService.compareWith = value;
  }

  @Input()
  set autoFocus(value: boolean) {
    this._autoFocus = toBoolean(value);
    this.updateAutoFocus();
  }

  get autoFocus(): boolean {
    return this._autoFocus;
  }

  @Input()
  set cmacsOpen(value: boolean) {
    this.nzOpen = value;
    this.nzSelectService.setOpenState(value);
  }

  @Input()
  set disabled(value: boolean) {
    this._disabled = toBoolean(value);
    this.nzSelectService.disabled = this._disabled;
    this.nzSelectService.check();
    if (this.disabled && this.isInit) {
      this.closeDropDown();
    }
  }

  get disabled(): boolean {
    return this._disabled;
  }

  updateAutoFocus(): void {
    if (this.selectTopControlComponent.inputElement) {
      if (this.autoFocus) {
        this.renderer.setAttribute(
          this.selectTopControlComponent.inputElement.nativeElement,
          'autofocus',
          'autofocus'
        );
      } else {
        this.renderer.removeAttribute(this.selectTopControlComponent.inputElement.nativeElement, 'autofocus');
      }
    }
  }

  focus(): void {
    if (this.selectTopControlComponent.inputElement) {
      this.focusMonitor.focusVia(this.selectTopControlComponent.inputElement, 'keyboard');
      this.cmacsFocus.emit();
    }
  }

  // tslint:disable-next-line:no-any
  trackValue(_index: number, option: CmacsOptionComponent): any {
    return option.nzValue;
  }

  blur(): void {
    if (this.selectTopControlComponent.inputElement) {
      this.selectTopControlComponent.inputElement.nativeElement.blur();
      this.cmacsBlur.emit();
    }
  }

  removeSelectedValue(option: CmacsOptionComponent, e: MouseEvent): void {
    this.nzSelectService.removeValueFormSelected(option);
    e.stopPropagation();
  }

  onKeyDown(event: KeyboardEvent): void {
    this.nzSelectService.onKeyDown(event);
  }

  toggleDropDown(): void {
    if (!this.disabled) {
      this.nzSelectService.setOpenState(!this.nzOpen);
    }
  }

  closeDropDown(): void {
    this.nzSelectService.setOpenState(false);
  }

  onPositionChange(position: ConnectedOverlayPositionChange): void {
    this.dropDownPosition = position.connectionPair.originY;
  }

  updateCdkConnectedOverlayStatus(): void {
    if (this.platform.isBrowser) {
      this.triggerWidth = this.cdkOverlayOrigin.elementRef.nativeElement.getBoundingClientRect().width;
    }
  }

  updateCdkConnectedOverlayPositions(): void {
    setTimeout(() => {
      if (this.cdkConnectedOverlay && this.cdkConnectedOverlay.overlayRef) {
        this.cdkConnectedOverlay.overlayRef.updatePosition();
      }
    });
  }

  constructor(
    private renderer: Renderer2,
    public nzSelectService: CmacsSelectService,
    private cdr: ChangeDetectorRef,
    private focusMonitor: FocusMonitor,
    private platform: Platform,
    elementRef: ElementRef,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    renderer.addClass(elementRef.nativeElement, 'ant-select');
  }

// tslint:disable-next-line: jsdoc-format
  /** update ngModel -> update listOfSelectedValue **/
  // tslint:disable-next-line:no-any
  writeValue(value: any | any[]): void {
    this.value = value;
    let listValue: any[] = []; // tslint:disable-line:no-any
    if (isNotNil(value)) {
      if (Array.isArray(value)) {
        listValue = value;
      } else {
        listValue = [value];
      }
    }
    this.nzSelectService.updateListOfSelectedValue(listValue, false);
    this.cdr.markForCheck();
  }

  registerOnChange(fn: (value: string | string[]) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    this.cdr.markForCheck();
  }

  ngOnInit(): void {
    this.nzSelectService.searchValue$.pipe(takeUntil(this.destroy$)).subscribe(data => {
      this.cmacsOnSearch.emit(data);
      this.updateCdkConnectedOverlayPositions();
    });
    this.nzSelectService.modelChange$.pipe(takeUntil(this.destroy$)).subscribe(modelValue => {
      if (this.value !== modelValue) {
        this.value = modelValue;
        this.onChange(this.value);
        this.updateCdkConnectedOverlayPositions();
      }
    });
    this.nzSelectService.open$.pipe(takeUntil(this.destroy$)).subscribe(value => {
      if (this.nzOpen !== value) {
        this.openChange.emit(value);
      }
      if (value) {
        this.focus();
        this.updateCdkConnectedOverlayStatus();
      } else {
        this.blur();
        this.onTouched();
      }
      this.nzOpen = value;
    });
    this.nzSelectService.check$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.cdr.markForCheck();
    });
  }

  ngAfterViewInit(): void {
    this.updateCdkConnectedOverlayStatus();
    this.isInit = true;
  }

  ngAfterContentInit(): void {
    this.listOfCmacsOptionGroupComponent.changes
      .pipe(
        startWith(true),
        flatMap(() =>
          merge(
            this.listOfCmacsOptionGroupComponent.changes,
            this.listOfCmacsOptionComponent.changes,
            ...this.listOfCmacsOptionComponent.map(option => option.changes),
            ...this.listOfCmacsOptionGroupComponent.map(group =>
              group.listOfNzOptionComponent ? group.listOfNzOptionComponent.changes : EMPTY
            )
          ).pipe(startWith(true))
        )
      )
      .subscribe(() => {
        this.nzSelectService.updateTemplateOption(
          this.listOfCmacsOptionComponent.toArray(),
          this.listOfCmacsOptionGroupComponent.toArray()
        );
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  onSearch(value: string){
    this.searchValue = value;
  }
}
