import { Platform } from '@angular/cdk/platform';
import {
    AfterContentInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ContentChild,
    ContentChildren,
    ElementRef,
    Input,
    OnChanges,
    OnDestroy,
    QueryList,
    SimpleChanges,
    TemplateRef,
    ViewChild,
    ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { reverseChildNodes } from 'ng-zorro-antd/core';

import { CmacsTimelineItemComponent } from './cmacs-timeline-item.component';

export type CmacsTimelineMode = 'left' | 'alternate' | 'right';

@Component({
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None,
    preserveWhitespaces: false,
    // tslint:disable-next-line:component-selector
    selector: 'cmacs-timeline',
    exportAs: 'cmacsTimeline',
    styleUrls: ['./cmacs-timeline.component.css'],
    templateUrl: './cmacs-timeline.component.html'
})

export class CmacsTimelineComponent implements AfterContentInit, OnChanges, OnDestroy {
    @ViewChild('timeline') timeline: ElementRef<HTMLElement>;
    @ContentChildren(CmacsTimelineItemComponent) listOfTimeLine: QueryList<CmacsTimelineItemComponent>;
    @ContentChild('pending') pendingContent: TemplateRef<void>;

    @Input() mode: CmacsTimelineMode;
    @Input() pending: string | boolean | TemplateRef<void>;
    @Input() pendingDot: string | TemplateRef<void>;
    @Input() reverse = false;

    isPendingBoolean = false;

    private destroy$ = new Subject<void>();

    constructor(private cdr: ChangeDetectorRef, private platform: Platform) { }

    ngOnChanges(changes: SimpleChanges): void {
        const modeChanges = changes.mode;
        const reverseChanges = changes.reverse;
        const pendingChanges = changes.pending;

        if (modeChanges && (modeChanges.previousValue !== modeChanges.currentValue || modeChanges.isFirstChange())) {
            this.updateChildren();
        }
        if (
            reverseChanges &&
            reverseChanges.previousValue !== reverseChanges.currentValue &&
            !reverseChanges.isFirstChange()
        ) {
            this.reverseChildTimelineDots();
        }
        if (pendingChanges) {
            this.isPendingBoolean = pendingChanges.currentValue === true;
        }
    }

    ngAfterContentInit() {
        this.updateChildren();
        if (this.listOfTimeLine) {
            this.listOfTimeLine.changes.pipe(takeUntil(this.destroy$)).subscribe(() => {
                this.updateChildren();
            });
        }
    }

    ngOnDestroy(): void {
        this.destroy$.next();
        this.destroy$.complete();
    }

    private updateChildren(): void {
        if (this.listOfTimeLine && this.listOfTimeLine.length) {
            const length = this.listOfTimeLine.length;
            this.listOfTimeLine.toArray().forEach((item, index) => {
                item.isLast = !this.reverse ? index === length - 1 : index === 0;
                item.position =
                    this.mode === 'left' || !this.mode
                        ? undefined
                        : this.mode === 'right'
                            ? 'right'
                            : this.mode === 'alternate' && index % 2 === 0
                                ? 'left'
                                : 'right';
                item.detectChanges();
            });
            this.cdr.markForCheck();
        }
    }

    private reverseChildTimelineDots(): void {
        if (this.platform.isBrowser) {
            reverseChildNodes(this.timeline.nativeElement as HTMLElement);
            this.updateChildren();
          }
    }
}
