import {
  Component,
  Input,
  ViewChild,
  ElementRef, AfterViewInit
} from '@angular/core';
import {DomSanitizer} from "@angular/platform-browser";
import {InputBoolean} from "ng-zorro-antd";

export const KPI_COLORS = [
  '#2a7cff',
  '#00cda1',
  '#ffa234',
  '#a100cd',
  '#cc2229',
  '#009fe3',
  '#688cda',
  '#bec4cd'
];

export interface KPI {
  count: number;
  description: string;
}

@Component({
  selector: 'cmacs-kpi',
  exportAs: 'cmacsKpi',
  templateUrl: './cmacs-kpi.component.html',
  styleUrls: ['./cmacs-kpi.component.css'],
  styles: [
    `
      cmacs-kpi {
        display: block;
      }
    `
  ]
})
export class CmacsKpiComponent implements AfterViewInit {

  @Input() data: KPI[];
  @Input() title: string;
  @Input() type: 'line' | 'doughnut' = 'line';
  @Input() width: number = 84;
  @Input() @InputBoolean() showTotalCount = false;
  @ViewChild('canvas', { read: ElementRef }) canvasRef: ElementRef;

  constructor(private sanitizer: DomSanitizer) { }

  ngAfterViewInit() {
    if (this.canvasRef) {
      const canvas = this.canvasRef.nativeElement as HTMLCanvasElement;
      canvas.width = this.width;
      canvas.height = this.width;
      const ctx = canvas.getContext("2d");

      let start_angle = 0;
      let data = this.getColoredData();

      if (this.getTotalCount() > 0) {
        for (let categ of data){
          let val = categ.count;
          let slice_angle = 2 * Math.PI * val / this.getTotalCount();

          this.drawPieSlice(
            ctx,
            canvas.width/2,
            canvas.height/2,
            Math.min(canvas.width/2, canvas.height/2),
            start_angle,
            start_angle + slice_angle,
            categ.color
          );

          start_angle += slice_angle;
        }
      } else {
        this.drawPieSlice(
          ctx,
          canvas.width/2,
          canvas.height/2,
          Math.min(canvas.width/2, canvas.height/2),
          start_angle,
          start_angle + 2 * Math.PI,
          '#dee0e5'
        );
      }


      //drawing a white circle over the chart
      //to create the doughnut chart
      this.drawPieSlice(
        ctx,
        canvas.width/2,
        canvas.height/2,
        0.8 * Math.min(canvas.width/2, canvas.height/2),
        0,
        2 * Math.PI,
        "#ffffff"
      );
    }

  }

  drawPieSlice(ctx: any,centerX: number, centerY: number, radius: number, startAngle: number, endAngle: number, color: any ){
    ctx.fillStyle = color;
    ctx.beginPath();
    ctx.moveTo(centerX,centerY);
    ctx.arc(centerX, centerY, radius, startAngle, endAngle);
    ctx.closePath();
    ctx.fill();
  }

  drawLine(ctx: any, startX: number, startY: number, endX: number, endY: number){
    ctx.beginPath();
    ctx.moveTo(startX,startY);
    ctx.lineTo(endX,endY);
    ctx.stroke();
  }

  drawArc(ctx: any, centerX: number, centerY: number, radius: number, startAngle: number, endAngle: number){
    ctx.beginPath();
    ctx.arc(centerX, centerY, radius, startAngle, endAngle);
    ctx.stroke();
  }

  sanitizeStyle(style: string) {
    return this.sanitizer.bypassSecurityTrustStyle(style);
  }

  getTotalCount() {
    let total = 0;
    this.data.forEach((item) => {
      total += item.count;
    });
    return total;
  }

  getWidth(count: number) {
    return count !== 0 ? Math.trunc(count * 100 / this.getTotalCount()) - 2 : 0;
  }

  getColoredData() {
    let coloredData: any = [];
    const remaining = this.data.length % KPI_COLORS.length;
    let rate = this.data.length / KPI_COLORS.length;
    if (remaining > 0) {
      rate = Math.trunc(rate) + 1;
    }

    let tempRate = rate;
    let opacity = 1;
    let colorIndex = 0;
    this.data.forEach((item) => {
      if (tempRate === 0) {
        tempRate = rate;
        colorIndex += 1;
        opacity = 1;
      }

      if (colorIndex >= KPI_COLORS.length) {
        colorIndex = 0;
      }

      if (opacity === 0.4) {
        opacity = 1;
      }

      coloredData.push({
        count: item.count,
        description: item.description,
        color: KPI_COLORS[colorIndex],
        opacity: opacity
      });

      opacity = opacity - 0.2;
      tempRate--;
    });

    return coloredData;
  }

}
