import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Output,
  Renderer2,
  ViewEncapsulation
} from '@angular/core';

import { CmacsCheckboxComponent } from './cmacs-checkbox.component';

@Component({
  selector: 'cmacs-checkbox-wrapper',
  exportAs: 'cmacsCheckboxWrapper',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './cmacs-checkbox-wrapper.component.html'
})
export class CmacsCheckboxWrapperComponent {
  @Output() readonly nzOnChange = new EventEmitter<string[]>();
  private checkboxList: CmacsCheckboxComponent[] = [];

  addCheckbox(value: CmacsCheckboxComponent): void {
    this.checkboxList.push(value);
  }

  removeCheckbox(value: CmacsCheckboxComponent): void {
    this.checkboxList.splice(this.checkboxList.indexOf(value), 1);
  }

  outputValue(): string[] {
    const checkedList = this.checkboxList.filter(item => item.checked);
    return checkedList.map(item => item.value);
  }

  onChange(): void {
    this.nzOnChange.emit(this.outputValue());
  }

  constructor(renderer: Renderer2, elementRef: ElementRef) {
    renderer.addClass(elementRef.nativeElement, 'ant-checkbox-group');
  }
}
