import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  TemplateRef
} from '@angular/core';

import { toBoolean, valueFunctionProp, FunctionProp, InputBoolean, NzNoAnimationDirective } from 'ng-zorro-antd/core';
import { DateHelperService, NzI18nService } from 'ng-zorro-antd/i18n';

import { AbstractPickerComponent, CompatibleDate } from './abstract-picker.component';
import { CandyDate } from './lib/candy-date/candy-date';
import { DisabledTimeFn, PanelMode, PresetRanges } from './standard-types';

@Component({
  template: `` // Just for rollup
})
export class CmacsDateRangePickerComponent extends AbstractPickerComponent implements OnInit, OnChanges {
  showWeek = false; // Should show as week picker

  @Input() dateRender: FunctionProp<TemplateRef<Date> | string>;
  @Input() disabledTime: DisabledTimeFn;
  @Input() renderExtraFooter: FunctionProp<TemplateRef<void> | string>;
  @Input() @InputBoolean() showToday = false;
  @Input() mode: PanelMode | PanelMode[];
  @Input() ranges: PresetRanges;
  @Output() readonly cmacsOnPanelChange = new EventEmitter<PanelMode | PanelMode[]>();
  @Output() readonly cmacsOnCalendarChange = new EventEmitter<Date[]>();

  @Input() openPickerTitle = '';
  @Input() openPickerSubtitle = '';
  @Input() openPickerLeftRangeSub = '';
  @Input() openPickerRightRangeSub = '';

// tslint:disable-next-line: variable-name
  private _showTime: object | boolean;
  @Input() get showTime(): object | boolean {
    return this._showTime;
  }
  set showTime(value: object | boolean) {
    this._showTime = typeof value === 'object' ? value : toBoolean(value);
  }

  @Output() readonly cmacsOnOk = new EventEmitter<CompatibleDate | null>();

  get realShowToday(): boolean {
    return !this.isRange && this.showToday;
  }

  pickerStyle: object; // Final picker style that contains width fix corrections etc.
  extraFooter: TemplateRef<void> | string;

  constructor(
    i18n: NzI18nService,
    cdr: ChangeDetectorRef,
    dateHelper: DateHelperService,
    noAnimation?: NzNoAnimationDirective
  ) {
    super(i18n, cdr, dateHelper, noAnimation);
    if (this.isRange) {
      this.value = this.value as CandyDate[];
    } else {
      this.value = this.value as CandyDate;
    }
  }

  ngOnInit(): void {
    super.ngOnInit();

    // Default format when it's empty
    if (!this.format) {
      if (this.showWeek) {
        this.format = this.dateHelper.relyOnDatePipe ? 'yyyy-ww' : 'YYYY-WW'; // Format for week
      } else {
        if (this.dateHelper.relyOnDatePipe) {
          this.format = this.showTime ? 'yyyy-MM-dd HH:mm:ss' : 'yyyy-MM-dd';
        } else {
          this.format = this.showTime ? 'YYYY-MM-DD HH:mm:ss' : 'YYYY-MM-DD';
        }
      }
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    super.ngOnChanges(changes);

    if (changes.renderExtraFooter) {
      this.extraFooter = valueFunctionProp(this.renderExtraFooter);
    }

    if (changes.showTime || changes.cmacsStyle) {
      this.setFixedPickerStyle();
    }
  }

  // If has no timepicker and the user select a date by date panel, then close picker
  onValueChange(value: CandyDate): void {
    super.onValueChange(value);

    if (!this.showTime) {
      this.closeOverlay();
    }
  }

  // Emit nzOnCalendarChange when select date by nz-range-picker
  onCalendarChange(value: CandyDate[]): void {
    if (this.isRange) {
      const rangeValue = value.map(x => x.nativeDate);
      this.cmacsOnCalendarChange.emit(rangeValue);
    }
  }

  // Emitted when done with date selecting
  onResultOk(): void {
    if (this.isRange) {
      const value = this.value as CandyDate[];
      if (value.length) {
        this.cmacsOnOk.emit([value[0].nativeDate, value[1].nativeDate]);
      } else {
        this.cmacsOnOk.emit([]);
      }
    } else {
      if (this.value) {
        this.cmacsOnOk.emit((this.value as CandyDate).nativeDate);
      } else {
        this.cmacsOnOk.emit(null);
      }
    }
    this.closeOverlay();
  }

  onOpenChange(open: boolean): void {
    this.cmacsOnOpenChange.emit(open);
  }

  // Setup fixed style for picker
  private setFixedPickerStyle(): void {
    const showTimeFixes: { width?: string } = {};
    if (this.showTime) {
      showTimeFixes.width = this.isRange ? '350px' : '195px';
    }

    this.pickerStyle = { ...showTimeFixes, ...this.cmacsStyle };
  }

  parseDate() {
    if (!this.isRange) {
      const parsedValue = this.value as CandyDate;
      return `${this.getMonth(parsedValue.getMonth())} ${parsedValue.getDate()}, ${parsedValue.getYear()}`;
    }
    let parsedValues = [];
    const coerceValues = this.value as CandyDate[];

    for(let val of coerceValues) {
      const parsedValue = val as CandyDate;
      parsedValues.push(`${this.getMonth(parsedValue.getMonth())} ${parsedValue.getDate()}, ${parsedValue.getYear()}`);
    }
    return parsedValues;
  }

  getMonth(idx) {
    const months = [
      'Jan',
      'Feb',
      'Mar',
      'Apr',
      'May',
      'Jun',
      'Jul',
      'Aug',
      'Sep',
      'Oct',
      'Nov',
      'Dec'
    ];
    return months[idx];
  }
}
