import { CdkConnectedOverlay, CdkOverlayOrigin, ConnectedOverlayPositionChange } from '@angular/cdk/overlay';
import { Platform } from '@angular/cdk/platform';
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

import { combineLatest, merge, Subject } from 'rxjs';
import { flatMap, map, startWith, takeUntil } from 'rxjs/operators';

import {
  collapseMotion,
  getPlacementName,
  slideMotion,
  zoomBigMotion,
  DEFAULT_SUBMENU_POSITIONS,
  InputBoolean,
  NzMenuBaseService,
  NzNoAnimationDirective,
  NzUpdateHostClassService,
  POSITION_MAP
} from 'ng-zorro-antd/core';

import { CmacsMenuItemDirective } from './cmacs-menu-item.directive';
import { CmacsSubmenuService } from './cmacs-submenu.service';

@Component({
// tslint:disable-next-line: component-selector
  selector: '[cmacs-submenu]',
  exportAs: 'cmacsSubmenu',
  providers: [CmacsSubmenuService, NzUpdateHostClassService],
  animations: [collapseMotion, zoomBigMotion, slideMotion],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  preserveWhitespaces: false,
  templateUrl: './cmacs-submenu.component.html',
  styles: [
    `
      .ant-menu-submenu-placement-bottomLeft {
        top: 6px;
        position: relative;
      }

      .ant-menu-submenu-placement-rightTop {
        left: 4px;
        position: relative;
      }

      .ant-menu-submenu-placement-leftTop {
        right: 4px;
        position: relative;
      }
    `
  ]
})
export class CmacsSubMenuComponent implements OnInit, OnDestroy, AfterContentInit, OnChanges {
  @Input() menuClassName: string;
  @Input() paddingLeft: number;
  @Input() @InputBoolean() open = false;
  @Input() @InputBoolean() disabled = false;
  @Output() readonly openChange: EventEmitter<boolean> = new EventEmitter();

  @ViewChild(CdkConnectedOverlay) cdkConnectedOverlay: CdkConnectedOverlay;
  @ViewChild(CdkOverlayOrigin, { read: ElementRef }) cdkOverlayOrigin: ElementRef;
  @ContentChildren(CmacsSubMenuComponent, { descendants: true })
  listOfCmacsSubMenuComponent: QueryList<CmacsSubMenuComponent>;
  @ContentChildren(CmacsMenuItemDirective, { descendants: true })
  listOfCmacsMenuItemDirective: QueryList<CmacsMenuItemDirective>;

  placement = 'rightTop';
  triggerWidth: number;
  expandState = 'collapsed';
  overlayPositions = [...DEFAULT_SUBMENU_POSITIONS];

  private destroy$ = new Subject<void>();
  private isChildMenuSelected = false;
  private isMouseHover = false;

  setOpenState(open: boolean): void {
    this.submenuService.setOpenState(open);
  }

  clickSubMenuTitle(): void {
    if (this.submenuService.mode === 'inline' && !this.menuService.isInDropDown && !this.disabled) {
      this.setOpenState(!this.open);
    }
  }

  setMouseEnterState(value: boolean): void {
    this.isMouseHover = value;
    this.setClassMap();
    this.submenuService.setMouseEnterState(value);
  }

  setTriggerWidth(): void {
    if (this.submenuService.mode === 'horizontal' && this.platform.isBrowser) {
      this.triggerWidth = this.cdkOverlayOrigin.nativeElement.getBoundingClientRect().width;
    }
  }

  onPositionChange(position: ConnectedOverlayPositionChange): void {
// tslint:disable-next-line: no-non-null-assertion
    this.placement = getPlacementName(position)!;
    this.cdr.markForCheck();
  }

  setClassMap(): void {
    const prefixName = this.menuService.isInDropDown ? 'ant-dropdown-menu-submenu' : 'ant-menu-submenu';
    this.updateHostClassService.updateHostClass(this.elementRef.nativeElement, {
      [`${prefixName}`]: true,
      [`${prefixName}-disabled`]: this.disabled,
      [`${prefixName}-open`]: this.open,
      [`${prefixName}-selected`]: this.isChildMenuSelected,
      [`${prefixName}-${this.submenuService.mode}`]: true,
      [`${prefixName}-active`]: this.isMouseHover && !this.disabled
    });
  }

  constructor(
    private elementRef: ElementRef,
    public menuService: NzMenuBaseService,
    private cdr: ChangeDetectorRef,
    public submenuService: CmacsSubmenuService,
    private updateHostClassService: NzUpdateHostClassService,
    private platform: Platform,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {}

  ngOnInit(): void {
    combineLatest(this.submenuService.mode$, this.submenuService.open$)
      .pipe(takeUntil(this.destroy$))
      .subscribe(data => {
        const mode = data[0];
        const open = data[1];
        if (open && mode === 'inline') {
          this.expandState = 'expanded';
        } else if (open && mode === 'horizontal') {
          this.expandState = 'bottom';
        } else if (open && mode === 'vertical') {
          this.expandState = 'active';
        } else {
          this.expandState = 'collapsed';
        }
        this.overlayPositions =
          mode === 'horizontal' ? [POSITION_MAP.bottomLeft] : [POSITION_MAP.rightTop, POSITION_MAP.leftTop];
        if (open !== this.open) {
          this.open = open;
          this.openChange.emit(this.open);
        }
        this.setClassMap();
        this.setTriggerWidth();
      });
    this.submenuService.menuOpen$.pipe(takeUntil(this.destroy$)).subscribe((data: boolean) => {
      this.menuService.menuOpen$.next(data);
    });
    merge(
      this.menuService.mode$,
      this.menuService.inlineIndent$,
      this.submenuService.level$,
      this.submenuService.open$,
      this.submenuService.mode$
    )
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.cdr.markForCheck();
      });
  }

  ngAfterContentInit(): void {
    this.setTriggerWidth();
    this.listOfCmacsMenuItemDirective.changes
      .pipe(
        startWith(true),
        flatMap(() =>
          merge(this.listOfCmacsMenuItemDirective.changes, ...this.listOfCmacsMenuItemDirective.map(menu => menu.selected$))
        ),
        map(() => this.listOfCmacsMenuItemDirective.some(e => e.selected)),
        takeUntil(this.destroy$)
      )
      .subscribe(selected => {
        this.isChildMenuSelected = selected;
        this.setClassMap();
      });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.open) {
      this.submenuService.setOpenState(this.open);
    }
    if (changes.disabled) {
      this.submenuService.disabled = this.disabled;
      this.setClassMap();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
