import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Host,
  Input,
  OnDestroy,
  Optional,
  Renderer2,
  ViewEncapsulation
} from '@angular/core';

import { toBoolean, InputBoolean, NzUpdateHostClassService } from 'ng-zorro-antd/core';
import { NzColDirective, NzRowDirective } from 'ng-zorro-antd/grid';

import { CmacsFormItemComponent } from './cmacs-form-item.component';

@Component({
  selector: 'cmacs-form-label',
  exportAs: 'cmacsFormLabel',
  providers: [NzUpdateHostClassService],
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './cmacs-form-label.component.html'
})
export class CmacsFormLabelComponent extends NzColDirective implements OnDestroy, AfterViewInit {
  @Input() cmacsFor: string;
  @Input() @InputBoolean() cmacsRequired = false;
  @Input()
  set cmacsNoColon(value: boolean) {
    this.noColon = toBoolean(value);
  }
  get cmacsNoColon(): boolean {
    return !!this.noColon;
  }

  defaultNoColon: boolean = false;
  noColon: boolean | string = 'default';

  constructor(
    nzUpdateHostClassService: NzUpdateHostClassService,
    elementRef: ElementRef,
    @Optional() @Host() cmacsFormItemComponent: CmacsFormItemComponent,
    @Optional() @Host() nzRowDirective: NzRowDirective,
    renderer: Renderer2,
    private cdr: ChangeDetectorRef
  ) {
    super(nzUpdateHostClassService, elementRef, cmacsFormItemComponent || nzRowDirective, renderer);
    renderer.addClass(elementRef.nativeElement, 'ant-form-item-label');
  }

  setDefaultNoColon(value: boolean): void {
    this.defaultNoColon = toBoolean(value);
    this.cdr.markForCheck();
  }

  ngOnDestroy(): void {
    super.ngOnDestroy();
  }

  ngAfterViewInit(): void {
    super.ngAfterViewInit();
  }
}
