import {
    Component,
    EventEmitter,
    ChangeDetectionStrategy,
    Output,
    Input,
    ViewEncapsulation,
    OnInit,
    TemplateRef
} from '@angular/core';
import { CdkDragDrop, moveItemInArray, transferArrayItem } from '@angular/cdk/drag-drop';
import { KanbanBoard, KanbanColumn, KanbanItem, KanbanColumnTemplate } from './cmacs-kanban-definitions';
import { InputBoolean } from 'ng-zorro-antd';

@Component({
    selector: 'cmacs-kanban',
    exportAs: 'cmacsKanban',
    templateUrl: './cmacs-kanban.component.html',
    styleUrls: ['./cmacs-kanban.component.css'],
    encapsulation: ViewEncapsulation.None
})

export class CmacsKanbanComponent implements OnInit {

    /** Items to display  */
    @Input() board: KanbanBoard;
    @Input() @InputBoolean() multiselect = false;

    /**
     * Template for items to render. "item" object ist passed (see examples)
     * itemTemplate is for each column
     * itemTemplates is for change the template of a column
     */
    @Input() itemTemplate: TemplateRef<any> = null;
    @Input() itemTemplates: KanbanColumnTemplate[] = null;

    /** Template for column headers. Current groupName will be passed (see examples) */
    @Input() columnHeaderTemplate: TemplateRef<any> = null;

    /** Template for column headers. Current groupName will be passed (see examples) */
    @Input() columnHeaderCollapsedTemplate: TemplateRef<any> = null;

    /** Shrink columns */
    @Input() @InputBoolean() showShrink = false;

    // scrolling
    @Input() @InputBoolean() hasVerticalScroll = false;
    @Input() heightContainer = '500px';

    /** Templates for actions and description panels  */
    @Input() actionPanelTemplates: KanbanColumnTemplate[]  = null;
    @Input() descriptionPanelTemplates: KanbanColumnTemplate[]  = null;

    // styles
    @Input() columnWidth: string;

    // kanban type
    @Input() itemType: string;

    @Output() draggedItem = new EventEmitter<object>();
    @Output() noDraggedItem = new EventEmitter<KanbanItem>();
    @Output() onclickItem = new EventEmitter<object>();
    @Output() ondblclickItem = new EventEmitter<object>();
    @Output() selectionChange = new EventEmitter<KanbanItem[]>();
    @Output() onColumnExpand = new EventEmitter<KanbanColumn>();

    selectedItems: KanbanItem [] = [];
    dragStartedColumn: KanbanColumn = null;

    constructor() { }

    ngOnInit(): void {

    }

    getItemTemplate(id: string): TemplateRef<any> {
        if (this.actionPanelTemplates) {
            const elem = this.itemTemplates.find(i => i.id === id);
            if (elem) { return elem.template; }
        }
        return this.itemTemplate;
    }

    getActionPanel(id: string): TemplateRef<any> {
        if (this.actionPanelTemplates == null) { return null; }
        const elem = this.actionPanelTemplates.find(d => d.id === id);
        return elem ? elem.template : null;
    }

    getDescriptionPanel(id: string): TemplateRef<any> {
        if (this.descriptionPanelTemplates == null) { return null; }
        const elem = this.descriptionPanelTemplates.find(d => d.id === id);
        return elem ? elem.template : null;
    }

    verticalScrollStyle(): any {
        return (this.hasVerticalScroll) ? { height: this.heightContainer} : {};
    }

    boardStyle(): any {
        return (this.hasVerticalScroll) ? { 'min-height': this.heightContainer} : {};
    }

    columnStyle(collapsed: boolean): any {
        return (this.columnWidth && !collapsed) ? { minWidth: this.columnWidth} : {};
    }

    columnExpand(column: KanbanColumn): any {
        column.collapsed = false;
        this.onColumnExpand.emit(column);
    }

    columnCollapse(column: KanbanColumn): void {
        column.collapsed = true;
    }

    setDragStartedColumn(col: KanbanColumn): void {
        this.dragStartedColumn = col;
    }

    drop(event: CdkDragDrop<string[]>, columnId: string): void {

        if (event.previousContainer === event.container) {
            moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
            this.emitDroppedItem(event.item.data, columnId);
        } else {
            if (!this.dragStartedColumn.disabledDrop || !this.dragStartedColumn.disabledDrop.some(id => id === columnId)) {
                if (this.itemType === 'custom' && !event.item.data.data.isEditable) {
                    this.noDraggedItem.emit(event.item.data);
                    this.dragStartedColumn = null;
                    return;
                }
                transferArrayItem(event.previousContainer.data,
                    event.container.data,
                    event.previousIndex,
                    event.currentIndex);
                this.emitDroppedItem(event.item.data, columnId);
            } else {
                this.noDraggedItem.emit(event.item.data);
            }
        }
        this.dragStartedColumn = null;
    }
    private emitDroppedItem(data: any, columnId: string): void {
        data.columnId = columnId;
        this.draggedItem.emit(data);
    }

    clickItem(item: KanbanItem, columnId: string) {
        console.log(this.board);
        item.columnId = columnId;
        this.onclickItem.emit(item);

        // add or remove elements to selected items
        if (!item.disabled) {
            const idx = this.selectedItems.findIndex(elem => elem.id === item.id);
            if (idx === -1) {
                if (!this.multiselect) { this.selectedItems = []; }
                this.selectedItems.push(item);
            } else {
                this.selectedItems.splice(idx, 1);
            }
            this.selectionChange.emit(this.selectedItems);
        }
    }

    dblclickItem(item: KanbanItem, columnId: string) {
        item.columnId = columnId;
        this.ondblclickItem.emit(item);
    }

    isItemSelected(id: string) {
        return this.selectedItems.some(elem => elem.id === id);
    }
}
