import {
  ChangeDetectorRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { ControlValueAccessor } from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { InputBoolean, NzNoAnimationDirective } from 'ng-zorro-antd/core';
import { DateHelperService, NzDatePickerI18nInterface, NzI18nService } from 'ng-zorro-antd/i18n';

import { CandyDate } from './lib/candy-date/candy-date';
import { CmacsPickerComponent } from './picker.component';

// tslint:disable-next-line: max-line-length
const POPUP_STYLE_PATCH = { position: 'relative' }; // Aim to override antd's style to support overlay's position strategy (position:absolute will cause it not working beacuse the overlay can't get the height/width of it's content)

/**
 * The base picker for all common APIs
 */
export abstract class AbstractPickerComponent implements OnInit, OnChanges, OnDestroy, ControlValueAccessor {

  get realOpenState(): boolean {
    return this.picker.animationOpenState;
  } // Use picker's real open state to let re-render the picker's content when shown up

  constructor(
    protected i18n: NzI18nService,
    protected cdr: ChangeDetectorRef,
    protected dateHelper: DateHelperService,
    public noAnimation?: NzNoAnimationDirective
  ) {}
  // --- Common API
  @Input() @InputBoolean() allowClear = true;
  @Input() @InputBoolean() autoFocus = false;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() open: boolean;
  @Input() className: string;
  @Input() disabledDate: (d: Date) => boolean;
  @Input() locale: NzDatePickerI18nInterface;
  @Input() placeHolder: string | string[];
  @Input() popupStyle: object = POPUP_STYLE_PATCH;
  @Input() dropdownClassName: string;
  @Input() size: 'large' | 'small';
  @Input() cmacsStyle: object;
  @Input() format: string;
  @Input() cmacsOpen = false;
  @Input() value: any;
  @Input() width: any = 'initial';

  @Output() readonly cmacsOnOpenChange = new EventEmitter<boolean>();

  @ViewChild(CmacsPickerComponent) protected picker: CmacsPickerComponent;

  isRange = false; // Indicate whether the value is a range value

  protected destroyed$: Subject<void> = new Subject();
  protected isCustomPlaceHolder = false;

  initValue(): void {
    this.value = this.isRange ? [] : null;
  }

  ngOnInit(): void {
    // Subscribe the every locale change if the Locale is not handled by user
    if (!this.locale) {
      this.i18n.localeChange.pipe(takeUntil(this.destroyed$)).subscribe(() => this.setLocale());
    }

    // Default value
    this.initValue();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.popupStyle) {
      // Always assign the popup style patch
      this.popupStyle = this.popupStyle ? { ...this.popupStyle, ...POPUP_STYLE_PATCH } : POPUP_STYLE_PATCH;
    }

    // Mark as customized placeholder by user once PlaceHolder assigned at the first time
    if (changes.placeHolder && changes.placeHolder.firstChange && typeof this.placeHolder !== 'undefined') {
      this.isCustomPlaceHolder = true;
    }

    if (changes.locale) {
      this.setDefaultPlaceHolder();
    }
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }

  closeOverlay(): void {
    this.picker.hideOverlay();
  }

  /**
   * Common handle for value changes
   * @param value changed value
   */
  onValueChange(value: CompatibleValue): void {
    this.value = value;
    if (this.isRange) {
      const vAsRange = this.value as CandyDate[];
      if (vAsRange.length) {
        this.onChangeFn([vAsRange[0].nativeDate, vAsRange[1].nativeDate]);
      } else {
        this.onChangeFn([]);
      }
    } else {
      if (this.value) {
        this.onChangeFn((this.value as CandyDate).nativeDate);
      } else {
        this.onChangeFn(null);
      }
    }
    this.onTouchedFn();
  }

  /**
   * Triggered when overlayOpen changes (different with realOpenState)
   * @param open The overlayOpen in picker component
   */
  onOpenChange(open: boolean): void {
    this.cmacsOnOpenChange.emit(open);
  }

  // ------------------------------------------------------------------------
  // | Control value accessor implements
  // ------------------------------------------------------------------------

  // NOTE: onChangeFn/onTouchedFn will not be assigned if user not use as ngModel
  onChangeFn: (val: CompatibleDate | null) => void = () => void 0;
  onTouchedFn: () => void = () => void 0;

  writeValue(value: CompatibleDate): void {
    this.setValue(value);
    this.cdr.markForCheck();
  }

  // tslint:disable-next-line:no-any
  registerOnChange(fn: any): void {
    this.onChangeFn = fn;
  }

  // tslint:disable-next-line:no-any
  registerOnTouched(fn: any): void {
    this.onTouchedFn = fn;
  }

  setDisabledState(disabled: boolean): void {
    this.disabled = disabled;
    this.cdr.markForCheck();
  }

  // ------------------------------------------------------------------------
  // | Internal methods
  // ------------------------------------------------------------------------

  // Reload locale from i18n with side effects
  private setLocale(): void {
    this.locale = this.i18n.getLocaleData('DatePicker', {});
    this.setDefaultPlaceHolder();
    this.cdr.markForCheck();
  }

  private setDefaultPlaceHolder(): void {
    if (!this.isCustomPlaceHolder && this.locale) {
      this.placeHolder = this.isRange ? this.locale.lang.rangePlaceholder : this.locale.lang.placeholder;
    }
  }

  // Safe way of setting value with default
  private setValue(value: CompatibleDate): void {
    if (this.isRange) {
      this.value = value ? (value as Date[]).map(val => new CandyDate(val)) : [];
    } else {
      this.value = value ? new CandyDate(value as Date) : null;
    }
  }
}

export type CompatibleValue = CandyDate | CandyDate[];

export type CompatibleDate = Date | Date[];
