import { CdkConnectedOverlay, ConnectedOverlayPositionChange, ConnectionPositionPair } from '@angular/cdk/overlay';
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild, ElementRef,
  EventEmitter,
  Host, HostBinding,
  Injector,
  Input,
  OnChanges,
  OnDestroy,
  Optional,
  Output,
  Self,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

import { combineLatest, merge, EMPTY, Observable, Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, map, mapTo, takeUntil } from 'rxjs/operators';

import {
  slideMotion,
  DEFAULT_DROPDOWN_POSITIONS,
  InputBoolean,
  NzDropdownHigherOrderServiceToken,
  NzMenuBaseService,
  NzNoAnimationDirective,
  POSITION_MAP
} from 'ng-zorro-antd/core';
import { NzMenuDirective } from 'ng-zorro-antd/menu';

import { CmacsDropdownDirective } from './cmacs-dropdown.directive';
import { CmacsMenuDropdownService } from './cmacs-menu-dropdown.service';

export type placement = 'bottomLeft' | 'bottomCenter' | 'bottomRight' | 'topLeft' | 'topCenter' | 'topRight' | 'leftTop' | 'rightTop';

export var CMACS_DROPDOWN_POSITIONS = [
  POSITION_MAP.bottomLeft,
  POSITION_MAP.bottomRight,
  POSITION_MAP.topLeft,
  POSITION_MAP.topRight,
  POSITION_MAP.leftTop,
  POSITION_MAP.rightTop
];

export function menuServiceFactory(injector: Injector): NzMenuBaseService {
  return injector.get(CmacsMenuDropdownService);
}

@Component({
  selector: 'cmacs-dropdown',
  exportAs: 'cmacsDropdown',
  preserveWhitespaces: false,
  providers: [
    CmacsMenuDropdownService,
    {
      provide: NzDropdownHigherOrderServiceToken,
      useFactory: menuServiceFactory,
      deps: [[new Self(), Injector]]
    }
  ],
  animations: [slideMotion],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './cmacs-dropdown.component.html',
  styleUrls: ['./cmacs-dropdowm.component.css'],
  styles: [
    `
      .ant-dropdown {
        top: 100%;
        left: 0;
        position: relative;
        width: 100%;
        margin-top: 4px;
        margin-bottom: 4px;
      }
    `
  ]
})
export class CmacsDropdownComponent implements OnDestroy, AfterContentInit, OnChanges {
  triggerWidth = 0;
  updatedPosition = 'bottom';
  updatedOverLay = 'end';
  updatedOrigin = 'end';
  dropDownPosition: 'top' | 'center' | 'bottom' = 'bottom';
  positions: ConnectionPositionPair[] = [...CMACS_DROPDOWN_POSITIONS];
  visible$ = new Subject<boolean>();
  private destroy$ = new Subject<void>();
  @ContentChild(CmacsDropdownDirective) cmacsDropdownDirective: CmacsDropdownDirective;
  @ContentChild(NzMenuDirective) nzMenuDirective: NzMenuDirective;
  @ViewChild(CdkConnectedOverlay) cdkConnectedOverlay: CdkConnectedOverlay;
  @Input() trigger: 'click' | 'hover' = 'hover';
  @Input() overlayClassName = '';
  @Input() overlayStyle: { [key: string]: string } = {};
  @Input() placement: placement = 'bottomLeft';
  @Input() @InputBoolean() clickHide = true;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() visible = false;
  @Input() @InputBoolean() cmacsOpen = false;
  @Input() @InputBoolean() tableFilter = false;
  @Output() readonly visibleChange: EventEmitter<boolean> = new EventEmitter();

  setVisibleStateWhen(visible: boolean, trigger: 'click' | 'hover' | 'all' = 'all'): void {
    if (this.trigger === trigger || trigger === 'all') {
      this.visible$.next(visible);
    }
  }

  onPositionChange(position: ConnectedOverlayPositionChange): void {
    this.dropDownPosition = position.connectionPair.originY;
    this.updatedOverLay = position.connectionPair.overlayX;
    this.updatedOrigin = position.connectionPair.originX;
    this.updatedPosition = this.dropDownPosition;
    this.cdr.detectChanges();
    this.cdr.markForCheck();
  }

  startSubscribe(observable$: Observable<boolean>): void {
    const click$ = this.clickHide ? this.cmacsMenuDropdownService.menuItemClick$.pipe(mapTo(false)) : EMPTY;
    combineLatest(merge(observable$, click$), this.cmacsMenuDropdownService.menuOpen$)
      .pipe(
        map(value => value[0] || value[1]),
        debounceTime(50),
        distinctUntilChanged(),
        takeUntil(this.destroy$)
      )
      .subscribe(visible => {
        if (!this.disabled && this.visible !== visible) {
          this.visible = visible;
          this.visibleChange.emit(this.visible);
          this.triggerWidth = this.cmacsDropdownDirective.elementRef.nativeElement.getBoundingClientRect().width;
          this.cdr.markForCheck();
        }
      });
  }

  updateDisabledState(): void {
    if (this.cmacsDropdownDirective) {
      this.cmacsDropdownDirective.setDisabled(this.disabled);
    }
  }

  constructor(
    protected cdr: ChangeDetectorRef,
    private cmacsMenuDropdownService: CmacsMenuDropdownService,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {}

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  ngAfterContentInit(): void {
    this.startSubscribe(
      merge(
        this.visible$,
        this.trigger === 'hover' ? this.cmacsDropdownDirective.hover$ : this.cmacsDropdownDirective.$click
      )
    );
    this.updateDisabledState();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.visible) {
      this.visible$.next(this.visible);
    }
    if (changes.disabled) {
      this.updateDisabledState();
    }
    if (changes.placement) {
      this.dropDownPosition = this.placement.indexOf('top') !== -1 ? 'top' : 'bottom';
      this.positions = [POSITION_MAP[this.placement], ...this.positions];
    }
  }
}
