import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Inject,
  Optional,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';

import { toCssPixel } from 'ng-zorro-antd/core';

import { CmacsMessageConfig, CMACS_MESSAGE_CONFIG, CMACS_MESSAGE_DEFAULT_CONFIG } from './cmacs-message-config';
import { CmacsMessageDataFilled, CmacsMessageDataOptions } from './cmacs-message.definitions';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'cmacs-message-container',
  exportAs: 'cmacsMessageContainer',
  preserveWhitespaces: false,
  templateUrl: './cmacs-message-container.component.html'
})
export class CmacsMessageContainerComponent {
  messages: CmacsMessageDataFilled[] = [];
  config: Required<CmacsMessageConfig>;
  top: string | null;

  constructor(
    protected cdr: ChangeDetectorRef,
    @Optional() @Inject(CMACS_MESSAGE_DEFAULT_CONFIG) defaultConfig: CmacsMessageConfig,
    @Optional() @Inject(CMACS_MESSAGE_CONFIG) config: CmacsMessageConfig
  ) {
    this.setConfig({ ...defaultConfig, ...config });
  }

  setConfig(config: CmacsMessageConfig): void {
    this.config = { ...this.config, ...config };
    this.top = toCssPixel(this.config.top);
    this.cdr.markForCheck();
  }

  /**
   * Create a new message.
   * @param message Parsed message configuration.
   */
  createMessage(message: CmacsMessageDataFilled): void {
    if (this.messages.length >= this.config.maxStack) {
      this.messages.splice(0, 1);
    }
    message.options = this._mergeMessageOptions(message.options);
    message.onClose = new Subject<boolean>();
    this.messages.push(message);
    this.cdr.detectChanges();
  }

  /**
   * Remove a message by `messageId`.
   * @param messageId Id of the message to be removed.
   * @param userAction Whether this is closed by user interaction.
   */
  removeMessage(messageId: string, userAction: boolean = false): void {
    this.messages.some((message, index) => {
      if (message.messageId === messageId) {
        this.messages.splice(index, 1);
        this.cdr.detectChanges();
        message.onClose!.next(userAction);
        message.onClose!.complete();
        return true;
      }
      return false;
    });
  }

  /**
   * Remove all messages.
   */
  removeMessageAll(): void {
    this.messages = [];
    this.cdr.detectChanges();
  }

  /**
   * Merge default options and custom message options
   * @param options
   */
  protected _mergeMessageOptions(options?: CmacsMessageDataOptions): CmacsMessageDataOptions {
    const defaultOptions: CmacsMessageDataOptions = {
      duration: this.config.duration,
      animate: this.config.animate,
      pauseOnHover: this.config.pauseOnHover
    };
    return { ...defaultOptions, ...options };
  }
}
