import {
  ChangeDetectionStrategy,
  Component,
  ViewEncapsulation,
  Output,
  Input,
  EventEmitter,
  ElementRef,
  AfterViewInit, ChangeDetectorRef
} from '@angular/core';

@Component({
  selector: 'cmacs-color-picker',
  exportAs: 'cmacsColorPicker',
  templateUrl: './cmacs-color-picker.component.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    class: 'cmacs-color-picker'
  },
  styleUrls: ['./cmacs-color-picker.component.css'],
  styles: [
    `
      cmacs-color-picker {
        display: block;
      }
    `
  ]
})
export class CmacsColorPickerComponent implements AfterViewInit{

  @Input() type : 'basic' | 'basicWithTransparent' = 'basic';
  @Input() color: string;
  @Output() colorChange = new EventEmitter<string>();

  width: number = 0;
  basicColorList: string [] = [
    '#000000',
    '#ffffff',
    '#cc2229',
    '#f6503c',
    '#ffa234',
    '#fff100',
    '#00ce7d',
    '#009fe3',
    '#2a7cff',
    '#a100cd',
    '#cf1384',
    '#794c18',
    '#bec4cd',
    '#656c79'
  ];

  constructor(private ref: ElementRef,
              private cdr: ChangeDetectorRef) {}

  setColor(color: string) {
    this.color = color;
    this.colorChange.emit(color);
  }

  isColorPickerType(type: string) {
    return this.type === type;
  }

  ngAfterViewInit() {
    this.width = this.ref.nativeElement.offsetWidth;
    this.cdr.detectChanges();
  }

}
