
import {
  forwardRef,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  EventEmitter,
  Host,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  SimpleChange,
  SkipSelf,
  TemplateRef
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Observable, ReplaySubject, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  isNotNil,
  toBoolean,
  InputBoolean,
  NzFormatBeforeDropEvent,
  NzFormatEmitEvent,
  NzNoAnimationDirective,
  NzTreeBase,
  NzTreeBaseService,
  NzTreeHigherOrderServiceToken,
  NzTreeNode
} from 'ng-zorro-antd/core';

import { CmacsTreeService } from './cmacs-tree.service';

export function NzTreeServiceFactory(
  higherOrderService: NzTreeBaseService,
  treeService: CmacsTreeService
): NzTreeBaseService {
  return higherOrderService ? higherOrderService : treeService;
}

@Component({
// tslint:disable-next-line: component-selector
  selector: 'cmacs-tree',
  exportAs: 'cmacsTree',
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './cmacs-tree.component.html',
  styleUrls: ['./cmacs-tree.component.css'],
  providers: [
    CmacsTreeService,
    {
      provide: NzTreeBaseService,
      useFactory: NzTreeServiceFactory,
      deps: [[new SkipSelf(), new Optional(), NzTreeHigherOrderServiceToken], CmacsTreeService]
    },
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CmacsTreeComponent),
      multi: true
    }
  ]
})
export class CmacsTreeComponent extends NzTreeBase implements OnInit, OnDestroy, ControlValueAccessor, OnChanges {
  @Input() @InputBoolean() showIcon = false;
  @Input() @InputBoolean() showExpand = true;
  @Input() @InputBoolean() showLine = false;
  @Input() expandedIcon: TemplateRef<{ $implicit: NzTreeNode }>;
  @Input() @InputBoolean() checkable = false;
  @Input() @InputBoolean() asyncData = false;
  @Input() @InputBoolean() draggable = false;
  @Input() @InputBoolean() expandAll = false;
  @Input() @InputBoolean() hideUnMatched = false;
  @Input() @InputBoolean() selectMode = false;
  @Input() @InputBoolean() nzCheckStrictly = false;
  @Input() @InputBoolean() nzBlockNode = false;
  @Input() @InputBoolean() inlineEdit = false;
  @Input() @InputBoolean() radio = false;
  @Output() onaddchild: EventEmitter<NzTreeNode> = new EventEmitter<NzTreeNode>();

  /**
   * @deprecated use
   * expandAll instead
   */
  @Input() @InputBoolean() defaultExpandAll = false;
  @Input() beforeDrop: (confirm: NzFormatBeforeDropEvent) => Observable<boolean>;

  @Input()
  @InputBoolean()
  set nzMultiple(value: boolean) {
    this._nzMultiple = toBoolean(value);
    (this.nzTreeService as CmacsTreeService).nzMultiple = toBoolean(value);
  }

  get nzMultiple(): boolean {
    return this._nzMultiple;
  }

  @Input()
  // tslint:disable-next-line:no-any
  set nzData(value: any[]) {
    this.initNzData(value);
  }

  /**
   * @deprecated use
   * nzExpandedKeys instead
   */
  @Input()
  set nzDefaultExpandedKeys(value: string[]) {
    this.nzDefaultSubject.next({ type: 'nzExpandedKeys', keys: value });
  }

  /**
   * @deprecated use
   * nzSelectedKeys instead
   */
  @Input()
  set nzDefaultSelectedKeys(value: string[]) {
    this.nzDefaultSubject.next({ type: 'nzSelectedKeys', keys: value });
  }

  /**
   * @deprecated use
   * nzCheckedKeys instead
   */
  @Input()
  set nzDefaultCheckedKeys(value: string[]) {
    this.nzDefaultSubject.next({ type: 'nzCheckedKeys', keys: value });
  }

  @Input()
  set nzExpandedKeys(value: string[]) {
    this.nzDefaultSubject.next({ type: 'nzExpandedKeys', keys: value });
  }

  @Input()
  set nzSelectedKeys(value: string[]) {
    this.nzDefaultSubject.next({ type: 'nzSelectedKeys', keys: value });
  }

  @Input()
  set nzCheckedKeys(value: string[]) {
    this.nzDefaultSubject.next({ type: 'nzCheckedKeys', keys: value });
  }

  @Input()
  set searchValue(value: string) {
    this._searchValue = value;
    this.nzTreeService.searchExpand(value);
    if (!value.length && this.inlineEdit) {
      this.nzNodes.forEach((node) => {
        node.isExpanded = true;
      });
      this.cdr.detectChanges();
    }
    if (isNotNil(value)) {
      this.searchValueChange.emit(this.nzTreeService.formatEvent('search', null, null));
// tslint:disable-next-line: deprecation
      this.nzOnSearchNode.emit(this.nzTreeService.formatEvent('search', null, null));
    }
  }

  get searchValue(): string {
    return this._searchValue;
  }

  /**
   * To render nodes if root is changed
   */
  get nzNodes(): NzTreeNode[] {
    return this.nzTreeService.rootNodes;
  }

  onaddchildevt($event: NzTreeNode)  {
    this.onaddchild.emit($event);
  }

  // model bind
  @Output() readonly nzExpandedKeysChange: EventEmitter<string[]> = new EventEmitter<string[]>();
  @Output() readonly nzSelectedKeysChange: EventEmitter<string[]> = new EventEmitter<string[]>();
  @Output() readonly nzCheckedKeysChange: EventEmitter<string[]> = new EventEmitter<string[]>();

  @Output() readonly searchValueChange: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  /**
   * @deprecated use
   * searchValueChange instead
   */
  @Output() readonly nzOnSearchNode: EventEmitter<NzFormatEmitEvent> = new EventEmitter();

  @Output() readonly nzClick: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  @Output() readonly nzDblClick: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  @Output() readonly nzContextMenu: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  @Output() readonly nzCheckBoxChange: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  @Output() readonly nzExpandChange: EventEmitter<NzFormatEmitEvent> = new EventEmitter();

  @Output() readonly nzOnDragStart: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  @Output() readonly nzOnDragEnter: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  @Output() readonly nzOnDragOver: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  @Output() readonly nzOnDragLeave: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  @Output() readonly nzOnDrop: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  @Output() readonly nzOnDragEnd: EventEmitter<NzFormatEmitEvent> = new EventEmitter();
  // tslint:disable-next-line:no-any
  @ContentChild('treeTemplate') treeTemplate: TemplateRef<any>;
// tslint:disable-next-line: variable-name
  _searchValue: string;
// tslint:disable-next-line: variable-name
  _nzMultiple = false;
  nzDefaultSubject = new ReplaySubject<{ type: string; keys: string[] }>(6);
  destroy$ = new Subject();
  prefixCls = 'ant-tree';
  classMap = {};

  onChange: (value: NzTreeNode[]) => void = () => null;
  onTouched: () => void = () => null;

  setClassMap(): void {
    this.classMap = {
      [this.prefixCls]: true,
      [this.prefixCls + '-show-line']: this.showLine,
      [`${this.prefixCls}-icon-hide`]: !this.showIcon,
      [`${this.prefixCls}-block-node`]: this.nzBlockNode,
      ['draggable-tree']: this.draggable,
      ['ant-select-tree']: this.selectMode
    };
  }

  writeValue(value: NzTreeNode[]): void {
    this.initNzData(value);
  }

  registerOnChange(fn: (_: NzTreeNode[]) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  // tslint:disable-next-line:no-any
  initNzData(value: any[]): void {
    if (Array.isArray(value)) {
      this.nzTreeService.isCheckStrictly = this.radio ? true : this.nzCheckStrictly;
      this.nzTreeService.isMultiple = this.nzMultiple;
      this.nzTreeService.initTree(this.coerceTreeNodes(value));
    }
  }

  constructor(
    nzTreeService: CmacsTreeService,
    private cdr: ChangeDetectorRef,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    super(nzTreeService);
  }

  ngOnInit(): void {
    this.setClassMap();
    this.nzDefaultSubject.pipe(takeUntil(this.destroy$)).subscribe((data: { type: string; keys: string[] }) => {
      if (!data || !data.keys) {
        return;
      }
      switch (data.type) {
        case 'nzExpandedKeys':
          this.nzTreeService.calcExpandedKeys(data.keys, this.nzNodes);
          this.nzExpandedKeysChange.emit(data.keys);
          break;
        case 'nzSelectedKeys':
          this.nzTreeService.calcSelectedKeys(data.keys, this.nzNodes, this.nzMultiple);
          this.nzSelectedKeysChange.emit(data.keys);
          break;
        case 'nzCheckedKeys':
          this.nzTreeService.calcCheckedKeys(data.keys, this.nzNodes, this.nzCheckStrictly);
          this.nzCheckedKeysChange.emit(data.keys);
          break;
      }
      this.cdr.markForCheck();
    });
    this.nzTreeService
      .eventTriggerChanged()
      .pipe(takeUntil(this.destroy$))
      .subscribe(data => {
        switch (data.eventName) {
          case 'expand':
            this.nzExpandChange.emit(data);
            break;
          case 'click':
            this.nzClick.emit(data);
            break;
          case 'check':
            this.nzCheckBoxChange.emit(data);
            break;
          case 'dblclick':
            this.nzDblClick.emit(data);
            break;
          case 'contextmenu':
            this.nzContextMenu.emit(data);
            break;
          // drag drop
          case 'dragstart':
            this.nzOnDragStart.emit(data);
            break;
          case 'dragenter':
            this.nzOnDragEnter.emit(data);
            break;
          case 'dragover':
            this.nzOnDragOver.emit(data);
            break;
          case 'dragleave':
            this.nzOnDragLeave.emit(data);
            break;
          case 'drop':
            this.nzOnDrop.emit(data);
            break;
          case 'dragend':
            this.nzOnDragEnd.emit(data);
            break;
          case 'selectedMultiple':
            this.onSelectionMultiple(data);
            break;
        }
      });
  }

  onSelectionMultiple(selectedNode) {
    let flatNodesList: NzTreeNode[] = [], idxs = [];
    const nodesSelected = this.nzTreeService.getSelectedNodeList();
    const nodesSelectedCount = nodesSelected.length;
    this.nzNodes.forEach(node => {
      let array = this.convertTreeToList(node, selectedNode.node, nodesSelected[nodesSelectedCount - 1],  idxs);
      flatNodesList = flatNodesList.concat(array);
    });
    let i = idxs[0];
    for (i; i <= idxs[1]; i++) {
      if (flatNodesList[i].isSelectable && !flatNodesList[i].isDisabled) {
        flatNodesList[i].isSelected = true;
      }
    }
  }


  ngOnChanges(changes: { [propertyName: string]: SimpleChange }): void {
    if (changes.nzCheckStrictly) {
      this.nzTreeService.isCheckStrictly = toBoolean(changes.nzCheckStrictly.currentValue);
    }
    if (changes.nzMultiple) {
      this._nzMultiple = toBoolean(changes.nzMultiple.currentValue);
      (this.nzTreeService as CmacsTreeService).nzMultiple = toBoolean(changes.nzMultiple.currentValue);
    }
  }

  addParent(node: any, index = null) {
    let parent = this.coerceTreeNodes(node);
    if (index) {
      this.nzTreeService.rootNodes.splice( index, 0, ...parent );
    } else {
      this.nzTreeService.rootNodes.push(...parent);
    }
    this.cdr.detectChanges();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  convertTreeToList(root, endNode, startNode, idxs) {
    let stack = [], array = [], hashMap = {};
    stack.push(root);

    while(stack.length !== 0) {
      let node: NzTreeNode = stack.pop();
      this.visitNode(node, hashMap, array);
      if (endNode.key === node.key || startNode.key === node.key) {
        idxs.push(array.length - 1);
      }
      if(node.children.length) {
        for(let i = node.children.length - 1; i >= 0; i--) {
          stack.push(node.children[i]);
        }
      }
    }
    return array;
  }

  visitNode(node, hashMap, array) {
    if(!hashMap[node.key]) {
      hashMap[node.key] = true;
      array.push(node);
    }
  }
}
