import { FocusTrap, FocusTrapFactory } from '@angular/cdk/a11y';

import { ESCAPE } from '@angular/cdk/keycodes';
import { BlockScrollStrategy, Overlay, OverlayRef } from '@angular/cdk/overlay';
import { DOCUMENT } from '@angular/common';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  ElementRef,
  EventEmitter,
  Inject,
  Injector,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output, QueryList,
  SimpleChanges,
  TemplateRef,
  Type,
  ViewChild, ViewChildren,
  ViewContainerRef
} from '@angular/core';

import { fromEvent, Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { getElementOffset, isPromise, InputBoolean } from 'ng-zorro-antd/core';
import { NzI18nService } from 'ng-zorro-antd/i18n';

import { MODAL_CONFIG, ModalConfig } from './cmacs-modal-config';
import { ModalControlService } from './cmacs-modal-control.service';
import { CmacsModalRef } from './cmacs-modal-ref.class';
import { ModalButtonOptions, ModalOptions, ModalType, OnClickCallback } from './cmacs-modal.type';

export const MODAL_ANIMATE_DURATION = 200; // Duration when perform animations (ms)

type AnimationState = 'enter' | 'leave' | null;

@Component({
  selector: 'cmacs-modal',
  exportAs: 'cmacsModal',
  templateUrl: './cmacs-modal.component.html',
  styleUrls: ['./cmacs-modal.component.css'],
  // Using OnPush for modal caused footer can not to detect changes. we can fix it when 8.x.
  changeDetection: ChangeDetectionStrategy.Default
})

// tslint:disable-next-line:no-any
export class CmacsModalComponent<T = any, R = any> extends CmacsModalRef<T, R>
  implements OnInit, OnChanges, AfterViewInit, OnDestroy, ModalOptions<T> {
  @Input() @InputBoolean() visible: boolean = false;
  @Input() @InputBoolean() closable: boolean = true;
  @Input() @InputBoolean() okLoading: boolean = false;
  @Input() @InputBoolean() okDisabled: boolean = false;
  @Input() @InputBoolean() cancelDisabled: boolean = false;
  @Input() @InputBoolean() cancelLoading: boolean = false;
  @Input() @InputBoolean() keyboard: boolean = true;
  @Input() @InputBoolean() noAnimation = false;
  @Input() @InputBoolean() cmacsMask: boolean;
  @Input() @InputBoolean() cmacsMaskClosable: boolean;
  @Input() @InputBoolean() showHelpfulTips = true;
  @Input() @InputBoolean() useCmacsDefaultSizes = true;
  @Input() content: string | TemplateRef<{}> | Type<T>; // [STATIC] If not specified, will use <ng-content>
  @Input() componentParams: T; // [STATIC] ONLY avaliable when content is a component
  @Input() footer: string | TemplateRef<{}> | Array<ModalButtonOptions<T>> | null; // [STATIC] Default Modal ONLY
  @Input() getContainer: HTMLElement | OverlayRef | (() => HTMLElement | OverlayRef) = () => this.overlay.create(); // [STATIC]
  @Input() zIndex: number = 1000;
  @Input() leftPanelCols: number;
  @Input() centerPanelCols: number;
  @Input() rightPanelCols: number;
  @Input() width: number | string = 400;
  @Input() wrapClassName: string;
  @Input() className: string;
  @Input() cmacsStyle: object;
  @Input() title: string | TemplateRef<{}>;
  @Input() maskStyle: object;
  @Input() bodyStyle: object;
  @Input() cmacsOkText: string | null;
  @Input() cmacsCancelText: string | null;
  @Input() okType = 'primary';
  @Input() iconType: string = 'question-circle'; // Confirm Modal ONLY
  @Input() modalType: ModalType = 'transactional';

  @Input() @Output() readonly onOk: EventEmitter<T> | OnClickCallback<T> = new EventEmitter<T>();
  @Input() @Output() readonly onCancel: EventEmitter<T> | OnClickCallback<T> = new EventEmitter<T>();

  @Output() readonly cmacsAfterOpen = new EventEmitter<void>(); // Trigger when modal open(visible) after animations
  @Output() readonly cmacsAfterClose = new EventEmitter<R>(); // Trigger when modal leave-animation over
  @Output() readonly visibleChange = new EventEmitter<boolean>();

  @ViewChild('modalContainer') modalContainer: ElementRef;
  @ViewChild('bodyContainer', { read: ViewContainerRef }) bodyContainer: ViewContainerRef;
  @ViewChild('autoFocusButtonOk', { read: ElementRef }) autoFocusButtonOk: ElementRef; // Only aim to focus the ok button that needs to be auto focused
  @ViewChildren('tipsCreationWizard') tipsCreationWizard: QueryList<any>;

  get afterOpen(): Observable<void> {
    // Observable alias for cmacsAfterOpen
    return this.cmacsAfterOpen.asObservable();
  }

  get afterClose(): Observable<R> {
    // Observable alias for afterClose
    return this.cmacsAfterClose.asObservable();
  }

  get cancelText(): string {
    return this.cmacsCancelText || this.locale.cancelText!;
  }

  get okText(): string {
    return this.cmacsOkText || this.locale.okText!;
  }

  get hidden(): boolean {
    return !this.visible && !this.animationState;
  } // Indicate whether this dialog should hidden

  /**
   * @description
   * The calculated highest weight of mask value
   *
   * Weight of different mask input:
   * component default value < global configuration < component input value
   */
  get mask(): boolean {
    if (this.cmacsMask != null) {
      return this.cmacsMask;
    } else if (this.modalGlobalConfig && this.modalGlobalConfig.cmacsMask != null) {
      return this.modalGlobalConfig.cmacsMask;
    } else {
      return true;
    }
  }

  /**
   * @description
   * The calculated highest weight of maskClosable value
   *
   * Weight of different maskClosable input:
   * component default value < global configuration < component input value
   */
  get maskClosable(): boolean {
    if (this.cmacsMaskClosable != null) {
      return this.cmacsMaskClosable;
    } else if (this.modalGlobalConfig && this.modalGlobalConfig.cmacsMaskClosable != null) {
      return this.modalGlobalConfig.cmacsMaskClosable;
    } else {
      return true;
    }
  }

  locale: { okText?: string; cancelText?: string } = {};
  maskAnimationClassMap: object | null;
  modalAnimationClassMap: object | null;
  transformOrigin = '0px 0px 0px'; // The origin point that animation based on

  private contentComponentRef: ComponentRef<T>; // Handle the reference when using content as Component
  private animationState: AnimationState; // Current animation state
  private container: HTMLElement | OverlayRef;
  private unsubscribe$ = new Subject<void>();
  private previouslyFocusedElement: HTMLElement;
  private focusTrap: FocusTrap;
  private scrollStrategy: BlockScrollStrategy;

  [key: string]: any; // tslint:disable-line:no-any

  constructor(
    private overlay: Overlay,
    private i18n: NzI18nService,
    private cfr: ComponentFactoryResolver,
    private elementRef: ElementRef,
    private viewContainer: ViewContainerRef,
    private modalControl: ModalControlService,
    private focusTrapFactory: FocusTrapFactory,
    private cdr: ChangeDetectorRef,
    @Optional() @Inject(MODAL_CONFIG) private modalGlobalConfig: ModalConfig,
    @Inject(DOCUMENT) private document: any // tslint:disable-line:no-any
  ) {
    super();
    this.scrollStrategy = this.overlay.scrollStrategies.block();
  }

  ngOnInit(): void {
    this.i18n.localeChange.pipe(takeUntil(this.unsubscribe$)).subscribe(() => {
      this.locale = this.i18n.getLocaleData('Modal') as { okText: string; cancelText: string };
    });

    fromEvent<KeyboardEvent>(this.document.body, 'keydown')
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(e => this.keydownListener(e));

    if (this.isComponent(this.content)) {
      this.createDynamicComponent(this.content as Type<T>); // Create component along without View
    }

    if (this.isModalButtons(this.footer)) {
      // Setup default button options
      this.footer = this.formatModalButtons(this.footer as Array<ModalButtonOptions<T>>);
    }

    // Place the modal dom to elsewhere
    this.container = typeof this.getContainer === 'function' ? this.getContainer() : this.getContainer;
    if (this.container instanceof HTMLElement) {
      this.container.appendChild(this.elementRef.nativeElement);
    } else if (this.container instanceof OverlayRef) {
      // NOTE: only attach the dom to overlay, the view container is not changed actually
      this.container.overlayElement.appendChild(this.elementRef.nativeElement);
    }

    // Creation modal settings
    if (this.isModalType('creation') || this.isModalType('helpfulTips') || this.isModalType('helpfulTipsNoPanel')) {
      this.cmacsCancelText = null;
      this.cmacsOkText = null;
    }

    if(this.isModalType('helpfulTips')){
      this.width = 835;
    }

    if(this.isModalType('helpfulTipsNoPanel') || this.isModalType('basic')){
      this.width = 570;
    }

    if (this.useCmacsDefaultSizes) {
      this.transformToDefaultSizes();
    }

    // Register modal when afterOpen/afterClose is stable
    this.modalControl.registerModal(this);
  }

  // [NOTE] NOT available when using by service!
  // Because ngOnChanges never be called when using by service,
  // here we can't support "content"(Component) etc. as inputs that initialized dynamically.
  // BUT: User also can change "content" dynamically to trigger UI changes (provided you don't use Component that needs initializations)
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.visible) {
      this.handleVisibleStateChange(this.visible, !changes.visible.firstChange); // Do not trigger animation while initializing
    }
    if (changes.showHelpfulTips !== undefined || changes.useCmacsDefaultSizes !== undefined) {
      if (this.useCmacsDefaultSizes) { this.transformToDefaultSizes(); }
    }
  }

  transformToDefaultSizes() {
    switch (this.modalType) {
      case 'creation':
        this.width = this.showHelpfulTips ? 1100 : 835;
        break;
        case 'helpfulTips':
        this.width = 835;
        break;
        case 'helpfulTipsNoPanel':
        this.width = 570;
        break;
        case 'basic':
        this.width = 570;
        break;
    }
  }

  ngAfterViewInit(): void {
    // If using Component, it is the time to attach View while bodyContainer is ready
    if (this.contentComponentRef) {
      this.bodyContainer.insert(this.contentComponentRef.hostView);
    }

    if (this.autoFocusButtonOk) {
      (this.autoFocusButtonOk.nativeElement as HTMLButtonElement).focus();
    }

  }

  ngOnDestroy(): void {
    // Close self before destructing
    if (!this.isModalType('interaction')) {
      this.changeVisibleFromInside(false).then(() => {
        this.destroyModal();
      });
    } else {
      this.closeInteraction();
      this.destroyModal();
    }

  }

  destroyModal() {
    this.modalControl.deregisterModal(this);

    if (this.container instanceof OverlayRef) {
      this.container.dispose();
    }
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  keydownListener(event: KeyboardEvent): void {
    if (event.keyCode === ESCAPE && this.keyboard) {
      this.onClickOkCancel('cancel');
    }
  }

  open(): void {
    this.changeVisibleFromInside(true);
  }

  close(result?: R): void {
    this.changeVisibleFromInside(false, result);
  }

  destroy(result?: R): void {
    // Destroy equals Close
    this.close(result);
  }

  triggerOk(): void {
    this.onClickOkCancel('ok');
  }

  triggerCancel(): void {
    this.onClickOkCancel('cancel');
  }

  getInstance(): CmacsModalComponent {
    return this;
  }

  getContentComponentRef(): ComponentRef<T> {
    return this.contentComponentRef;
  }

  getContentComponent(): T {
    return this.contentComponentRef && this.contentComponentRef.instance;
  }

  getElement(): HTMLElement {
    return this.elementRef && this.elementRef.nativeElement;
  }

  onClickMask($event: MouseEvent): void {
    if (
      this.cmacsMask &&
      this.cmacsMaskClosable &&
      ($event.target as HTMLElement).classList.contains('ant-modal-wrap') &&
      this.visible
    ) {
      this.onClickOkCancel('cancel');
    }
  }

  isModalType(type: ModalType): boolean {
    return this.modalType === type;
  }

  public onClickCloseBtn(): void {
    if (this.visible) {
      this.onClickOkCancel('cancel');
    }
  }

  public closeInteraction(): void {
    this.visible = !this.visible;
    this.visibleChange.emit(this.visible);
  }

  public onClickOkCancel(type: 'ok' | 'cancel'): void {
    const trigger = { ok: this.onOk, cancel: this.onCancel }[type];
    const loadingKey = { ok: 'okLoading', cancel: 'cancelLoading' }[type];
    if (trigger instanceof EventEmitter) {
      trigger.emit(this.getContentComponent());
    } else if (typeof trigger === 'function') {
      const result = trigger(this.getContentComponent());
      const caseClose = (doClose: boolean | void | {}) => doClose !== false && this.close(doClose as R); // Users can return "false" to prevent closing by default
      if (isPromise(result)) {
        this[loadingKey] = true;
        const handleThen = (doClose: boolean | void | {}) => {
          this[loadingKey] = false;
          caseClose(doClose);
        };
        (result as Promise<void>).then(handleThen).catch(handleThen);
      } else {
        caseClose(result);
      }
    }
  }

  public isNonEmptyString(value: {}): boolean {
    return typeof value === 'string' && value !== '';
  }

  public isTemplateRef(value: {}): boolean {
    return value instanceof TemplateRef;
  }

  public isComponent(value: {}): boolean {
    return value instanceof Type;
  }

  public isModalButtons(value: string | TemplateRef<{}> | Array<ModalButtonOptions<T>> | null): boolean {
    return Array.isArray(value) && value.length > 0;
  }

  // Do rest things when visible state changed
  private handleVisibleStateChange(visible: boolean, animation: boolean = true, closeResult?: R): Promise<void> {
    if (!this.isModalType('interaction')) {
      if (visible) {
        // Hide scrollbar at the first time when shown up
        this.scrollStrategy.enable();
        this.savePreviouslyFocusedElement();
        this.trapFocus();
      }

      return Promise.resolve(animation ? this.animateTo(visible) : undefined).then(() => {
        // Emit open/close event after animations over
        if (visible) {
          this.cmacsAfterOpen.emit();
        } else {
          this.cmacsAfterClose.emit(closeResult);
          this.restoreFocus();
          this.scrollStrategy.disable();
          // Mark the for check so it can react if the view container is using OnPush change detection.
          this.cdr.markForCheck();
        }
      });
    }
  }

  // Lookup a button's property, if the prop is a function, call & then return the result, otherwise, return itself.
  public getButtonCallableProp(options: ModalButtonOptions<T>, prop: string): {} {
    const value = options[prop];
    const args: T[] = [];
    if (this.contentComponentRef) {
      args.push(this.contentComponentRef.instance);
    }
    return typeof value === 'function' ? value.apply(options, args) : value;
  }

  // On footer's modal button click
  public onButtonClick(button: ModalButtonOptions<T>): void {
    const result = this.getButtonCallableProp(button, 'onClick'); // Call onClick directly
    if (isPromise(result)) {
      button.loading = true;
      (result as Promise<{}>).then(() => (button.loading = false)).catch(() => (button.loading = false));
    }
  }

  // Change visible from inside
  private changeVisibleFromInside(visible: boolean, closeResult?: R): Promise<void> {
    if (this.visible !== visible) {
      // Change visible value immediately
      this.visible = visible;
      this.visibleChange.emit(visible);
      return this.handleVisibleStateChange(visible, true, closeResult);
    }
    return Promise.resolve();
  }

  private changeAnimationState(state: AnimationState): void {
    this.animationState = state;
    if (state) {
      this.maskAnimationClassMap = {
        [`fade-${state}`]: true,
        [`fade-${state}-active`]: true
      };
      this.modalAnimationClassMap = {
        [`zoom-${state}`]: true,
        [`zoom-${state}-active`]: true
      };
    } else {
      this.maskAnimationClassMap = this.modalAnimationClassMap = null;
    }
  }

  private animateTo(isVisible: boolean): Promise<void> {
    if (isVisible) {
      // Figure out the lastest click position when shows up
      setTimeout(() => this.updateTransformOrigin()); // [NOTE] Using timeout due to the document.click event is fired later than visible change, so if not postponed to next event-loop, we can't get the lastest click position
    }

    this.changeAnimationState(isVisible ? 'enter' : 'leave');
    return new Promise(resolve =>
      setTimeout(
        () => {
          // Return when animation is over
          this.changeAnimationState(null);
          resolve();
        },
        this.noAnimation ? 0 : MODAL_ANIMATE_DURATION
      )
    );
  }

  private formatModalButtons(buttons: Array<ModalButtonOptions<T>>): Array<ModalButtonOptions<T>> {
    return buttons.map(button => {
      return {
        ...{
          type: 'default',
          size: 'default',
          autoLoading: true,
          show: true,
          loading: false,
          disabled: false
        },
        ...button
      };
    });
  }

  /**
   * Create a component dynamically but not attach to any View (this action will be executed when bodyContainer is ready)
   * @param component Component class
   */
  private createDynamicComponent(component: Type<T>): void {
    const factory = this.cfr.resolveComponentFactory(component);
    const childInjector = Injector.create({
      providers: [{ provide: CmacsModalRef, useValue: this }],
      parent: this.viewContainer.parentInjector
    });
    this.contentComponentRef = factory.create(childInjector);
    if (this.componentParams) {
      Object.assign(this.contentComponentRef.instance, this.componentParams);
    }
    // Do the first change detection immediately (or we do detection at ngAfterViewInit, multi-changes error will be thrown)
    this.contentComponentRef.changeDetectorRef.detectChanges();
  }

  // Update transform-origin to the last click position on document
  private updateTransformOrigin(): void {
    const modalElement = this.modalContainer.nativeElement as HTMLElement;
    if (this.previouslyFocusedElement) {
      const previouslyDOMRect = this.previouslyFocusedElement.getBoundingClientRect();
      const lastPosition = getElementOffset(this.previouslyFocusedElement);
      const x = lastPosition.left + previouslyDOMRect.width / 2;
      const y = lastPosition.top + previouslyDOMRect.height / 2;
      this.transformOrigin = `${x - modalElement.offsetLeft}px ${y - modalElement.offsetTop}px 0px`;
    }
  }

  private savePreviouslyFocusedElement(): void {
    if (this.document) {
      this.previouslyFocusedElement = this.document.activeElement as HTMLElement;
    }
  }

  private trapFocus(): void {
    if (!this.focusTrap) {
      this.focusTrap = this.focusTrapFactory.create(this.elementRef.nativeElement);
    }
    this.focusTrap.focusInitialElementWhenReady();
  }

  private restoreFocus(): void {
    // We need the extra check, because IE can set the `activeElement` to null in some cases.
    if (this.previouslyFocusedElement && typeof this.previouslyFocusedElement.focus === 'function') {
      this.previouslyFocusedElement.focus();
    }
    if (this.focusTrap) {
      this.focusTrap.destroy();
    }
  }
}
