import {
  AfterContentInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren, HostListener,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  QueryList,
  SimpleChanges,
  TemplateRef,
  AfterViewInit,
  ViewEncapsulation, EventEmitter, Output
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { toBoolean, NgClassType, NzSizeDSType } from 'ng-zorro-antd/core';

import { CmacsStepComponent} from './cmacs-step.component';
import {InputBoolean} from "ng-zorro-antd";

export type NzDirectionType = 'horizontal' | 'vertical';
export type NzStatusType = 'wait' | 'process' | 'finish' | 'error';


@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  selector: 'cmacs-wizard',
  exportAs: 'cmacsWizard',
  templateUrl: './cmacs-wizard.component.html',
  styleUrls: ['./cmacs-wizard.component.css'],
})
export class CmacsWizardComponent implements OnChanges, OnInit, OnDestroy, AfterContentInit {

  @ContentChildren(CmacsStepComponent) steps: QueryList<CmacsStepComponent>;

  @Input() current = 0;
  @Input() direction: NzDirectionType = 'vertical';
  @Input() labelPlacement: 'horizontal' | 'vertical' = 'horizontal';
  @Input() size: NzSizeDSType = 'default';
  @Input() startIndex = 0;
  @Input() @InputBoolean() clickable = false;
  @Input() status: NzStatusType = 'process';
  @Output() indexChange: EventEmitter<number> = new EventEmitter<number>();

  @Input()
  set nzProgressDot(value: boolean | TemplateRef<{ $implicit: TemplateRef<void>; status: string; index: number }>) {
    if (value instanceof TemplateRef) {
      this.showProcessDot = true;
      this.customProcessDotTemplate = value;
    } else {
      this.showProcessDot = toBoolean(value);
    }
    this.updateChildrenSteps();
  }
  showProcessDot = false;
  customProcessDotTemplate: TemplateRef<{ $implicit: TemplateRef<void>; status: string; index: number }>;

  classMap: NgClassType;

  private destroy$ = new Subject<void>();

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.startIndex || changes.direction || changes.status || changes.current) {
      this.updateChildrenSteps();
    }
    if (changes.direction || changes.nzProgressDot || changes.labelPlacement || changes.size) {
      this.setClassMap();
    }
  }

  ngOnInit(): void {
    this.setClassMap();
    this.updateChildrenSteps();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  ngAfterContentInit(): void {
    this.updateChildrenSteps();
    if (this.steps) {
      this.steps.changes.pipe(takeUntil(this.destroy$)).subscribe(() => {
        this.updateChildrenSteps();
      });
    }
  }

  private updateChildrenSteps(): void {
    /*let stepPrimaryIndexes = [];
    const stepsFiltered = this.steps.filter((step: CmacsStepComponent, index: number) => {
      if (!step.secondary) {
        stepPrimaryIndexes.push(index);
      }
      return !step.secondary;
    });
    this.steps.filter((step: CmacsStepComponent, index: number) => {
      if (step.secondary && stepPrimaryIndexes[this.current] > index) {
        step.index = 0;
        step.currentIndex = 1;
      } else {
        step.index = 1;
        step.currentIndex = 0;
      }
      return !step.secondary;
    });
    const length = stepsFiltered.length;
    stepsFiltered.forEach((step, index) => {*/
    if (this.steps) {
      const length = this.steps.length;
      this.steps.toArray().forEach((step, index) => {
        Promise.resolve().then(() => {
          step.clickable = this.clickable;
          step.parent = this.elem;
          step.outStatus = this.status;
          step.showProcessDot = this.showProcessDot;
          if (this.customProcessDotTemplate) {
            step.customProcessTemplate = this.customProcessDotTemplate;
          }
          step.direction = this.direction;
          step.index = index + this.startIndex;
          step.currentIndex = this.current;
          step.last = length === index + 1;
          step.markForCheck();
        });
      });
    }
  }

  constructor(private elem: ElementRef){
  }

  private setClassMap(): void {
    this.classMap = {
      [`ant-steps-${this.direction}`]: true,
      [`ant-steps-label-horizontal`]: this.direction === 'horizontal',
      [`ant-steps-label-vertical`]:
      (this.showProcessDot || this.labelPlacement === 'vertical') && this.direction === 'horizontal',
      [`ant-steps-dot`]: this.showProcessDot,
      ['ant-steps-small']: this.size === 'small'
    };
  }

}
