import {
  AfterContentInit,
  ContentChildren,
  Directive,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  QueryList,
  Renderer2,
  SimpleChanges
} from '@angular/core';
import { Subject } from 'rxjs';
import { startWith, takeUntil } from 'rxjs/operators';

import { InputBoolean, NzUpdateHostClassService } from 'ng-zorro-antd/core';

import { CmacsFormLabelComponent } from './cmacs-form-label.component';

@Directive({
  selector: '[cmacs-form]',
  exportAs: 'cmacsForm',
  providers: [NzUpdateHostClassService]
})
export class CmacsFormDirective implements OnInit, OnChanges, AfterContentInit, OnDestroy {
  @Input() layout = 'horizontal';
  @Input() @InputBoolean() cmacsNoColon: boolean = false;

  @ContentChildren(CmacsFormLabelComponent, { descendants: true }) cmacsFormLabelComponent: QueryList<CmacsFormLabelComponent>;

  destroy$ = new Subject();

  setClassMap(): void {
    this.nzUpdateHostClassService.updateHostClass(this.elementRef.nativeElement, {
      [`ant-form-${this.layout}`]: this.layout
    });
  }

  updateItemsDefaultColon(): void {
    if (this.cmacsFormLabelComponent) {
      this.cmacsFormLabelComponent.forEach(item => item.setDefaultNoColon(this.cmacsNoColon));
    }
  }

  constructor(
    private elementRef: ElementRef,
    private renderer: Renderer2,
    private nzUpdateHostClassService: NzUpdateHostClassService
  ) {
    this.renderer.addClass(elementRef.nativeElement, 'ant-form');
  }

  ngOnInit(): void {
    this.setClassMap();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.setClassMap();
    if (changes.hasOwnProperty('cmacsNoColon')) {
      this.updateItemsDefaultColon();
    }
  }

  ngAfterContentInit(): void {
    this.cmacsFormLabelComponent.changes
      .pipe(
        startWith(null),
        takeUntil(this.destroy$)
      )
      .subscribe(() => {
        this.updateItemsDefaultColon();
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
