import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  TemplateRef,
  OnChanges,
  SimpleChanges
} from '@angular/core';

@Component({
  selector: 'cmacs-section',
  templateUrl: 'cmacs-section.component.html',
  styleUrls: ['cmacs-section.component.css'],
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class CmacsSectionComponent implements OnInit, OnChanges {

  @Input() extra: string | TemplateRef<void>;
  @Input() widgetSpan = '24';
  @Input() title = '';
  @Input() titleIcon!: string;
  @Input() showCollapse = true;
  @Input() extraData!: any;
  @Input() collapsed = false;
  @Input() validate = false;

  @Output() onbeforecollapse: EventEmitter<any> = new EventEmitter<any>();
  @Output() oncollapse: EventEmitter<any> = new EventEmitter<any>();
  @Output() onbeforeexpand: EventEmitter<any> = new EventEmitter<any>();
  @Output() onexpand: EventEmitter<any> = new EventEmitter<any>();

  constructor() { }

  ngOnInit() {
  }

  collapseSection(): void {
    if (!this.validate) {
      this.collapsed = !this.collapsed;
      this.triggerCollapseEvents(false);
    } else {
      this.triggerCollapseEvents(true);
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.collapsed && changes.collapsed.previousValue !== undefined) {
      this.triggerCollapseEvents(false);
    }

  }

  triggerCollapseEvents(before: boolean) {
    if (before) {
      if (this.collapsed) {
        this.onbeforeexpand.emit();
      } else {
        this.onbeforecollapse.emit();
      }
    } else if (this.collapsed) {
      this.oncollapse.emit();
    } else {
      this.onexpand.emit();
    }


  }
}
