import { BACKSPACE } from '@angular/cdk/keycodes';
import { CdkConnectedOverlay, CdkOverlayOrigin, ConnectedOverlayPositionChange } from '@angular/cdk/overlay';
import {
  forwardRef,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Host,
  Injector,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  Renderer2,
  Self,
  SimpleChanges,
  TemplateRef,
  ViewChild, AfterViewInit
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

import { merge, of as observableOf, Subscription } from 'rxjs';
import { filter, tap } from 'rxjs/operators';

import {
  isNotNil,
  slideMotion,
  zoomMotion,
  InputBoolean,
  NzFormatEmitEvent,
  NzNoAnimationDirective,
  NzSizeLDSType,
  NzTreeBase,
  NzTreeBaseService,
  NzTreeHigherOrderServiceToken,
  NzTreeNode,
  NzTreeNodeOptions
} from 'ng-zorro-antd/core';
import { NzTreeComponent } from 'ng-zorro-antd/tree';

import { CmacsTreeSelectService } from './cmacs-tree-select.service';
import {CmacsTreeComponent} from "../cmacs-tree/cmacs-tree.component";

export function higherOrderServiceFactory(injector: Injector): NzTreeBaseService {
  return injector.get(CmacsTreeSelectService);
}

@Component({
  selector: 'cmacs-tree-select',
  exportAs: 'cmacsTreeSelect',
  animations: [slideMotion, zoomMotion],
  templateUrl: './cmacs-tree-select.component.html',
  styleUrls: ['./cmacs-tree-select.component.css'],
  providers: [
    CmacsTreeSelectService,
    {
      provide: NzTreeHigherOrderServiceToken,
      useFactory: higherOrderServiceFactory,
      deps: [[new Self(), Injector]]
    },
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CmacsTreeSelectComponent),
      multi: true
    }
  ],
  host: {
    '[class.ant-select-lg]': 'size==="large"',
    '[class.ant-select-sm]': 'size==="small"',
    '[class.ant-select-enabled]': '!disabled',
    '[class.ant-select-disabled]': 'disabled',
    '[class.ant-select-allow-clear]': 'allowClear',
    '[class.ant-select-open]': 'open',
    '(click)': 'trigger()'
  },
  styles: [
    `
      .ant-select-dropdown {
        top: 100%;
        left: 0;
        position: relative;
        width: 100%;
        margin-top: 4px;
        margin-bottom: 4px;
        overflow: auto;
      }
      
      cmacs-tree-select {
        overflow: hidden;
      }
    `
  ]
})
export class CmacsTreeSelectComponent extends NzTreeBase implements ControlValueAccessor, OnInit, OnDestroy, OnChanges, AfterViewInit {
  @Input() @InputBoolean() allowClear = true;
  @Input() @InputBoolean() showExpand = true;
  @Input() @InputBoolean() showLine = false;
  @Input() expandedIcon: TemplateRef<{ $implicit: NzTreeNode }>;
  @Input() @InputBoolean() dropdownMatchSelectWidth = true;
  @Input() @InputBoolean() checkable = false;
  @Input() @InputBoolean() showSearch = false;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() asyncData = false;
  @Input() @InputBoolean() multiple = false;
  @Input() @InputBoolean() defaultExpandAll = false;
  @Input() @InputBoolean() cmacsOpen = false;
  @Input() @InputBoolean() inlineEdit = false;
  @Input() @InputBoolean() radio = false;
  @Input() notFoundContent: string;
  @Input() nodes: Array<NzTreeNode | NzTreeNodeOptions> = [];
  @Input() open = false;
  @Input() size: NzSizeLDSType = 'default';
  @Input() placeHolder = '';
  @Input() dropdownStyle: { [key: string]: string };
  @Input() defaultExpandedKeys: string[] = [];
  @Input() displayWith: (node: NzTreeNode) => string | undefined = (node: NzTreeNode) => node.title;
  @Input() maxTagCount: number;
  @Input() maxTagPlaceholder: TemplateRef<{ $implicit: NzTreeNode[] }>;
  @Output() readonly openChange = new EventEmitter<boolean>();
  @Output() readonly cleared = new EventEmitter<void>();
  @Output() readonly removed = new EventEmitter<NzTreeNode>();
  @Output() readonly expandChange = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly treeClick = new EventEmitter<NzFormatEmitEvent>();
  @Output() readonly treeCheckBoxChange = new EventEmitter<NzFormatEmitEvent>();
  @Output() onaddchild: EventEmitter<NzTreeNode> = new EventEmitter<NzTreeNode>();
  @Output() onaddparent: EventEmitter<any> = new EventEmitter<any>();

  @ViewChild('inputElement') inputElement: ElementRef;
  @ViewChild('treeRef') treeRef: CmacsTreeComponent;
  @ViewChild(CdkOverlayOrigin) cdkOverlayOrigin: CdkOverlayOrigin;
  @ViewChild(CdkConnectedOverlay) cdkConnectedOverlay: CdkConnectedOverlay;

  triggerWidth: number;
  isComposing = false;
  isDestroy = true;
  isNotFound = false;
  inputValue = '';
  dropDownPosition: 'top' | 'center' | 'bottom' = 'bottom';
  selectionChangeSubscription: Subscription;
  selectedNodes: NzTreeNode[] = [];
  value: string[] = [];

  onChange: (value: string[] | string | null) => void;
  onTouched: () => void = () => null;

  onaddparentevt($event: Event) {
    $event.preventDefault();
    $event.stopImmediatePropagation();
    this.onaddparent.emit();
  }

  addParent(node: any) {
    this.treeRef.addParent(node);
  }

  get placeHolderDisplay(): string {
    return this.inputValue || this.isComposing || this.selectedNodes.length ? 'none' : 'block';
  }

  get searchDisplay(): string {
    return this.open ? 'block' : 'none';
  }

  get isMultiple(): boolean {
    return this.multiple || this.checkable;
  }

  get selectedValueDisplay(): { [key: string]: string } {
    let showSelectedValue = false;
    let opacity = 1;
    if (!this.showSearch) {
      showSelectedValue = true;
    } else {
      if (this.open) {
        showSelectedValue = !(this.inputValue || this.isComposing);
        if (showSelectedValue) {
          opacity = 0.4;
        }
      } else {
        showSelectedValue = true;
      }
    }
    return {
      display: showSelectedValue ? 'block' : 'none',
      opacity: `${opacity}`
    };
  }

  constructor(
    nzTreeService: CmacsTreeSelectService,
    private renderer: Renderer2,
    private cdr: ChangeDetectorRef,
    private elementRef: ElementRef,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    super(nzTreeService);
    this.renderer.addClass(this.elementRef.nativeElement, 'ant-select');
  }

  ngOnInit(): void {
    this.isDestroy = false;
    this.selectionChangeSubscription = this.subscribeSelectionChange();
    if (this.inlineEdit) {
      this.showExpand = false;
      this.defaultExpandAll = true;
    }
  }

  ngAfterViewInit() {
    if (this.inlineEdit && this.treeRef) {
      this.treeRef.searchValue = '';
    }
  }

  onaddchildevt($event: NzTreeNode)  {
    this.onaddchild.emit($event);
  }

  ngOnDestroy(): void {
    this.isDestroy = true;
    this.closeDropDown();
    this.selectionChangeSubscription.unsubscribe();
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    this.closeDropDown();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.hasOwnProperty('nzNodes')) {
      this.updateSelectedNodes(true);
    }
  }

  writeValue(value: string[] | string): void {
    if (isNotNil(value)) {
      if (this.isMultiple && Array.isArray(value)) {
        this.value = value;
      } else {
        this.value = [value as string];
      }
      this.updateSelectedNodes(true);
    } else {
      this.value = [];
      this.selectedNodes.forEach(node => {
        this.removeSelected(node, false);
      });
      this.selectedNodes = [];
    }
    this.cdr.markForCheck();
  }

  registerOnChange(fn: (_: string[] | string | null) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  trigger(): void {
    if (this.disabled || (!this.disabled && this.open)) {
      this.closeDropDown();
    } else {
      this.openDropdown();
      if (this.showSearch || this.isMultiple) {
        this.focusOnInput();
      }
    }
  }

  openDropdown(): void {
    if (!this.disabled) {
      this.open = true;
      this.openChange.emit(this.open);
      this.updateCdkConnectedOverlayStatus();
      this.updatePosition();
    }
  }

  closeDropDown(): void {
    this.onTouched();
    this.open = false;
    this.openChange.emit(this.open);
    this.cdr.markForCheck();
  }

  onKeyDownInput(e: KeyboardEvent): void {
    const keyCode = e.keyCode;
    const eventTarget = e.target as HTMLInputElement;
    if (this.isMultiple && !eventTarget.value && keyCode === BACKSPACE) {
      e.preventDefault();
      if (this.selectedNodes.length) {
        const removeNode = this.selectedNodes[this.selectedNodes.length - 1];
        this.removeSelected(removeNode);
        this.nzTreeService!.triggerEventChange$!.next({
          eventName: 'removeSelect',
          node: removeNode
        });
      }
      if (this.inlineEdit) {
        this.treeRef.searchValue = '';
      }
    }
  }

  onExpandedKeysChange(value: NzFormatEmitEvent): void {
    this.expandChange.emit(value);
    this.defaultExpandedKeys = [...value.keys!];
  }

  setInputValue(value: string): void {
    this.inputValue = value;
    this.updateInputWidth();
    this.updatePosition();
  }

  removeSelected(node: NzTreeNode, emit: boolean = true, event?: MouseEvent): void {
    node.isSelected = false;
    node.isChecked = false;
    if (this.checkable) {
      this.nzTreeService.conduct(node);
    } else {
      this.nzTreeService.setSelectedNodeList(node, this.multiple);
    }

    if (emit) {
      this.removed.emit(node);
    }

    // Do not trigger the popup
    if (event && event.stopPropagation) {
      event.stopPropagation();
    }
  }

  focusOnInput(): void {
    setTimeout(() => {
      if (this.inputElement) {
        this.inputElement.nativeElement.focus();
      }
    });
  }

  subscribeSelectionChange(): Subscription {
    return merge(
      this.treeClick.pipe(
        tap((event: NzFormatEmitEvent) => {
          const node = event.node!;
          if (this.radio  && this.checkable) {
            const nodes = this.coerceTreeNodes(this.nodes);
            this.nzTreeService.calcCheckedKeys([], nodes, true);
            node.isChecked = !node.isChecked;
            this.nzTreeService.calcCheckedKeys([node.key], nodes, true);
            this.updateSelectedNodes();
          } else if (this.checkable && !node.isDisabled && !node.isDisableCheckbox) {
            node.isChecked = !node.isChecked;
            this.nzTreeService.conduct(node);
          }
          if (this.checkable) {
            node.isSelected = false;
          }
        }),
        filter((event: NzFormatEmitEvent) => {
          const node = event.node!;
          return this.checkable ? !node.isDisabled && !node.isDisableCheckbox : !node.isDisabled;
        })
      ),
      this.checkable ? this.treeCheckBoxChange : observableOf(),
      this.cleared,
      this.removed
    ).subscribe(() => {
      this.updateSelectedNodes();
      const value = this.selectedNodes.map(node => node.key!);
      this.value = [...value];
      if (this.showSearch || this.isMultiple) {
        this.inputValue = '';
        this.isNotFound = false;
      }
      if (this.isMultiple) {
        this.onChange(value);
        this.focusOnInput();
        this.updatePosition();
      } else {
        this.closeDropDown();
        this.onChange(value.length ? value[0] : null);
      }
    });
  }

  updateSelectedNodes(init: boolean = false): void {
    if (init) {
      const nodes = this.coerceTreeNodes(this.nodes);
      this.nzTreeService.isMultiple = this.isMultiple;
      this.nzTreeService.initTree(nodes);
      if (this.checkable && !this.radio) {
        this.nzTreeService.calcCheckedKeys(this.value, nodes);
      } else if (!this.radio) {
        this.nzTreeService.calcSelectedKeys(this.value, nodes, this.isMultiple);
      } else {
        this.nzTreeService.calcCheckedKeys(this.value, nodes, true);
      }
    }

    if (this.checkable && !this.radio) {
      this.selectedNodes = [...this.getCheckedNodeList()];
    } else if (!this.radio) {
      this.selectedNodes = [...this.getSelectedNodeList()];
    } else {
      this.selectedNodes = [...this.getCheckedNodeList()];
    }
  }

  updatePosition(): void {
    setTimeout(() => {
      if (this.cdkConnectedOverlay && this.cdkConnectedOverlay.overlayRef) {
        this.cdkConnectedOverlay.overlayRef.updatePosition();
      }
    });
  }

  onPositionChange(position: ConnectedOverlayPositionChange): void {
    this.dropDownPosition = position.connectionPair.originY;
  }

  updateInputWidth(): void {
    if (this.isMultiple && this.inputElement) {
      if (this.inputValue || this.isComposing) {
        this.renderer.setStyle(
          this.inputElement.nativeElement,
          'width',
          `${this.inputElement.nativeElement.scrollWidth}px`
        );
      } else {
        this.renderer.removeStyle(this.inputElement.nativeElement, 'width');
      }
    }
  }

  onClearSelection($event: MouseEvent): void {
    $event.stopPropagation();
    $event.preventDefault();
    this.selectedNodes.forEach(node => {
      this.removeSelected(node, false);
    });
    this.cleared.emit();
  }

  setSearchValues($event: NzFormatEmitEvent): void {
    Promise.resolve().then(() => {
      this.isNotFound = (this.showSearch || this.isMultiple) && !!this.inputValue && $event.matchedKeys!.length === 0;
    });
  }

  updateCdkConnectedOverlayStatus(): void {
    this.triggerWidth = this.cdkOverlayOrigin.elementRef.nativeElement.getBoundingClientRect().width;
  }

  trackValue(_index: number, option: NzTreeNode): string {
    return option.key!;
  }
}
